<template>
  <header class="page-header">
    <div class="header-left">
      <div class="back" @click="goBack">
        <i class="el-icon-back"></i>
        <el-button type="text">返回</el-button>
      </div>
      <span class="app-name">{{ info.appName }}</span>
      <el-tag class="version-tag">{{ info.version }}</el-tag>
      <el-tag class="version-tag" effect="plain" v-if='statusMap[info.status]' :type='statusMap[info.status].type'>{{ statusMap[info.status].name }}</el-tag>
    </div>
    <div class="flex-full"></div>
    <el-button type='text' style='margin-right: 10px;' @click='showKeyTip'>快捷键</el-button>
    <el-button style="margin-right: 10px;" size="mini" type="primary" @click="handleOpenApi">自定义API</el-button>
    <el-button style="margin-right: 10px;" size="mini" type="primary" @click="() => $refs.dataSource.open()">数据源</el-button>
    <div class="header-right">
      <el-tooltip effect="dark" content="后退（ctrl + z）" placement="top">
        <n-badge :value="redoLength" :hidden='redoLength === 0'>
          <el-button type='icon' @click="handleGoBack" :disabled='redoLength === 0'>
            <img :src="$getImgPrefix('/design/svg/edit-back.svg')" alt="">
          </el-button>
        </n-badge>
      </el-tooltip>
      <el-tooltip effect="dark" content="前进（ctrl + shift + z）" placement="top">
        <n-badge :value="undoLength" :hidden='undoLength === 0'>
          <el-button type='icon' @click="handleGoForward" :disabled='undoLength === 0'>
            <img :src="$getImgPrefix('/design/svg/edit-goforward.svg')" alt="">
          </el-button>
        </n-badge>
      </el-tooltip>
      <el-tooltip effect="dark" content="快照记录" placement="top">
        <el-button type='icon' @click="handleShowHistory">
          <img :src="$getImgPrefix('/design/svg/history.svg')" alt="">
        </el-button>
      </el-tooltip>
      <el-dropdown :hide-on-click="true" trigger='click' @command='handleMoreClick'>
        <el-button type='icon'>
          <img :src="$getImgPrefix('/design/svg/edit-more.svg')" alt="">
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command='app'>应用变量管理</el-dropdown-item>
          <el-dropdown-item command='dataset'>数据集管理</el-dropdown-item>
          <el-dropdown-item command='events'>事件管理</el-dropdown-item>
          <el-dropdown-item command='source'>资源管理</el-dropdown-item>
          <el-dropdown-item command='settings'>应用设置</el-dropdown-item>
          <el-dropdown-item command='dataModel'>数据模型</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-tooltip effect="dark" content="保存（ctrl + s）" placement="top">
        <el-button ref="saveBtn" type='icon' @click="handleSave">
          <img :src="$getImgPrefix('/design/svg/edit-save.svg')" alt="">
        </el-button>
      </el-tooltip>
      <el-tooltip effect="dark" content="预览（ctrl + p）" placement="top">
        <el-button type='icon' @click="openPreview">
          <img :src="$getImgPrefix('/design/svg/edit-preview.svg')" alt="">
        </el-button>
      </el-tooltip>
      <!-- <el-tooltip effect="dark" content="全屏" placement="top">
        <el-button type='icon' @click="handleScreen">
          <i class="iconfont icon-quanping2"></i>
        </el-button>
      </el-tooltip> -->
    </div>
    <data-model ref="dataModel" />
    <data-source ref="dataSource" />
    <app-variables ref="appVariables" />
    <data-set-manage ref="dataSetManage" />
    <key-tip ref='keyTip' />
    <custom-api ref="customApi" />
    <event-select ref='eventSelect'></event-select>
    <SourceManage ref="sourceManage" />
    <PageHistoryManage ref="pageHistoryManage" />
    <settingsManage ref="settingsManage"></settingsManage>
  </header>
</template>

<script>
  import API from '@/api/application'
  import { DELETE_LAYOUT_DATA, HANDLE_COPY, SAVE_PAGE_CONFIG, UPDATE_DYNAMIC_PROPS, CONFIRM_SAVE_CONFIG } from '@/config/storeKey'
  import { jumpPreview } from '@/utils/url'
  import { dialogOpend, fullScreen } from '@/utils'
  import { mapMutations, mapActions } from 'vuex'
  import KEY from '@/store/base-info/mutation-types'
  import DataModel from '@/components/DataModel'
  import DataSource from '@/components/DataSource'
  import AppVariables from '@/components/AppVariables'
  import DataSetManage from '@/components/DataSetManage'
  import KeyTip from '@/components/KeyTip'
  import CustomApi from '@/components/CustomApi'
  import { UPDATE_HISTORY_LENGTH } from '@/config/eventName'
  import keyboardJS from 'keyboardjs'
  import {
    getNodeByElementId,
    getParentNodeByElementId,
    getParentNodeListByElementId, insertElement,
    isIncludes,
    treeUtil,
    removeLayoutElement
  } from '@/utils/tree-utils'
  import {
    canDropInsideTypes,
    dialogTypes,
    formTypes,
    ignoreCopyTypes,
    ignoreDeleteTypes,
  } from '@/config/widgets'
  import { authKey } from '@/router'
  import { goForward, goBack, getGlobalConfig, update } from '@/renderer/tools/history'
  import EventBus, { globalEmit } from '@/renderer/tools/eventBus'
  import { HIGH_LIGHT, PROPS } from '@/config/eventKey'
  import SourceManage from '@/components/SourceManage/index.vue'
  import settingsManage from '@/components/settingsManage/index.vue'
  import PageHistoryManage from '@/components/PageHistoryManage/index.vue'
import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    name: 'PageHeader',
    components: {
      DataModel,
      DataSource,
      AppVariables,
      DataSetManage,
      KeyTip,
      CustomApi,
      SourceManage,
      settingsManage,
      PageHistoryManage,
    },
    inject: ['dndOptions', ...pageInject],
    computed: {
      pageId() {
        return this.getStore().layoutData.pageDesignId
      },
      layoutData() {
        return this.getStore().layoutData
      },
      dynamicProps() {
        return this.getStore().dynamicProps
      },
    },
    data() {
      return {
        redoLength: 0,
        undoLength: 0,
        info: {},
        statusMap: {
          1: { name: '未上架', type: 'info' },
          2: { name: '已上架', type: 'success' },
          3: { name: '已下架', type: 'danger' },
        },
        clipboard: {
          elementId: undefined,
          type: 'copy'
        }
      }
    },
    created() {
      this.getAppInfo()
    },
    methods: {
      ...mapMutations({
        setVersionInfo: KEY.VERSION_INFO
      }),
      ...mapActions({
        judgeVersion: KEY.VERSION_JUDGE
      }),
      handleGoBack() {
        goBack(this)
      },
      handleGoForward() {
        goForward(this)
      },
      getAppInfo() {
        this.$http({
          url: API.appDesignDetail,
          data: { id: this.$route.query.vid },
        }).then((res) => {
          this.info = res.data
          this.setVersionInfo(this.info)
          this.judgeVersion({ message: `当前版本${{ '2': '已上架', '3': '已下架' }[this.info.status]}，无法编辑，是否生成新版本？` })
        })
      },
      async goBack() {
        await this._storeDispatch(CONFIRM_SAVE_CONFIG)
        if (history.length > 2) {
          rootApp.$router.back()
        } else {
          rootApp.$router.replace({ name: authKey.AppDetail, query: { id: this.$route.query.id } })
        }
      },
      ctlrSave(e) {
        e?.preventDefault()

        if(dialogOpend()) {
          return
        }
        this.$refs.saveBtn && this.$refs.saveBtn.$el.focus()
        this.$nextTick(() => {
          this.handleSave()
        })
      },
      handleSave(e) {
        this._storeDispatch(SAVE_PAGE_CONFIG)
      },
      async openPreview(e) {
        e?.preventDefault()
        const { vid, id } = this.$route.query
        const query = {
          appVersionId: vid,
          appId: id,
          pageId: this.pageId,
        }
        jumpPreview(query)
      },
      // 全屏
      handleScreen() {
        fullScreen()
      },
      handleMoreClick(type) {
        switch (type) {
          case 'app':
            this.handleShowAppVariables()
            break
          case 'dataset':
            this.handleShowDataSetManage()
            break
          case 'events':
            this.handleShowEventManage()
            break
          case 'source':
            this.$refs.sourceManage.open()
            break
          case 'settings':
            this.$refs.settingsManage.open()
            break
          case 'dataModel':
            this.$refs.dataModel.open()
            break
        }
      },
      handleShowAppVariables() {
        this.$refs.appVariables.open()
      },
      handleShowDataSetManage() {
        this.$refs.dataSetManage.open()
      },
      handleShowEventManage() {
        this.$refs.eventSelect.open({ isManage: true })
      },
      handleUpdateLength(e) {
        const {redoLength, undoLength} = e.detail
        this.redoLength = redoLength
        this.undoLength = undoLength
      },
      isInDynamicContainer(elementId) {
        const parentNode = getParentNodeByElementId(this.layoutData.children, elementId)
        return parentNode && ['dynamic-container'].includes(parentNode.elementType)
      },
      isInSlot(elementId) {
        const parentNode = getParentNodeByElementId(this.layoutData, elementId)
        return parentNode && ['slot'].includes(parentNode.elementType)
      },
      isInGrid(elementId) {
        const parentNode = getParentNodeByElementId(this.layoutData, elementId)
        return parentNode && ['grid-dnd'].includes(parentNode.elementType)
      },
      isInForm(elementId) {
        const parentList = getParentNodeListByElementId(
          this.layoutData,
          elementId
        )
        return (
          parentList &&
          parentList.some((item) =>
            ['form-container'].includes(item.elementType)
          )
        )
      },
      getPlacement({currentElement, draggingWidget, parentElement}) {
        const currentProps = this.getElementPropsById(currentElement.elementId)
        if (currentElement.elementId === this.layoutData.elementId) {
          return 'center'
        }
        const isInSlot = parentElement && ['slot'].includes(parentElement.elementType)
        const isInDynamicContainer = parentElement && ['dynamic-container'].includes(parentElement.elementType)
        const { elementType: type } = currentElement
        switch (true) {
          case currentProps.canDropInsideTypes &&
          !currentProps.canDropInsideTypes.includes(draggingWidget.elementType) && !['slot'].includes(currentElement.elementType):
            return 'bottom'
          case currentProps.ignoreDropInsideTypes &&
          currentProps.ignoreDropInsideTypes.includes(draggingWidget.elementType) && !['slot'].includes(currentElement.elementType):
            return 'bottom'
          case isInSlot &&
          this.getElementPropsById(parentElement.elementId).canDropInsideTypes &&
          !this.getElementPropsById(parentElement.elementId).canDropInsideTypes.includes(draggingWidget.elementType):
            return undefined
          case isInDynamicContainer:
            if (['layout', 'grid-dnd', 'dynamic-container'].includes(type) && !currentElement.children.filter((c) => this.getElementPropsById(c.elementId).isVisible !== false).length) {
              return 'center'
            }
            return undefined
          case canDropInsideTypes.includes(type) &&
          !currentElement.children.filter(
            (c) => this.getElementPropsById(c.elementId).isVisible !== false && !['dialog-container'].includes(c.elementType)
          ).length:
            if (['layout'].includes(type)) {
              if ((['layout'].includes(draggingWidget.elementType) && ['layout-container', 'form-container'].includes(parentElement.elementType))) {
                return 'bottom'
              }
              if (!['layout-container', 'form-container'].includes(parentElement.elementType)) {
                return 'bottom'
              }
              return 'center'
            } else if (['grid-dnd'].includes(type)) {
              return 'bottom'
            } else if (['dynamic-container'].includes(type)) {
              if (['dynamic-container'].includes(draggingWidget.elementType)) {
                return 'bottom'
              } else {
                return 'bottom'
              }
            } else {
              return 'center'
            }
          case canDropInsideTypes.includes(type) &&
          currentElement.children.length &&
          currentElement.children.some(
            (c) => this.getElementPropsById(c.elementId).isVisible !== false && !['dialog-container'].includes(c.elementType)
          ):
            if (['layout'].includes(type)) {
              if ((['layout'].includes(draggingWidget.elementType) && ['layout-container', 'form-container'].includes(parentElement.elementType))) {
                return 'bottom'
              }
              if (!['layout-container', 'form-container'].includes(parentElement.elementType)) {
                return 'bottom'
              }
              return undefined
            } else if (['grid-dnd', 'dynamic-container'].includes(type)) {
              return 'bottom'
            } else if (['layout-container', 'form-container'].includes(type)) {
              return 'bottom'
            } else {
              return undefined
            }
        }
        return 'bottom'
      },
      handleDelete(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        const { selectedId } = this.dndOptions
        if (selectedId) {
          const current = getNodeByElementId(this.layoutData || [], selectedId)
          if (current && !ignoreDeleteTypes.includes(current.elementType)) {
            removeLayoutElement.call(this, selectedId, this.layoutData?.elementId)
          }
        }
      },
      handleUndo(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        if (this.redoLength === 0) {
          return
        }
        this.handleGoBack()
      },
      handleRedo(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        if (this.undoLength === 0) {
          return
        }
        this.handleGoForward()
      },
      handleCopy(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        const { selectedId } = this.dndOptions
        if (selectedId) {
          const current = getNodeByElementId(this.layoutData || [], selectedId)
          if (current && !ignoreCopyTypes.includes(current.elementType) && !this.isInDynamicContainer(current.elementId)) {
            this.clipboard.elementId = current.elementId
            this.clipboard.type = 'copy'
          } else {
            this.clipboard.elementId = undefined
            this.clipboard.type = 'copy'
          }
        }
      },
      handleCut(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        const { selectedId } = this.dndOptions
        if (selectedId) {
          const current = getNodeByElementId(this.layoutData || [], selectedId)
          if (current && !ignoreCopyTypes.includes(current.elementType) && !this.isInDynamicContainer(current.elementId)) {
            this.clipboard.elementId = current.elementId
            this.clipboard.type = 'cut'
          } else {
            this.clipboard.elementId = undefined
            this.clipboard.type = 'cut'
          }
        }
      },
      async handlePaste(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        const {elementId, type} = this.clipboard
        let oldCopyVal, oldCopyId
        if (elementId) {
          let current = getNodeByElementId(this.layoutData || [], elementId)
          if (current && !ignoreCopyTypes.includes(current.elementType) && !this.isInDynamicContainer(current.elementId)) {
            let { selectedId: dropZoneId } = this.dndOptions
            const targetNode = getNodeByElementId(this.layoutData, dropZoneId)
            const targetParentNode = getParentNodeByElementId(
              this.layoutData,
              dropZoneId
            )
            if (!dialogTypes.includes(elementId) && dialogTypes.includes(targetNode.elementType)) {
              return
            }
            if (type !== 'timer' && targetNode.elementType === 'timer') {
              return
            }
            let placement = this.getPlacement({currentElement: targetNode, draggingWidget: current, parentElement: targetParentNode})
            if (!placement) {
              return
            }
            if (type === 'cut') {
              if (elementId === dropZoneId) {
                return
              }
              if (isIncludes(current, dropZoneId)) {
                return
              }
            } else if (type === 'copy') {
              oldCopyVal = getGlobalConfig.call(this)
              oldCopyId = current.elementId
              current = await this._storeDispatch(HANDLE_COPY, { elementId: current.elementId })
            }
            if (dialogTypes.includes(current.elementType)) {
              dropZoneId = 'dialog-container'
              placement = 'center'
              treeUtil.forEach(this.layoutData.children, (node) => {
                if (
                  (dialogTypes.includes(node.elementType) || node.elementType === 'timer') &&
                  node.elementId !== current.elementId
                ) {
                  globalEmit.call(this, PROPS, { elementId: node.elementId, data: {isVisible: false} })
                }
              })
            }
            if (current.elementType === 'timer') {
              dropZoneId = 'timer-container'
              placement = 'center'
              treeUtil.forEach(this.layoutData.children, (node) => {
                if (
                  (dialogTypes.includes(node.elementType) || node.elementType === 'timer') &&
                  node.elementId !== current.elementId
                ) {
                  globalEmit.call(this, PROPS, { elementId: node.elementId, data: {isVisible: false} })
                }
              })
            }
            if (
              placement !== 'center' &&
              this.isInSlot(dropZoneId) &&
              ['data'].includes(this.getElementPropsById(targetParentNode.elementId).slotType)
            ) {
              this._storeCommit(UPDATE_DYNAMIC_PROPS, {
                dynamicProps: {
                  [current.elementId]: {
                    prop: this.getElementPropsById(targetParentNode.elementId).slotName,
                    useSlotProps: true,
                  },
                },
              })
            }
            if (
              formTypes.includes(current.elementType) &&
              this.isInForm(dropZoneId)
            ) {
              this._storeCommit(UPDATE_DYNAMIC_PROPS, {
                dynamicProps: {
                  [current.elementId]: {
                    useSlotProps: true,
                  },
                },
              })
            }
            if (this.isInGrid(dropZoneId) || ['grid-dnd'].includes(targetNode.elementType)) {
                let x
                let y
                let w
                let h

                if (['grid-dnd'].includes(targetNode.elementType)) {
                  const {colNum, rowHeight} = this.getElementPropsById(targetNode.elementId)
                  w = colNum
                } else {
                  const {colNum, rowHeight} = this.getElementPropsById(targetParentNode.elementId)
                  w = colNum
                }
                const layout = this.getElementPropsById(targetNode.elementId).layout || {}
                switch (placement) {
                  case 'bottom':
                    x = layout.x
                    y = layout.y + layout.h
                    break
                  case 'center':
                    x = 0
                    y = 0
                    break
                }
                globalEmit.call(this, PROPS, {elementId: current.elementId, data: {
                  layout: {
                    i: current.elementId,
                    x: x,
                    y: y,
                    w: this.getElementPropsById(current.elementId).layout?.w || w,
                    h: this.getElementPropsById(current.elementId).layout?.h || h || 6
                  }}, pushHistory: false})

                if ((this.isInGrid(dropZoneId) && placement !== 'center') || (['grid-dnd'].includes(targetNode.elementType) && placement === 'center')) {
                  this._storeCommit(UPDATE_DYNAMIC_PROPS, {
                    dynamicProps: {
                      [current.elementId]: {
                        styleConfigList: [
                          {
                            name: '根元素',
                            query: '',
                            styles: {
                              overflowX: 'auto',
                              overflowY: 'auto',
                            },
                            className: [],
                            animation: []
                          }
                        ]
                      },
                    },
                  })
                }
              }
            const isCopy = type === 'copy'
            // 非复制时这个插入节点的方法还是默认的记录历史记录
            insertElement.call(this, {
              rootTree: this.layoutData,
              targetId: dropZoneId,
              placement,
              newNode: current,
              oldSelectedId: this.dndOptions.selectedId,
              pushHistory: !isCopy
            })
            // 是复制的时候, 自定义记录历史记录
            if(isCopy) {
              update({
                oldVal: oldCopyId,
                val: current.elementId,
                extra: {
                  oldVal: oldCopyVal,
                  val: getGlobalConfig.call(this)
                },
                callback: id => {
                  EventBus.$emit(HIGH_LIGHT, id)
                }
              })
            }
            setTimeout(() => {
              this.dndOptions.selectedId = current.elementId
            })
          }
        }
      },
      handleUp(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        const { selectedId } = this.dndOptions
        if (selectedId) {
          const parent = getParentNodeByElementId(this.layoutData, selectedId)
          if (parent) {
            if (['dialog-container'].includes(parent.elementType)) {
              return
            }
            if (['page'].includes(parent.elementType)) {
              const preIndex = parent.children.findIndex(node => node.elementId === selectedId) - 1
              const preNode = parent.children[preIndex]
              if (preNode && !['dialog-container'].includes(preNode.elementType)) {
                this.dndOptions.selectedId = preNode.elementId
              }
              return
            }
            this.dndOptions.selectedId = parent.elementId
          }
        }
      },
      handleDown(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        const { selectedId } = this.dndOptions
        if (selectedId) {
          const current = getNodeByElementId(this.layoutData, selectedId)
          const parent = getParentNodeByElementId(this.layoutData, selectedId)
          if (current.children.length) {
            if (['page'].includes(current.elementType)) {
              this.dndOptions.selectedId = current.children[1]?.elementId
              return
            }
            this.dndOptions.selectedId = current.children[0].elementId
            return
          }
          if (parent) {
            const nextIndex = parent.children.findIndex(node => node.elementId === selectedId) + 1
            const nextNode = parent.children[nextIndex]
            if (nextNode) {
              this.dndOptions.selectedId = nextNode.elementId
            }
          }
        }
      },
      handleLeft(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        const { selectedId } = this.dndOptions
        if (selectedId) {
          const parent = getParentNodeByElementId(this.layoutData, selectedId)
          if (parent) {
            const preIndex = parent.children.findIndex(node => node.elementId === selectedId) - 1
            const preNode = parent.children[preIndex]
            if (preNode && !['dialog-container', ...dialogTypes].includes(preNode.elementType)) {
              this.dndOptions.selectedId = preNode.elementId
            }
          }
        }
      },
      handleRight(e) {
        if (e.target !== document.body || dialogOpend()) {
          return
        }
        e.preventDefault()

        const { selectedId } = this.dndOptions
        if (selectedId) {
          const parent = getParentNodeByElementId(this.layoutData, selectedId)
          if (parent) {
            const nextIndex = parent.children.findIndex(node => node.elementId === selectedId) + 1
            const nextNode = parent.children[nextIndex]
            if (nextNode && !['dialog-container', ...dialogTypes].includes(nextNode.elementType)) {
              this.dndOptions.selectedId = nextNode.elementId
            }
          }
        }
      },
      initKeyEvents() {
        // 删除
        keyboardJS.bind('delete', this.handleDelete)
        // 后退
        keyboardJS.bind('ctrl + z', this.handleUndo)
        // 前进
        keyboardJS.bind('ctrl + shift > z', this.handleRedo)
        // 保存
        keyboardJS.bind('ctrl + s', this.ctlrSave)
        // 预览
        keyboardJS.bind('ctrl + p', this.openPreview)
        // 复制
        keyboardJS.bind('ctrl + c', this.handleCopy)
        // 剪切
        keyboardJS.bind('ctrl + x', this.handleCut)
        // 粘贴
        keyboardJS.bind('ctrl + v', this.handlePaste)
        // 选择父节点
        keyboardJS.bind('up', this.handleUp)
        // 选择子节点
        keyboardJS.bind('down', this.handleDown)
        // 选择前一个节点
        keyboardJS.bind('left', this.handleLeft)
        // 选择后一个节点
        keyboardJS.bind('right', this.handleRight)
      },
      handleunbindKeys() {
        // 删除
        keyboardJS.unbind('delete', this.handleDelete)
        // 后退
        keyboardJS.unbind('ctrl + z', this.handleUndo)
        // 前进
        keyboardJS.unbind('ctrl + shift > z', this.handleRedo)
        // 保存
        keyboardJS.unbind('ctrl + s', this.ctlrSave)
        // 预览
        keyboardJS.unbind('ctrl + p', this.openPreview)
        // 复制
        keyboardJS.unbind('ctrl + c', this.handleCopy)
        // 剪切
        keyboardJS.unbind('ctrl + x', this.handleCut)
        // 粘贴
        keyboardJS.unbind('ctrl + v', this.handlePaste)
        // 选择父节点
        keyboardJS.unbind('up', this.handleUp)
        // 选择子节点
        keyboardJS.unbind('down', this.handleDown)
        // 选择前一个节点
        keyboardJS.unbind('left', this.handleLeft)
        // 选择后一个节点
        keyboardJS.unbind('right', this.handleRight)
      },
      showKeyTip() {
        this.$refs.keyTip.open()
      },
      handleOpenApi() {
        this.$refs.customApi.open()
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
      handleShowHistory() {
        this.$refs.pageHistoryManage.open()
      }
    },
    mounted() {
      window.addEventListener(UPDATE_HISTORY_LENGTH, this.handleUpdateLength)
      this.$nextTick(() => {
        this.initKeyEvents()
      })
    },
    beforeDestroy() {
      this.handleunbindKeys()
      window.removeEventListener(UPDATE_HISTORY_LENGTH, this.handleUpdateLength)
    }
  }
</script>

