<template>
  <div class="step-item" v-loading="loading">
    <div class="step-left">
      <div class="step-index">{{ order }}</div>
      <div class="step-line"></div>
      <div class="step-arrow"></div>
    </div>
    <div class="step-right">
      <i class="step-remove el-icon-circle-close" @click="handleRemoveStep"></i>
      <div class="name right-item">
        <div class="label" style="align-items: center;">步骤名称</div>
        <div class="content">
          <el-input
            v-model.trim="stepData.name"
            style="flex: 1;"
            placeholder="请输入步骤名称"
          ></el-input>
          <el-popover
            width="300"
            trigger="click"
            ref="popover"
            placement="right"
            title="执行规则"
          >
            <n-form
              label-position="left"
              label-width="100px"
              size="mini"
              @submit.native.prevent
            >
              <n-form-item label="超时时间（秒）">
                <el-input-number
                  v-model="stepData.config.timeout"
                  :min="1"
                  :max="60"
                ></el-input-number>
              </n-form-item>
              <n-form-item label="忽略异常继续">
                <el-switch v-model="stepData.config.ignoreError"></el-switch>
              </n-form-item>
            </n-form>
            <el-button
              slot="reference"
              style="margin-left: 24px;margin-right: 20px;"
              >规则</el-button
            >
          </el-popover>
        </div>
      </div>
      <div class="actions right-item">
        <div class="label">
          <div>
            逻辑定义
            <el-tooltip popper-class="tooltip-question" placement="top">
              <span slot="content"
                >逻辑之间为并行关系</span
              >
              <i class="el-icon-question"></i>
            </el-tooltip>
          </div>
        </div>
        <div class="content">
          <div class="actions-item-wrapper">
            <template v-if="stepData.actions.length">
              <div
                class="actions-item"
                v-for="action in stepData.actions"
                :key="action.code"
                @click="handleEditAction(action)"
              >
                <div class="action-handler"></div>
                <span class="action-name">{{ action.name }}</span>
                <i
                  class="action-remove el-icon-close"
                  @click.stop="handleRemoveAction(action)"
                ></i>
              </div>
            </template>
            <div v-else class="actions-empty">暂无数据</div>
          </div>
          <div class="btn-wrapper">
            <el-popover
              width="200"
              trigger="click"
              ref="popover"
              placement="right"
              title=""
            >
              <el-button type="primary" @click="handleAddAction(1)"
                >调用API</el-button
              >
              <el-button type="primary" @click="handleAddAction(2)"
                >调用SQL</el-button
              >
              <!-- <el-button type="primary" @click="handleAddAction(3)"
                >调用脚本</el-button
              > -->
              <el-button
                slot="reference"
                style="margin-left: 24px; margin-right: 20px;"
                type="primary"
                >新增</el-button
              >
            </el-popover>
          </div>
        </div>
      </div>
      <div class="response right-item">
        <div class="label">步骤出参</div>
        <div class="content">
          <div class="res-top">
            <el-button v-if="!stepData.mapConfig" @click="handleFormat">格式化</el-button>
            <el-tag v-else @click="handleFormat" closable disable-transitions @close="handleRemoveFormat">重新配置格式化</el-tag>
            <el-tooltip popper-class="tooltip-question" placement="top">
              <span slot="content"
                >非必填，支持对当前步骤的出参进行数值、文本或布尔等类型的格式化操作</span
              >
              <i class="el-icon-question"></i>
            </el-tooltip>
          </div>
          <div class="res-bottom">
            <div class="response-text">
              <el-popover
                placement="bottom"
                title=""
                :width="popoverWidth"
                trigger="click"
              >
                <div class="body">
                  <vue-json-pretty
                    v-show="response"
                    class="json-pretty"
                    :data="response"
                    :virtual="true"
                    :itemHeight="32"
                  >
                  </vue-json-pretty>
                  <div v-if="!response" class="empty-text">暂无数据，请点击测试</div>
                </div>
                <el-select
                  popper-class="tree-select-popper-trigger"
                  slot="reference"
                  style="width: 100%"
                  placeholder="{ }"
                  @focus="handleUpdateResponse"
                ></el-select>
              </el-popover>
            </div>
            <el-button
              style="margin-left: 23px; margin-right: 21px; margin-top:10px;"
              type="primary"
              @click="handleMock"
              >测试</el-button
            >
          </div>
        </div>
      </div>
    </div>
    <ActionConfig ref="actionConfig" @confirm="handleSaveAction" />
    <StepMock ref="stepMock" />
    <Formatter ref="formatter" @confirm="handleFormatConfirm" />
  </div>
</template>

<script>
  import _ from 'lodash'
  import ActionConfig from './ActionConfig.vue'
  import StepMock from './StepMock.vue'
  import Formatter from './Format/Formatter.vue';
  import { CUSTOM_API_RESPONSE_MAP } from '@/config'
  import { Factory } from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  const VueJsonPretty = new Factory({ virtualLines: 20 })

  export default {
    name: 'StepItem',
    components: {
      ActionConfig,
      VueJsonPretty,
      StepMock,
      Formatter,
    },
    inject: ['customApiProvide'],
    provide() {
      return {
        stepProvide: {
          stepData: this.stepData,
        }
      }
    },
    props: {
      stepData: {
        type: Object,
      },
      order: {
        type: Number,
      },
    },
    data() {
      return {
        loading: false,
        response: null,
        popoverWidth: ''
      }
    },
    methods: {
      async handleRemoveStep() {
        await this.$confirm('确认删除此步骤配置吗？')
        this.$emit('remove')
      },
      handleAddAction(type) {
        if (!this.stepData.name) {
          this.$message.error('请输入步骤名称！')
          return
        }
        this.$refs.actionConfig.open({ type, stepData: this.stepData })
      },
      handleEditAction(actionData) {
        this.$refs.actionConfig.open({ type: actionData.type, actionData, stepData: this.stepData })
      },
      handleSaveAction({ actionData }) {
        const currentIndex = this.stepData.actions.findIndex(
          (item) => item.code === actionData.code
        )
        if (currentIndex !== -1) {
          this.$set(this.stepData.actions, currentIndex, actionData)
        } else {
          this.stepData.actions.push(actionData)
        }
      },
      async handleRemoveAction(action) {
        await this.$confirm('确认删除此逻辑吗？', '提示')
        this.stepData.actions = this.stepData.actions.filter(
          (item) => item.code !== action.code
        )
      },
      handleMock() {
        if (!this.stepData.name) {
          this.$message.error('请输入步骤名称！')
          return
        }
        if (!this.stepData.actions.length) {
          this.$message.error('请先配置逻辑！')
          return
        }
        this.$refs.stepMock.open({stepData: this.stepData})
      },
      handleUpdateResponse(e) {
        this.popoverWidth = `${e.target.getBoundingClientRect().width}`
        this.response = this.stepData.mapConfig ? window[CUSTOM_API_RESPONSE_MAP][this.stepData.code] : window[CUSTOM_API_RESPONSE_MAP][`${this.stepData.code}_source`]
      },
      handleFormat() {
        if (!this.stepData.name) {
          this.$message.error('请输入步骤名称！')
          return
        }
        if (!this.stepData.actions.length) {
          this.$message.error('请先配置逻辑！')
          return
        }
        this.$refs.formatter.open({ stepData: this.stepData })
      },
      handleFormatConfirm(mapConfig) {
        this.$set(this.stepData, 'mapConfig', mapConfig)
      },
      async handleRemoveFormat() {
        await this.$confirm('确认删除格式化配置吗？', '提示')
        this.stepData.mapConfig = null
      }
    },
  }
</script>

