<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    :title="title"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible">
      <div class="action-info">
        <div class="title">
          <span class="t">逻辑信息</span>
        </div>
        <div class="action-info-item">
          <n-form
            style="flex: 1;"
            label-position="left"
            label-width="80px"
            size="mini"
            @submit.native.prevent
          >
            <n-form-item required label="逻辑名称">
              <el-input v-model="actionData.name"></el-input>
            </n-form-item>
          </n-form>
          <logic-list @change="handleLogicChange">
            <el-button size="mini" style="margin-left: 20px;" type="primary"
              >选择已存逻辑</el-button
            >
          </logic-list>
        </div>
      </div>
      <div class="action-condition">
        <div class="title">
          <span class="t">条件配置</span>
          <el-tooltip popper-class="tooltip-question" placement="top">
            <span slot="content"
              >配置该逻辑的条件判断，满足条件时执行逻辑，不满足则停止执行。</span
            >
            <i style="margin-right: 10px;" class="el-icon-question"></i>
          </el-tooltip>
          <el-switch
            v-model="actionData.condition.enable"
            @change="handleEnableChange"
          ></el-switch>
        </div>
        <Condition
          v-if="actionData.condition.enable"
          :condition="actionData.condition"
        />
      </div>
      <div class="action-config">
        <div class="title">
          <span class="t">逻辑配置</span>
        </div>
        <ApiAction
          ref="apiAction"
          v-if="actionData.type === 1"
          :actionData="actionData"
          :stepData="stepData"
          @response="handleResponse"
        />
        <SqlAction
          ref="sqlAction"
          v-if="actionData.type === 2"
          :actionData="actionData"
          :stepData="stepData"
          @response="handleResponse"
        />
      </div>
      <div class="action-response">
        <div class="title">
          <span class="t">响应体</span>
          <!-- <el-button size="mini" type="primary" @click="showInputResponseDialog"
            >手动输入</el-button
          > -->
        </div>
        <div class="body">
          <vue-json-pretty
            v-show="response"
            class="json-pretty"
            path="response"
            :data="response"
            :virtual="true"
            :itemHeight="32"
            v-model="selectedPath"
          >
          </vue-json-pretty>
          <div v-if="!response" class="empty-text">暂无数据</div>
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="handleConfirm">确 认</el-button>
    </div>
    <custom-input-dialog @confirm="handleUpdateValue" ref="inputDialog"></custom-input-dialog>
  </el-dialog>
</template>

<script>
  import CustomApiVariableSelect from './CustomApiVariableSelect.vue'
  import LogicList from './LogicList.vue'
  import ApiAction from './ApiAction.vue'
  import SqlAction from './SqlAction.vue'
  import Condition from './Condition.vue'
  import _ from 'lodash'
  import mixins from './mixins'
  import { Factory } from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  import { getRandomHexadecimal } from '@/utils/tree-utils'
  const VueJsonPretty = new Factory({ virtualLines: 20 })
  export default {
    name: 'ActionConfig',
    components: {
      Condition,
      CustomApiVariableSelect,
      ApiAction,
      VueJsonPretty,
      LogicList,
      SqlAction,
    },
    mixins: [mixins],
    inject: ['customApiProvide'],
    data() {
      return {
        dialogVisible: false,
        loading: false,
        actionData: null,
        response: null,
        selectedPath: '',
        stepData: null
      }
    },
    computed: {
      title() {
        return this.actionData?.type === 1 ? '调用API' : '调用SQL'
      }
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      open({ type, actionData, stepData } = {}) {
        this.stepData = stepData
        this.actionData = actionData
          ? _.cloneDeep(actionData)
          : {
              code: getRandomHexadecimal(6),
              name: '',
              type,
              condition: {
                conditionItems: [],
                enable: false,
              },
              config: {
                db: {
                  dataSourceId: 0,
                  params: [
                    {
                      key: '',
                      mockValue: undefined,
                      value: undefined,
                    },
                  ],
                  sql: '',
                  sqlType: 1,
                },
                http: {
                  body: [
                    {
                      key: '',
                      mockValue: undefined,
                      value: undefined,
                    },
                  ],
                  headers: [
                    {
                      key: '',
                      mockValue: undefined,
                      value: undefined,
                    },
                  ],
                  method: 'POST',
                  params: [
                    {
                      key: '',
                      mockValue: undefined,
                      value: undefined,
                    },
                  ],
                  url: '',
                  type: 6,
                  dynamicOrigin: undefined,
                  useDynamicOrigin: false,
                  urlBuildConfig: null
                },
              },
            }
        switch (this.actionData.type) {
          case 1: {
            this.actionData.config.http = {
              ...this.actionData.config.http,
              body: this.actionData.config.http.body.length
                ? this.actionData.config.http.body
                : [
                    {
                      key: '',
                      mockValue: undefined,
                      value: undefined,
                    },
                  ],
              headers: this.actionData.config.http.headers.length
                ? this.actionData.config.http.headers
                : [
                    {
                      key: '',
                      mockValue: undefined,
                      value: undefined,
                    },
                  ],
              params: this.actionData.config.http.params.length
                ? this.actionData.config.http.params
                : [
                    {
                      key: '',
                      mockValue: undefined,
                      value: undefined,
                    },
                  ],
            }
            break
          }
          case 2: {
            this.actionData.config.db = {
              ...this.actionData.config.db,
              params: this.actionData.config.db.params.length
                ? this.actionData.config.db.params
                : [
                    {
                      key: '',
                      mockValue: undefined,
                      value: undefined,
                    },
                  ],
            }
            break
          }
        }
        this.response = null
        this.dialogVisible = true
      },
      async handleConfirm() {
        if (!this.actionData.name) {
          this.$message.error('请输入逻辑名称！')
          return
        }
        switch (this.actionData.type) {
          case 1: {
            const http = await this.$refs.apiAction.getResult()
            this.actionData.config.http = http
            break
          }
          case 2: {
            const db = await this.$refs.sqlAction.getResult()
            this.actionData.config.db = db
            break
          }
        }
        this.$emit('confirm', { actionData: this.actionData })
        this.dialogVisible = false
      },
      showInputResponseDialog() {
        this.$refs.inputDialog.open({
          type: 'object',
          data: this,
          key: 'response',
          from: 'response',
        })
      },
      handleEnableChange(enable) {
        if (enable) {
          if (!this.actionData.condition.conditionItems.length) {
            this.actionData.condition.conditionItems.push(
              {
                items: [
                  {
                    left: {
                      source: {
                        expression: '',
                        name: '',
                      },
                      type: undefined,
                      value: undefined,
                    },
                    logic: 'or',
                    op: 'EQ',
                    right: {
                      source: {
                        expression: '',
                        name: '',
                      },
                      type: undefined,
                      value: undefined,
                    },
                  },
                ],
                logic: 'or',
              },
              {
                items: [
                  {
                    left: {
                      source: {
                        expression: '',
                        name: '',
                      },
                      type: undefined,
                      value: undefined,
                    },
                    logic: 'or',
                    op: 'EQ',
                    right: {
                      source: {
                        expression: '',
                        name: '',
                      },
                      type: undefined,
                      value: undefined,
                    },
                  },
                ],
                logic: 'or',
              }
            )
          }
        }
      },
      handleResponse({ response }) {
        this.response = response
      },
      handleLogicChange(selectedList) {
        const selectedCode = selectedList[0].code
        const actionsArr = this.customApiProvide.apiDetail.steps.map(
          (item) => item.actions
        )
        const actions = actionsArr.flat(Infinity)
        const selectedAction = actions.find(
          (item) => item.code === selectedCode
        )
        const actionData = _.cloneDeep(selectedAction)
        actionData.code = getRandomHexadecimal(6)
        this.open({ type: actionData.type, actionData, stepData: this.stepData })
      },
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .dialog-content::v-deep {
    .action-info,
    .action-condition,
    .action-config,
    .action-response {
      .title {
        position: relative;
        margin: 10px 0;
        .t {
          font-weight: 700;
          margin-right: 10px;
          margin-left: 10px;
        }
        &::before {
          position: absolute;
          left: 0;
          top: 0;
          content: '';
          width: 4px;
          height: 100%;
          background-color: @drop-main-color;
        }
      }
    }
    .action-info {
      .action-info-item {
        margin-top: 10px;
        display: flex;
        align-items: flex-start;
      }
    }
    .action-condition {
      width: 100%;
      min-height: 60px;
      background-color: #ffffff;
    }

    .action-config {
      margin-top: 10px;
    }

    .action-response {
      .json-pretty {
        height: 300px;
      }
    }
  }
</style>
