<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="选择出参"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible" v-loading="loading">
      <div class="aside">
        <div
          class="menu-item"
          :class="{selected: selectedCode === 'global'}"
          @click="selectedCode = 'global'"
         >API请求出参</div>
        <el-divider><span style="font-size: 12px; color: #999;">步骤出参</span></el-divider>
        <div
          class="menu-item"
          :class="{selected: selectedCode === item.code, disabled: isDisabledStep(item.code)}"
          v-for="item in steps"
          :key="item.code"
          @click="handleSelectStep(item.code)"
        >{{item.name || '未命名步骤'}}</div>
      </div>
      <div class="body">
        <vue-json-pretty
          v-show="response"
          class="json-pretty no-select"
          path="response"
          :data="response"
          :virtual="true"
          :itemHeight="32"
          v-model="selectedPath"
          @click="clickPath"
          selectableType="single"
          :showSelectController="true"
          :selectOnClickNode="true"
        >
        </vue-json-pretty>
        <div style="display: flex; align-items: center;" v-if='!response'>
          <div class='empty-text'>暂无数据</div>
          <el-button style="margin-left: 20px;" size="mini" v-if="selectedCode !== 'global'" type="primary" @click="getResponse">测试</el-button>
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="handleConfirm">确 认</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import customApi from '@/api/customApi'
  import { CUSTOM_API_SESSION_ID, CUSTOM_API_RESPONSE_MAP } from '@/config'
  import { Factory } from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  import { getPathList } from '@/utils/format'
  const VueJsonPretty = new Factory({ virtualLines: 30 })
  export default {
    name: 'CustomApiVariableSelect',
    components: {
      VueJsonPretty,
    },
    inject: ['customApiProvide'],
    props: {
      showSelf: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        dialogVisible: false,
        selectedPath: '',
        selectedCode: 'global',
        stepCode: '',
        onlyGlobal: false,
        loading: false
      }
    },
    computed: {
      steps() {
        return this.customApiProvide.apiDetail.steps
      },
      response() {
        if (this.selectedCode === 'global') {
          return this.customApiProvide.apiDetail.requestBody
        }
        if (this.showSelf && this.selectedCode === this.stepCode) {
          return window[CUSTOM_API_RESPONSE_MAP][`${this.selectedCode}_source`]
        }
        return window[CUSTOM_API_RESPONSE_MAP][this.selectedCode] || window[CUSTOM_API_RESPONSE_MAP][`${this.selectedCode}_source`]
      }
    },
    methods: {
      open({ fullVariable, stepCode, onlyGlobal = false } = {}) {
        if (fullVariable) {
          const pathList = getPathList(fullVariable)
          this.selectedCode = pathList[0].replace('$', '')
          this.selectedPath = fullVariable.replace(pathList[0], 'response')
        } else {
          this.selectedCode = 'global'
          this.selectedPath = ''
        }
        this.onlyGlobal = onlyGlobal
        this.stepCode = stepCode
        this.dialogVisible = true
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      handleConfirm() {
        if (!this.selectedPath) {
          this.$message.error('请选择出参节点！')
          return
        }
        const result = this.selectedPath.replace('response', `$${this.selectedCode}`)
        this.$emit('confirm', result)
        this.dialogVisible = false
      },
      clickPath() {

      },
      handleSelectStep(code) {
        if (this.isDisabledStep(code)) {
          return
        }
        this.selectedCode = code
      },
      isDisabledStep(code) {
        if (this.onlyGlobal) {
          return code !== 'global'
        }
        const codeList = this.steps.map(item => item.code)
        const index = codeList.findIndex(item => item === this.stepCode)
        return this.showSelf ? codeList.splice(index + 1).includes(code) : codeList.splice(index).includes(code)
      },
      getResponse() {
        const stepData = this.steps.find(item => item.code === this.selectedCode)
        this.loading = true
        this.$http({
          url: customApi.apiDesignMockRunStep,
          data: {
            appId: Number(this.$route.query.id),
            sessionId: window[CUSTOM_API_SESSION_ID],
            requestBody: this.customApiProvide.apiDetail.requestBody,
            step: stepData,
          },
        })
          .then((res) => {
            window[CUSTOM_API_RESPONSE_MAP][stepData.code] = res.data.mappedOutput
            window[CUSTOM_API_RESPONSE_MAP][`${stepData.code}_source`] = res.data.output
            this.selectedCode = 'global'
            this.selectedCode = stepData.code
          })
          .finally(() => {
            this.loading = false
          })
      }
    },
  }
</script>

<style lang="less" scoped>
  .dialog-content::v-deep {
    display: flex;
    .aside {
      display: flex;
      flex-direction: column;
      width: 200px;
      border-right: 1px solid #eee;
      padding: 0 10px;
      overflow: auto;
      .menu-item {
        height: 40px;
        line-height: 40px;
        padding-left: 10px;
        border-radius: 3px;
        &.selected {
          background-color: #f0f6ff;
          color: #3f92fe;
        }
        &.disabled {
          opacity: 0.5;
          &:hover {
            cursor: not-allowed;
          }
        }
        &:hover {
          cursor: pointer;
          color: #3f92fe;
        }
      }
    }
    .body {
      flex: 1;
      padding-left: 10px;
      height: 350px;
      max-width: 960px;
      .json-pretty {
        height: 350px;

        .vjs-key {
          white-space: nowrap;
        }
      }
    }
  }
</style>
