<script>
  import _ from 'lodash'
  import SourceSelect from './SourceSelect'
  import Condition from '../Condition.vue'
  import mixins from '../mixins'
  const initialItems = [
    {
      items: [
        {
          left: {
            source: {
              expression: '',
              name: '',
            },
            type: undefined,
            value: undefined,
          },
          logic: 'or',
          op: 'EQ',
          right: {
            source: {
              expression: '',
              name: '',
            },
            type: undefined,
            value: undefined,
          },
        },
      ],
      logic: 'or',
    },
    {
      items: [
        {
          left: {
            source: {
              expression: '',
              name: '',
            },
            type: undefined,
            value: undefined,
          },
          logic: 'or',
          op: 'EQ',
          right: {
            source: {
              expression: '',
              name: '',
            },
            type: undefined,
            value: undefined,
          },
        },
      ],
      logic: 'or',
    },
  ]
  export default {
    name: 'StringFormatter',
    components: {
      SourceSelect,
      Condition,
    },
    mixins: [mixins],
    data() {
      return {
        dialogVisible: false,
        currentNode: null,
        functionOptions: [
          {
            label: '求平均值',
            value: 'AVG',
          },
          {
            label: '取最大值',
            value: 'MAX',
          },
          {
            label: '取最小值',
            value: 'MIN',
          },
          {
            label: '求和',
            value: 'SUM',
          },
          {
            label: '取总数',
            value: 'SIZE',
          },
        ],
      }
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      open({ currentNode }) {
        this.currentNode = _.cloneDeep(currentNode)
        this.handleAddAttr()
        this.dialogVisible = true
      },
      handleConfirm() {
        let isSelectSource = false
        switch(this.currentNode.mapperConfig.type) {
          case 'NUMBER_COUNT': {
            isSelectSource = !!this.currentNode.mapperConfig.numberCount.source.name
            break
          }
          case 'NUMBER_CALCULATE': {
            isSelectSource = !!this.currentNode.mapperConfig.numberCalculate.items.filter(item => item.source.name).length
            if (!this.currentNode.mapperConfig.numberCalculate.formula) {
              this.$message.error('请输入公式')
              return
            }
            break
          }
          case 'DECIMAL_POINT_FORMAT': {
            isSelectSource = !!this.currentNode.mapperConfig.decimalPointFormat.source.name
            break
          }
          default: {
            isSelectSource = true
            break
          }
        }
        if (!isSelectSource) {
          this.$message.error('请选择参数')
          return
        }
        this.$emit('confirm', this.currentNode)
        this.dialogVisible = false
      },
      handleAddAttr() {
        if (!this.currentNode.mapperConfig) {
          this.$set(this.currentNode, 'mapperConfig', {
            type: 'NUMBER_COUNT',
            decimalPointFormat: {
              pointNumber: 0,
              source: {
                name: '',
              },
            },
            numberCalculate: {
              pointNumber: 0,
              formula: '',
              items: [
                {
                  description: '',
                  name: 'A',
                  source: {
                    name: '',
                  },
                },
              ],
            },
            numberCount: {
              filter: {
                enable: false,
                conditionItems: _.cloneDeep(initialItems),
              },
              function: 'AVG',
              pointNumber: 0,
              source: {
                name: '',
              },
            },
          })
        }
      },
      handleSourceChange(fullVariable) {},
      handleAddItem() {
        const newItem = {
          name: '',
          description: '',
          source: {
            name: '',
          },
        }
        this.currentNode.mapperConfig.numberCalculate.items.push(newItem)
      },
      handleDeleteItem(index) {
        const items = this.currentNode.mapperConfig.numberCalculate.items
        if (items.length === 1) {
          items[0].name = ''
          items[0].description = ''
          items[0].source.name = ''
          return
        }
        items.splice(index, 1)
      },
      getTagErrorType(row) {
        return this.getElementName(row.source.name, true) ===
          '参数不合法，请重新选择'
          ? 'danger'
          : ''
      },
    },
  }
</script>

