<template>
  <div class="group-panel" v-loading="loading">
    <div class="group-panel-search-bar">
      <el-autocomplete
        size="mini"
        class="autocomplete"
        popper-class="my-autocomplete"
        v-model="keyword"
        :fetch-suggestions="apiSearch"
        placeholder="请输入名称查找"
        @select="handleSelect"
      >
        <i class="el-icon-search el-input__icon" slot="suffix"></i>
        <template slot-scope="{ item }">
          <div class="name">{{ item.name }}</div>
        </template>
      </el-autocomplete>
      <div class="btn-group">
        <el-dropdown trigger="click" @command="handleCommand">
          <el-button type="text">添加</el-button>
          <el-dropdown-menu class="custom-api-dropdown" slot="dropdown">
            <el-dropdown-item class="drop-down-item" command="api" :disabled="!apiGroupList.length"
              >新增API
            </el-dropdown-item>
            <el-dropdown-item class="drop-down-item" command="group"
              >新增分组
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <div class="group-list">
      <n-tree
        ref="nodeTree"
        class="api-group-tree"
        :data="apiGroupList"
        node-key="id"
        :expand-on-click-node="true"
        default-expand-all
        highlight-current
        :draggable="false"
        icon-class="icon-right"
        :props="{children: 'apis'}"
      >
        <div
          class="tree-node"
          :class="{
            'is-selected': isSelected(data),
          }"
          slot-scope="{ data }"
          @click.stop="handleSelect(data)"
        >
          <div class="node-name">
            <img class="node-icon" :src="getIcon(data)" alt="" />
            <div v-if="data.rename">
              <el-input v-model="data.name" @blur="handleRename(data, data.name)" @keyup.enter.native="handleRename(data, data.name)" placeholder="请输入分组名"></el-input>
            </div>
            <span v-else>{{ data.name }}</span>
          </div>
          <el-dropdown
            v-if="data.apis"
            class="node-right-menu-icon"
            trigger="click"
            @command="(v) => handleApiGroup(v, data)"
          >
            <span class="el-dropdown-link" @click.stop>
              <i class="el-icon-more el-icon--right"></i>
            </span>
            <el-dropdown-menu class="custom-api-dropdown" slot="dropdown">
              <el-dropdown-item command="edit">重命名</el-dropdown-item>
              <el-dropdown-item
                command="delete"
                v-show="data.apis.length === 0"
              >
                删除
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </n-tree>
    </div>
  </div>
</template>

<script>
  import _ from 'lodash';
  import customApi from '@/api/customApi'
  import treeTool from 'tree-tool'
  const treeUtil = treeTool.createInstance({
    id: 'id',
    children: 'apis',
    pid: 'parentId',
  })
  function createFilter(queryString) {
    return (api) => {
      return api.name.toLowerCase().indexOf(queryString.toLowerCase()) !== -1
    }
  }
  export default {
    name: 'GroupPanel',
    props: {
      defaultSelectId: {
        type: Number,
      }
    },
    data() {
      return {
        keyword: '',
        apiGroupList: [],
        loading: false,
      }
    },
    watch: {
      defaultSelectId: {
        handler(n, o) {
          if (n) {
            this.$nextTick(() => {
              this.$refs.nodeTree?.setCurrentKey(n)
            })
          } else {
            this.$nextTick(() => {
              this.$refs.nodeTree?.setCurrentKey(null)
            })
          }
        },
        immediate: true,
      },
    },
    methods: {
      apiSearch(queryString, cb) {
        const results = queryString
          ? treeUtil.filter(this.apiGroupList, createFilter(queryString))
          : this.apiGroupList
        cb(treeUtil.toList(results).filter(item => !item.apis))
      },
      handleSelect(api) {
        if (api.apis) {
          return
        }
        this.$emit('select', _.cloneDeep(api))
      },
      handleCommand(command) {
        this.$emit('create', command)
      },
      handleGetApiGroupList() {
        this.loading = true
        return this.$http({
          url: customApi.apiGroupList,
          data: {
            appId: +this.$route.query.id,
          },
        })
          .then((res) => {
            this.apiGroupList = res.data
            this.$emit('update', this.apiGroupList)
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleApiGroupRemoveById(id) {
        this.loading = true
        this.$http({
          url: customApi.apiGroupRemove,
          data: {
            id,
          },
        })
          .then((res) => {
            this.handleGetApiGroupList()
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleApiGroup(command, api) {
        switch(command) {
          case 'delete': {
            this.handleApiGroupRemoveById(api.id)
            break
          }
          case 'edit': {
            this.$set(api, 'rename', true)
            break
          }
        }
      },
      getIcon(api) {
        if (api.apis) {
          return this.$getImgPrefix('/design/el-icon-folder-opened.png')
        }
        if (api.status === 2) {
          // 已发布
          return this.$getImgPrefix('/design/edit-page.png')
        }
        if (api.status === 1) {
          // 发布中
          return this.$getImgPrefix('/design/el-icon-tickets.png')
        }
        // 未发布
        return this.$getImgPrefix('/design/el-icon-tickets.png')
      },
      isSelected(api) {
        return this.defaultSelectId === api.id
      },
      handleRename(api, name) {
        this.loading = true
        this.$http({
          url: customApi.apiGroupUpdate,
          data: {
            appId: +this.$route.query.id,
            id: api.id,
            name
          },
        })
          .then((res) => {
            this.$set(api, 'rename', false)
            this.handleGetApiGroupList()
          })
          .finally(() => {
            this.loading = false
          })
      }
    },
    created() {
      this.handleGetApiGroupList()
    },
  }
</script>

