<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="data-select-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="数据集编辑"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class='dialog-content' v-loading.lock='loading'>
      <n-form
        v-if='detail'
        class='data-set-save-form'
        ref="dataForm"
        label-position="left"
        :model="detail"
        :rules="rules"
        @submit.native.prevent
        label-width='80px'
      >
        <n-form-item label='名称' prop='name'>
          <el-input v-model.trim='detail.name'></el-input>
        </n-form-item>
        <n-form-item label='描述' prop='description'>
          <el-input type='textarea' v-model.trim='detail.description'></el-input>
        </n-form-item>
      </n-form>
      <data-set-config
        v-if='dialogVisible'
        ref='dataSetConfig'
        :request='{apiRequest, sqlRequest, staticData}'
        :type.sync='activeDataType'
        :is-hide-select='true'
        @response='handleResponse'
      ></data-set-config>
      <el-collapse class='config-collapse' v-model='activeCollapse'>
        <el-collapse-item name='response'>
          <template slot="title">
            <div class='config-collapse-title'>响应体</div>
          </template>
          <div class="body">
            <vue-json-pretty
              v-show='response'
              class="json-pretty"
              path="response"
              :data="response"
              :virtual='true'
              :itemHeight='24'
              :showSelectController="false"
            >
            </vue-json-pretty>
            <div v-if='!response' class='empty-text'>暂无数据</div>
          </div>
        </el-collapse-item>
      </el-collapse>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type='primary' :loading='loading' @click="confirm">确 认</el-button>
    </div>
  </el-dialog>
</template>

<script>

  import _ from 'lodash'
  import DataSetConfig from '@/components/DataSetConfig'
  import VueJsonPretty from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  import API from '@/api/application'
  import { getValidators, required } from '@/utils/validateUtils'

  export default {
    name: 'DataSetDialog',
    components: {
      DataSetConfig,
      VueJsonPretty
    },
    data() {
      return {
        dialogVisible: false,
        loading: false,
        apiRequest: undefined,
        sqlRequest: undefined,
        staticData: undefined,
        response: null,
        activeDataType: 'api',
        activeCollapse: ['response'],
        detail: null,
        rules: {
          name: getValidators([
            {
              message: '请输入名称',
              include: required,
              trigger: 'change',
              required: true
            },
          ]),
          description: getValidators([
            {
              message: '请输入描述信息',
              include: required,
              trigger: 'change',
              required: true
            },
          ]),
        }
      }
    },
    methods: {
      open(data) {
        this.detail = _.cloneDeep(data)
        const config = JSON.parse(data.config)
        const { apiRequest, sqlRequest, staticData } = config
        this.staticData = _.cloneDeep(staticData) || undefined
        this.apiRequest = _.cloneDeep(apiRequest) || undefined
        this.sqlRequest = _.cloneDeep(sqlRequest) || undefined
        this.response = null
        this.dialogVisible = true
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      async confirm() {
        await this.$refs.dataForm.validate()
        const { apiRequest, sqlRequest, staticData } = this.$refs.dataSetConfig.getResult()
        this.apiRequest = apiRequest
        this.sqlRequest = sqlRequest
        this.staticData = staticData
        const config = JSON.stringify({apiRequest, sqlRequest, staticData})
        this.handleUpdate(config)
      },
      handleResponse({ response, responseOptions }) {
        this.response = response
        this.responseOptions = responseOptions
      },
      handleUpdate(config) {
        this.loading = true
        this.$http({
          url: API.datasetManageUpdate,
          data: {
            ...this.detail,
            config
          },
        })
          .then(({ data }) => {
            this.$message.success('保存成功！')
            this.$emit('refresh')
            this.dialogVisible = false
          })
          .finally(() => {
            this.loading = false
          })
      }
    }
  }
</script>

<style lang='less' scoped>
  .dialog-content::v-deep {
    .body {
      .json-pretty {
        max-height: 170px;
      }
    }
  }
</style>
