<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="布尔值格式化"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible">
      <Condition :condition="currentNode.mapperConfig.booleanMap" :showSelf="true"/>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="handleConfirm">确 认</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import Condition from '../Condition.vue'
  import _ from 'lodash'
  import mixins from '../mixins'
  const initialItems = [
    {
      items: [
        {
          left: {
            source: {
              expression: '',
              name: '',
            },
            type: undefined,
            value: undefined,
          },
          logic: 'or',
          op: 'EQ',
          right: {
            source: {
              expression: '',
              name: '',
            },
            type: undefined,
            value: undefined,
          },
        },
      ],
      logic: 'or',
    },
    {
      items: [
        {
          left: {
            source: {
              expression: '',
              name: '',
            },
            type: undefined,
            value: undefined,
          },
          logic: 'or',
          op: 'EQ',
          right: {
            source: {
              expression: '',
              name: '',
            },
            type: undefined,
            value: undefined,
          },
        },
      ],
      logic: 'or',
    },
  ]
  export default {
    name: 'StringFormatter',
    components: {
      Condition,
    },
    mixins: [mixins],
    data() {
      return {
        dialogVisible: false,
        currentNode: null,
      }
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      open({ currentNode }) {
        this.currentNode = _.cloneDeep(currentNode)
        this.handleAddAttr()
        this.dialogVisible = true
      },
      handleConfirm() {
        const booleanMap = this.currentNode.mapperConfig.booleanMap.conditionItems
        const items = booleanMap.map(item => item.items).flat()
        if (items.filter(item => item.left.source.name || item.left.value || item.right.source.name || item.right.value).length === 0) {
          this.$message.error('请配置格式化')
          return
        }
        this.$emit('confirm', this.currentNode)
        this.dialogVisible = false
      },
      handleAddAttr() {
        if (!this.currentNode.mapperConfig) {
          this.$set(this.currentNode, 'mapperConfig', {
            type: 'BOOLEAN_MAP',
            booleanMap: {
              conditionItems: _.cloneDeep(initialItems),
            },
          })
        } else {
          if (!this.currentNode.mapperConfig.booleanMap.conditionItems.length) {
            this.currentNode.mapperConfig.booleanMap.conditionItems = _.cloneDeep(initialItems)
          }
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .dialog-content::v-deep {
  }
</style>
