<template>
  <el-dialog
    class="settings-manage"
    title="应用设置"
    :visible="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    @close="dialogVisible = false"
  >
    <el-tabs v-model="activeName">
      <el-tab-pane label="导航设置" name="navigate">
        <el-form>
          <el-form-item label="显示导航">
            <el-switch
              v-model="settings.navigateShow"
              @change="updateSetting"
            ></el-switch>
          </el-form-item>
          <el-form-item label="导航位置">
            <el-radio-group v-model="settings.navigatePosition" @change="updateSetting">
              <el-radio-button label="left">左侧</el-radio-button>
              <el-radio-button label="top">顶部</el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="显示工具栏">
            <el-switch
              v-model="settings.headerShow"
              @change="updateSetting"
            ></el-switch>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="调试设置" name="debug">
        <el-form>
          <el-form-item label="编辑页调试">
            <el-switch
              v-model="settings.edit"
              @change="updateSetting"
            ></el-switch>
          </el-form-item>
          <el-form-item label="预览页调试">
            <el-switch
              v-model="settings.preview"
              @change="updateSetting"
            ></el-switch>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>

    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">关 闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
import API from '@/api/application'
import KEY from '@/store/base-info/mutation-types'
import { mapMutations } from 'vuex'
import _ from 'lodash'
import { formatSetting } from './config'
export default {
  components: {},
  props: {},
  data() {
    return {
      dialogVisible: false,
      activeName: 'navigate',
      settings: {},
    }
  },
  created() {},
  mounted() {},
  methods: {
    ...mapMutations({
      setVersionInfo: KEY.VERSION_INFO,
    }),
    open() {
      this.dialogVisible = true
    },
    updateSetting() {
      this.$http({
        url: API.updateSettings,
        data: {
          appDesignId: this.versionInfo.id,
          settings: JSON.stringify(this.settings),
        },
      }).then(() => {
        this.setVersionInfo({
          ...this.versionInfo,
          settings: JSON.stringify(this.settings),
        })
      })
    },
  },

  computed: {
    versionInfo() {
      return this.$store.state.CODE.baseInfo.VERSION_INFO
    },
  },

  watch: {
    versionInfo: {
      immediate: true,
      handler(val) {
        this.settings = formatSetting(val.settings)
      },
    },
  },
}
</script>
