<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="文本格式化"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible">
      <n-tabs
        v-model="currentNode.mapperConfig.type"
        type="card"
        @tab-click="handleChangeType"
      >
        <n-tab-pane label="正则提取" name="TEXT_EXTRACT">
          <SourceSelect
            :source="currentNode.mapperConfig.textExtract.source"
            @change="handleSourceChange"
          />
          <div class="input-item" style="margin-top: 10px;">
            <div class="label">正则表达式：</div>
            <el-input
              class="input"
              v-model="currentNode.mapperConfig.textExtract.regex"
              placeholder="请输入正则表达式"
            ></el-input>
          </div>
        </n-tab-pane>
        <n-tab-pane label="文本替换" name="TEXT_REPLACE">
          <SourceSelect
            :source="currentNode.mapperConfig.textReplace.source"
            @change="handleSourceChange"
          />
          <div class="input-item" style="margin-top: 10px;">
            <div class="label">文本内容：</div>
            <el-input
              class="input"
              v-model="currentNode.mapperConfig.textReplace.searchText"
              placeholder="请输入"
            ></el-input>
            <el-checkbox
              style="margin-left: 10px;"
              v-model="currentNode.mapperConfig.textReplace.regular"
              >正则表达式</el-checkbox
            >
          </div>
          <div class="input-item" style="margin-top: 10px;">
            <div class="label">替换为：</div>
            <el-input
              class="input"
              v-model="currentNode.mapperConfig.textReplace.replacement"
              placeholder="请输入"
            ></el-input>
          </div>
        </n-tab-pane>
        <n-tab-pane label="文本合并" name="TEXT_JOIN">
          <div style="display: flex; align-items: center; margin-bottom: 10px;">
            <div class="label" style="width: 100px;">
              <span style="color: red;">*</span>文本类型：
            </div>
            <SourceSelect
              :showLabel="false"
              title="选择参数"
              :source="{ name: '' }"
              @change="handleAddTextJoinVar"
            />
            <el-button
              style="margin-left: 20px;"
              size="mini"
              type="primary"
              @click="showInputDialog('text', { value: '' }, 'value')"
              >选择常量
            </el-button>
          </div>
          <div style="display: flex;">
            <div class="label" style="width: 100px;padding-left: 5px;">
              文本内容：
            </div>
            <div class="select-list">
              <el-tag
                v-for="(params, index) in currentNode.mapperConfig.textJoin
                  .joinParams"
                :key="index"
                closable
                @close="
                  currentNode.mapperConfig.textJoin.joinParams.splice(index, 1)
                "
                >{{ params.value || params.source.name }}</el-tag
              >
            </div>
          </div>

          <div class="input-item" style="margin-top: 10px;">
            <div class="label">
              分隔符：
              <el-tooltip popper-class="tooltip-question" placement="left">
                <div slot="content">合并后参数之间展示的间隔符号，不填则默认没有间隔符</div>
                <i class="el-icon-question test-btn tooltip"></i>
              </el-tooltip>
            </div>
            <el-input
              class="input"
              v-model="currentNode.mapperConfig.textJoin.separator"
              placeholder="请输入"
            ></el-input>
          </div>
        </n-tab-pane>
        <n-tab-pane label="日期转换" name="TEXT_DATE_FORMAT">
          <SourceSelect
            :source="currentNode.mapperConfig.textDateFormat.source"
            @change="handleSourceChange"
          />
          <div class="input-item" style="margin-top: 10px;">
            <div class="label">源格式：</div>
            <el-select
              size="mini"
              v-model="currentNode.mapperConfig.textDateFormat.srcPattern"
            >
              <el-option
                v-for="option in dateFormatOptions"
                :key="option.value"
                :label="option.label"
                :value="option.value"
              ></el-option>
            </el-select>
          </div>
          <div class="input-item" style="margin-top: 10px;">
            <div class="label">目标格式：</div>
            <el-select
              size="mini"
              v-model="currentNode.mapperConfig.textDateFormat.destPattern"
            >
              <el-option
                v-for="option in dateFormatOptions.slice(0, -1)"
                :key="option.value"
                :label="option.label"
                :value="option.value"
              ></el-option>
            </el-select>
          </div>
        </n-tab-pane>
      </n-tabs>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="handleConfirm">确 认</el-button>
    </div>
    <custom-input-dialog
      @confirm="handleAddTextJoinValue"
      ref="inputDialog"
    ></custom-input-dialog>
  </el-dialog>
</template>

<script>
  import _ from 'lodash'
  import SourceSelect from './SourceSelect'
  import mixins from '../mixins'
  export default {
    name: 'StringFormatter',
    components: {
      SourceSelect,
    },
    mixins: [mixins],
    data() {
      return {
        dialogVisible: false,
        currentNode: null,
        dateFormatOptions: [
          {
            label: 'YYYY-MM-DD',
            value: 'yyyy-MM-dd',
          },
          {
            label: 'YYYY-MM-DD HH:mm',
            value: 'yyyy-MM-dd HH:mm',
          },
          {
            label: 'YYYY-MM-DD HH:mm:ss',
            value: 'yyyy-MM-dd HH:mm:ss',
          },
          {
            label: 'HH:mm',
            value: 'HH:mm',
          },
          {
            label: 'HH:mm:ss',
            value: 'HH:mm:ss',
          },
          {
            label: '时间戳',
            value: 'TIMESTAMP',
          },
        ],
      }
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      open({ currentNode }) {
        this.currentNode = _.cloneDeep(currentNode)
        this.handleAddAttr()
        this.dialogVisible = true
      },
      handleConfirm() {
        let isSelectSource = false
        switch(this.currentNode.mapperConfig.type) {
          case 'TEXT_EXTRACT': {
            isSelectSource = !!this.currentNode.mapperConfig.textExtract.source.name
            if (!this.currentNode.mapperConfig.textExtract.regex) {
              this.$message.error('请输入正则表达式')
              return
            }
            break
          }
          case 'TEXT_REPLACE': {
            isSelectSource = !!this.currentNode.mapperConfig.textReplace.source.name
            if (!this.currentNode.mapperConfig.textReplace.searchText) {
              this.$message.error('请输入文本内容')
              return
            }
            break
          }
          case 'TEXT_JOIN': {
            isSelectSource = !!this.currentNode.mapperConfig.textJoin
                .joinParams.filter(item => item.value || item.source.name).length
            break
          }
          case 'TEXT_DATE_FORMAT': {
            isSelectSource = !!this.currentNode.mapperConfig.textDateFormat.source.name
            if (!this.currentNode.mapperConfig.textDateFormat.srcPattern) {
              this.$message.error('请选择源格式')
              return
            }
            if (!this.currentNode.mapperConfig.textDateFormat.destPattern) {
              this.$message.error('请选目标格式')
              return
            }
            break
          }
          default: {
            isSelectSource = true
            break
          }
        }
        if (!isSelectSource) {
          this.$message.error('请选择参数')
          return
        }
        this.$emit('confirm', this.currentNode)
        this.dialogVisible = false
      },
      handleAddAttr() {
        if (!this.currentNode.mapperConfig) {
          this.$set(this.currentNode, 'mapperConfig', {
            type: 'TEXT_EXTRACT',
            textExtract: {
              regex: '',
              source: {
                name: '',
              },
            },
            textDateFormat: {
              destPattern: '',
              source: {
                name: '',
              },
              srcPattern: '',
            },
            textJoin: {
              joinParams: [],
              separator: '',
            },
            textReplace: {
              regular: false,
              replacement: '',
              searchText: '',
              source: {
                name: '',
              },
            },
          })
        }
      },
      handleSourceChange(fullVariable) {},
      handleAddTextJoinVar(fullVariable) {
        this.currentNode.mapperConfig.textJoin.joinParams.push({
          source: {
            name: fullVariable,
          },
        })
      },
      handleAddTextJoinValue(value) {
        if (!value) {
          return
        }
        this.currentNode.mapperConfig.textJoin.joinParams.push({
          source: {
            name: '',
          },
          value,
        })
      },
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .dialog-content::v-deep {
    .input-item {
      display: flex;
      align-items: center;
      .label {
        white-space: nowrap;
        width: 90px;
      }
      .input {
        flex: 1;
      }
    }

    .select-list {
      display: flex;
      align-items: flex-start;
      flex-wrap: wrap;
      width: 100%;
      height: 100px;
      overflow: auto;
      border: 1px solid #eee;
      border-radius: 3px;
      padding: 5px;
    }
  }
</style>
