<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="发布自定义API"
    :visible.sync="dialogVisible"
    width="1000px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible" v-loading="loading">
      <div class="api-detail">
        <div class="title">
          <span class="t">API信息</span>
        </div>
        <div class="detail-content">
          <el-row class="detail-item">
            <el-col :span="2">API名称</el-col>
            <el-col :span="10">{{ apiData.name }}</el-col>
            <el-col :span="2">分组</el-col>
            <el-col :span="10">{{ apiGroupName }}</el-col>
          </el-row>
          <el-row class="detail-item">
            <el-col :span="2">API路径</el-col>
            <el-col :span="2"><el-tag>POST</el-tag></el-col>
            <el-col :span="20">{{ pathPrepend + apiData.path }}</el-col>
          </el-row>
          <el-row class="detail-item">
            <el-col :span="2">API描述</el-col>
            <el-col :span="22">{{ apiData.description }}</el-col>
          </el-row>
        </div>
      </div>
      <div class="api-res">
        <div class="api-request">
          <div class="title">
            <span class="t">入参定义</span>
          </div>
          <div class="body">
            <vue-json-pretty
              v-show="apiData.requestBody"
              class="json-pretty"
              :data="apiData.requestBody"
              :virtual="true"
              :itemHeight="32"
            >
            </vue-json-pretty>
            <div v-if="!apiData.requestBody" class="empty-text">暂无数据</div>
          </div>
        </div>
        <div class="api-response">
          <div class="title">
            <span class="t">响应体</span>
          </div>
          <div class="body">
            <vue-json-pretty
              v-show="response"
              class="json-pretty"
              :data="response"
              :virtual="true"
              :itemHeight="32"
            >
            </vue-json-pretty>
            <div v-if="!response" class="empty-text">暂无数据</div>
          </div>
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="handleConfirm">发 布</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import customApi from '@/api/customApi'
  import { Factory } from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  const VueJsonPretty = new Factory({ virtualLines: 20 })
  export default {
    name: 'Publish',
    components: {
      VueJsonPretty,
    },
    inject: ['customApiProvide'],
    props: {
      apiGroupList: {
        type: Array,
        default: () => [],
      },
    },
    data() {
      return {
        loading: false,
        dialogVisible: false,
        response: null,
        apiData: null,
        pathPrepend: `/app${this.$route.query.id}/`,
      }
    },
    computed: {
      apiGroupName() {
        return this.apiGroupList.find(item => item.id === this.apiData?.groupId)?.name
      }
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      open({apiData, response}) {
        this.apiData = apiData
        this.response = response
        this.dialogVisible = true
      },
      async handleConfirm() {
        await this.$confirm('发布后可在网关API中选用', '是否保存并发布至网关')
        this.loading = true
        this.$http({
          url: customApi.apiDesignPublish,
          data: {
            id: this.apiData.id
          },
        })
          .then((res) => {
            this.apiData.status = 2
            this.$message.success('发布成功！')
            this.$emit('success')
            this.dialogVisible = false
          })
          .finally(() => {
            this.loading = false
          })
      },
    },
  }
</script>

