<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="转换器"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible">
      <div class="config">
        <div class="title">
          <span class="t">步骤出参JSON源</span>
        </div>
        <json-code
          class="json-code-editor"
          :isObject="true"
          :readOnly="true"
          v-model="source"
        ></json-code>
      </div>
      <el-button
        size="mini"
        style="margin: 0 10px;"
        type="primary"
        @click="handleCopy"
        >一键复制<i style="margin-left: 5px;" class="el-icon-arrow-right"></i
      ></el-button>
      <div class="config">
        <div class="title">
          <span class="t">出参格式化JSON</span>
          <el-tooltip popper-class="tooltip-question" placement="top">
            <div slot="content">编辑步骤出参格式化后的JSON内容，可快速生成树形结构
            </div>
            <i class="el-icon-question"></i>
          </el-tooltip>
        </div>
        <json-code
          class="json-code-editor"
          :isObject="true"
          v-model="map"
        ></json-code>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="handleConfirm">确 认</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import _ from 'lodash';
  import JsonCode from '@/components/JsonCode'
  import { CUSTOM_API_RESPONSE_MAP } from '@/config'
  export default {
    name: 'TreeChange',
    components: {
      JsonCode,
    },
    data() {
      return {
        dialogVisible: false,
        source: {},
        map: {},
      }
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      async handleConfirm() {
        await this.$confirm('已配置的格式化数据将被覆盖，是否继续？')
        this.$emit('confirm', this.map)
        this.dialogVisible = false
      },
      open({ stepData } = {}) {
        const source = window[CUSTOM_API_RESPONSE_MAP][`${stepData.code}_source`]
        this.source = _.cloneDeep(source)
        this.map = {}
        this.dialogVisible = true
      },
      handleCopy() {
        this.map = _.cloneDeep(this.source)
      }
    },
  }
</script>

