<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="input-dialog"
    :title="title"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1000px"
    :before-close="handleClose"
    v-element-screen
  >
    <div v-if="dialogVisible" class="dialog-body">
      <el-input
        class="input-item"
        v-if="type === 'text'"
        v-model="value"
        type="textarea"
        :rows="3"
        placeholder="请输入文本"
      ></el-input>
      <el-input-number
        class="input-item"
        v-if="type === 'number'"
        v-model="value"
        placeholder="请输入数值"
      ></el-input-number>
      <div class="json-input" v-if="type === 'object'">
        <el-tabs v-model="activeName" type="card">
          <el-tab-pane label="树形结构" name="tree">
            <JsonEditor
              class='json-editor'
              v-if='activeName === "tree"'
              :callback-id='callbackId'
              v-model="value"
            >
            </JsonEditor>
          </el-tab-pane>
          <el-tab-pane name="json">
            <span slot="label">
                  JSON
                  <el-tooltip popper-class="tooltip-question" placement="top">
                    <div slot="content">
JSON文件都是被包裹在一个大括号{}或者方括号[]中，通过key-value的方式来表达数据。JSON的Key必须包裹在一个双引号中，在实践中，编写 JSON 的时候，忘了给 Key 值加双引号或者是把双引号写成单引号是常见错误。

JSON的值只能是以下几种数据格式，其他任何格式都会触发报错，例如 JavaScript 中的 undefined。

1.数字，包含浮点数和整数
2.字符串，需要包裹在双引号中
3.Bool值，true 或者 false
4.数组，需要包裹在方括号中 []
5.对象，需要包裹在大括号中 {}
6.Null

还需要注意的是 JSON 文件中无法使用注释，试图添加注释将会引发报错
                    </div>
                    <i class="el-icon-question"></i>
                  </el-tooltip>
                  </span>
            <json-code v-if='activeName === "json"' class="json-code-editor" v-model="value"></json-code>
          </el-tab-pane>
        </el-tabs>
      </div>
      <div class='js-input' v-if='type === "js"'>
        <js-config v-model='value' class='input-item'></js-config>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="cancel">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import JsonCode from '@/components/JsonCode'
  import JsConfig from '@/components/JsConfig'
  import JsonEditor from '@/components/CustomApi/components/JsonTreeEditor/JsonEditor'
  import { isValidJsonString } from '@/utils/validateUtils'
  export default {
    name: 'InputDialog',
    components: {
      JsonCode,
      JsonEditor,
      JsConfig,
    },
    data() {
      return {
        dialogVisible: false,
        activeName: 'tree',
        type: '',
        reference: null,
        value: undefined,
        key: '',
        from: '',
      }
    },
    computed: {
      title() {
        switch(this.type) {
          case 'text': return '文本编辑'
          case 'number': return '数值编辑'
          case 'object': return 'JSON编辑'
          case 'js': return 'JS脚本编辑'
          case 'css': return 'CSS样式编辑'
          default: return '编辑'
        }
      },
    },
    methods: {
      open({ type, data, key, from }) {
        this.type = type
        this.reference = data || {}
        this.key = key
        this.from = from
        this.value = this.getValue(this.type, data[key])
        this.dialogVisible = true
      },
      getValue(type, value) {
        switch (type) {
          case 'text':
            return value || ''
          case 'number':
            return value || 0
          case 'object':
            return value || {}
          default:
            return value
        }
      },
      cancel() {
        this.dialogVisible = false
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      async confirm() {
        let value
        if (this.type === 'object') {
          if (typeof this.value === 'object') {
            value = this.value
          } else if (isValidJsonString(this.value)) {
            try {
              value = JSON.parse(this.value)
            } catch (e) {
              console.log(e)
            }
          }
        } else {
          value = this.value
        }
        if (typeof value === 'undefined' && typeof this.value !== 'undefined') {
          await this.$confirm('JSON格式不正确', '提示', {
            showCancelButton: false,
          })
          return
        }
        this.reference[this.key] = value
        this.$emit('confirm', value, this.from)
        this.dialogVisible = false
      },
      handleAttrInput(value) {
        this.value = value
      }
    },
  }
</script>

<style lang="less" scoped>
  .input-dialog::v-deep {
    .el-dialog.is-fullscreen {
      .el-dialog__body {
        .json-editor, .json-code-editor {
          height: calc(100vh - 220px);
        }
      }
    }
    .el-dialog__body {
      .json-editor, .json-code-editor {
        height: calc(100vh - 270px - 15vh);
        font-size: 12px;
        overflow-y: auto;
        overflow-x: hidden;
      }
      .json-code-editor {
        overflow: hidden;
      }
    }
  }
  .dialog-body::v-deep {
    .json-input {
      .el-tabs__content {
        overflow: visible;
      }
    }
    .input-item {
      width: 100%;
    }
  }
</style>
