<template>
  <el-dialog
    custom-class="page-history-manage"
    title=""
    :visible="dialogVisible"
    :close-on-click-modal="true"
    :modal-append-to-body="true"
    :append-to-body="true"
    :before-close="beforeClose"
    width="290px"
    top="0"
  >
    <div class="dialog-content">
      <div class="dialog-header">
        <div class="title">快照记录</div>
        <el-button type="primary" size="mini" @click="handleSaveClick">快照备份</el-button>
      </div>
      <div class="dialog-body" v-loading="loading">
        <template v-if="historyDetail">
          <div class="history-list" v-if="historyDetail.records.length">
            <div class="history-item" v-for="item in historyDetail.records" :key="item.id" @click="handleSelectHistory(item)">
              <div class="left">
                <div class="time">{{formatTime(item.createdTime)}}</div>
                <div class="user">{{item.createdBy}}</div>
                <div class="desc" :title="item.description">{{item.description}}</div>
              </div>
              <div class="right">
                <el-button type="text" style="color: red;" @click.stop="handleDelete(item.id)">删除</el-button>
              </div>
            </div>
          </div>
          <n-empty v-else description="暂无数据"></n-empty>
          <el-button v-if="historyDetail.rowCount > pageCount * 10 && historyDetail.records.length <= pageCount * 10" class="load-more" type="text" @click="handleShowMore">显示更多</el-button>
          <div v-else class="load-more">已经没有更多数据了</div>
        </template>
      </div>
      <el-dialog
        title="快照备份"
        :visible="showSaveDialog"
        :close-on-click-modal="false"
        :modal-append-to-body="true"
        :append-to-body="true"
        :before-close="beforeSaveClose"
        width="500px"
        top="15vh"
      >
        <div class="history-save-dialog-content">
          <div class="form-item">
            <div class="form-label">创建时间</div>
            <div class="form-value">{{formData.createTime}}</div>
          </div>
          <div class="form-item">
            <div class="form-label">创建人</div>
            <div class="form-value">{{formData.user}}</div>
          </div>
          <div class="form-item">
            <div class="form-label">快照描述</div>
            <div class="form-value">
              <el-input
                type="textarea"
                :rows="4"
                placeholder="请输入内容"
                maxlength="30"
                show-word-limit
                v-model="formData.description"></el-input>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="showSaveDialog = false">取 消</el-button>
          <el-button type="primary" @click="handleSave">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </el-dialog>
</template>

<script>
import API from '@/api/application'
import { SAVE_PAGE_CONFIG, UPDATE_PAGE_CONFIG } from '@/config/storeKey'
import { formatTime } from '@/utils/format'
import { pageInject } from '@/renderer/tools/pageStore'
import { reset } from '@/renderer/tools/history'
export default {
  name: 'PageHistoryManage',
  inject: [...pageInject],
  data() {
    return {
      dialogVisible: false,
      loading: false,
      historyDetail: null,
      showSaveDialog: false,
      pageCount: 1,
      formData: {
        description: '',
        createTime: '',
        user: ''
      }
    }
  },
  methods: {
    formatTime,
    open() {
      this.handleGetHistoryList()
      this.dialogVisible = true
    },
    beforeClose(done) {
      this.dialogVisible = false
      done()
    },
    handleGetHistoryList() {
      this.loading = true
      const pageDesignId = this.getElementPropsById(this.getStore().layoutData.elementId).pageDesignId
      this.$http({
        url: API.appDesignPageSnapshotPaging,
        data: {
          condition: {
            pageDesignId
          },
          pagination: {
            pagenum: 1,
            pagesize: this.pageCount * 10,
            sort: {
              name: '',
              type: ''
            }
          }
        },
      })
        .then(({ data }) => {
          this.historyDetail = data
        })
        .finally(() => {
          this.loading = false
        })
    },
    async handleDelete(id) {
      await this.$confirm('确认删除这条快照记录吗？')
      this.loading = true
      this.$http({
        url: API.appDesignPageSnapshotRemove,
        data: {
          pageSnapshotId: id,
        },
      })
        .then(({ data }) => {
          this.handleGetHistoryList()
        })
        .finally(() => {
          this.loading = false
        })
    },
    getElementPropsById(elementId) {
      const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
      return this.getStore().dynamicProps[realId] || {}
    },
    async handleSave() {
      if (!this.formData.description) {
        this.$message.error('请输入快照描述！')
        return
      }
      await this._storeDispatch(SAVE_PAGE_CONFIG, { isToast: false })
      this.handleSaveHistory()
    },
    handleSaveHistory() {
      this.loading = true
      const pageDesignId = this.getElementPropsById(this.getStore().layoutData.elementId).pageDesignId
      return this.$http({
        url: API.appDesignPageSnapshotSave,
        data: {
          pageDesignId,
          description: this.formData.description
        },
      })
        .then(({ data }) => {
          this.handleGetHistoryList()
          this.showSaveDialog = false
        })
        .finally(() => {
          this.loading = false
        })
    },
    beforeSaveClose(done) {
      this.showSaveDialog = false
      done()
    },
    handleSaveClick() {
      const loginKey = window.rootStore.state.loginKey || {}
      this.formData = {
        description: '',
        createTime: formatTime(),
        user: loginKey.account
      }
      this.showSaveDialog = true
    },
    handleGetHistoryConfigById(id) {
      this.loading = true
      this.$http({
        url: API.appDesignPageSnapshotRevertToSnapshot,
        data: {
          pageSnapshotId: id
        },
      })
        .then(({ data }) => {
          this.getPageConfig()
        })
        .finally(() => {
          this.loading = false
        })
    },
    async handleSelectHistory(history) {
      this.$confirm('恢复快照记录将会覆盖当前页面的所有配置项且不可回退，是否将当前页面配置进行快照备份', '快照恢复提示', {
        type: 'info',
        cancelButtonText: '暂不保存',
        confirmButtonText: '保存备份',
        closeOnClickModal: false,
        closeOnPressEscape: false,
        showClose: false,
      }).then(async () => {
        this.formData.description = '该备份为最新的一次快照备份记录'
        await this.handleSaveHistory()
        this.handleGetHistoryConfigById(history.id)
      })
      .catch(() => {
        this.handleGetHistoryConfigById(history.id)
      })
    },
    getPageConfig() {
      const loading = this.$loading({
        lock: true,
        text: '正在请求页面配置',
      })
      setTimeout(() => {
        const elementId = this.getStore().layoutData.elementId
        const pageDesignId = this.getElementPropsById(elementId).pageDesignId
        this._storeDispatch(UPDATE_PAGE_CONFIG, { elementId, pageDesignId, appDesignId: this.$route.query.vid })
          .then(() => {
            this.$message.success('恢复快照成功！')
            reset()
            this.dialogVisible = false
          })
          .finally(() => {
            loading.close()
          })
      }, 100)
    },
    handleShowMore() {
      this.pageCount++
      this.handleGetHistoryList()
    }
  }
}
</script>

<style lang="less">
.page-history-manage {
  position: absolute;
  top: 100px;
  right: 20px;
  height: 80vh;
  margin: 0;
  .el-dialog__header {
    display: none;
  }
  .el-dialog__body {
    height: 100%;
    padding: 0;
  }
  .dialog-content {
    width: 100%;
    height: 100%;
    .dialog-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px;
    }
    .dialog-body {
      width: 100%;
      height: calc(100% - 48px);
      overflow-y: auto;
      .history-list {
        box-sizing: border-box;
        width: 100%;
        padding: 10px;
        .history-item {
          display: flex;
          border: 1px solid #eee;
          border-radius: 4px;
          margin-bottom: 10px;
          padding: 10px;
          &:hover {
            cursor: pointer;
            background-color: #ebf2f6;
            .right {
              display: flex;
            }
          }
          .left {
            width: calc(100% - 36px);
            font-size: 12px;
            .desc {
              overflow:hidden;
              white-space:nowrap;
              text-overflow:ellipsis;
              width:100%;
            }
          }
          .right {
            display: none;
            align-items: center;
            justify-content: center;
            width: 36px;
          }
        }
      }
      .load-more {
        width: 100%;
        height: 60px;
        line-height: 60px;
        text-align: center;
      }
    }
  }
}
</style>

<style scoped lang="less">
.history-save-dialog-content::v-deep {
  .form-item {
    display: flex;
    margin-top: 10px;
    .form-label {
      width: 100px;
    }
    .form-value {
      flex: 1;
    }
  }
}
</style>