<template>
  <el-dialog
    custom-class="low-code-dialog"
    ref="dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="API测试日志"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible" v-loading="loading">
      <div class="response-detail" :style="{ height: `${responseHeight}px` }">
        <div class="handler" @click="handleToggleShowResponse">
          响应体
          <i
            :class="
              isShowResponse ? 'el-icon-arrow-right' : 'el-icon-arrow-left'
            "
          ></i>
        </div>
        <div
          v-if="isShowResponse"
          class="content"
          :class="{
            show: isShowResponse,
          }"
        >
          <vue-json-pretty
            :style="{ height: `${responseHeight - 20}px` }"
            v-show="response"
            class="json-pretty"
            :data="response"
            :virtual="true"
            :itemHeight="32"
          >
          </vue-json-pretty>
          <div v-if="!response" style="text-align: center; margin: 10px;">暂无数据</div>
        </div>
      </div>
      <div class="mock-info">
        <div class="info-item">
          <img :src="$getImgPrefix('/custom-api/do-status.svg')" class="info-item" />
          <div class="info-content">
            <div class="info-title">{{ status }}</div>
            <div class="info-desc">执行状态</div>
          </div>
        </div>
        <div class="info-item">
          <img :src="$getImgPrefix('/custom-api/time.svg')" class="info-item" />
          <div class="info-content">
            <div class="info-title">{{ time }} ms</div>
            <div class="info-desc">总耗时</div>
          </div>
        </div>
        <div class="info-item">
          <img :src="$getImgPrefix('/custom-api/successCount.svg')" class="info-item" />
          <div class="info-content">
            <div class="info-title">{{ successCount }}个</div>
            <div class="info-desc">成功步骤</div>
          </div>
        </div>
        <div class="info-item">
          <img :src="$getImgPrefix('/custom-api/failedCount.svg')" class="info-item" />
          <div class="info-content">
            <div class="info-title">{{ failedCount }}个</div>
            <div class="info-desc">失败步骤</div>
          </div>
        </div>
      </div>
      <div class="mock-request">
        <div class="title" @click="isShowRequest = !isShowRequest">
          <span class="t">入参JSON</span>
          <i
            :class="
              isShowRequest ? 'el-icon-arrow-down' : 'el-icon-arrow-right'
            "
          ></i>
        </div>
        <div class="mock-request-content" v-if="isShowRequest">
          <pre class="json-code">{{ JSON.stringify(apiDetail.requestBody, function(k, v) { return v === undefined ? null : v }, 2) }}</pre>
        </div>
      </div>
      <div class="mock-detail">
        <div class="title">
          <span class="t">测试日志</span>
        </div>
        <template v-if="logData">
          <div
            class="step-item"
            v-for="step in logData.stepDetails"
            :key="step.stepInfo.code"
          >
            <div class="title" @click="handleChangeStepShow(step)">
              <div class="title-left">
                <i
                  class="title-left-icon"
                  :class="
                    step.isShow ? 'el-icon-arrow-down' : 'el-icon-arrow-right'
                  "
                ></i>
                <div class="name">{{ step.stepInfo.name }}</div>
              </div>

              <div class="step-info">
                <span
                  class="step-info-item"
                  :style="{
                    color: step.executeResult.success ? '#5ACD76' : '#F66E6E',
                  }"
                  >{{
                    step.executeResult.success ? '执行成功' : '执行失败'
                  }}</span
                >
                <el-divider direction="vertical"></el-divider>
                <span class="step-info-item"
                  >总耗时：{{
                    step.executeResult.endTime - step.executeResult.beginTime
                  }}
                  ms</span
                >
                <el-divider direction="vertical"></el-divider>
                <span class="step-info-item"
                  >成功逻辑：{{ step.executeResult.successCount }} 个</span
                >
                <el-divider direction="vertical"></el-divider>
                <span class="step-info-item"
                  >失败逻辑：{{ step.executeResult.failedCount }} 个</span
                >
              </div>
            </div>
            <div class="content" v-if="step.isShow">
              <el-button-group>
                <el-button
                  class="action-item-btn"
                  size="mini"
                  v-for="action in step.actionResults"
                  :key="action.actionInfo.code"
                  :class="{
                    success: action.executed
                      ? action.executeResult.success
                      : false,
                    skip: !action.executed,
                    selected: selectedAction === action,
                  }"
                  @click="changeAction(action)"
                  >{{ action.actionInfo.code }}：{{ action.actionInfo.name }}</el-button
                >
              </el-button-group>
              <div class="action-item-detail" v-loading="loading">
                <template v-for="(item, index) in actionLogList">
                  <div class="log-info-item" v-if="item.detail.indexOf('[JSON_FORMAT]') !== 0" :key="index">
                    {{item.detail}}
                  </div>
                  <div v-else :key="index" class="log-info-item json">
                    <el-button type="text" @click="item.showDetail = !item.showDetail">{{item.showDetail ? '收起' : '展开'}}</el-button>
                    <pre class="json-code" v-if="item.showDetail">{{JSON.stringify(JSON.parse(item.detail.replace('[JSON_FORMAT]', '')), function(k, v) { return v === undefined ? null : v }, 2)}}</pre>
                  </div>
                </template>
              </div>
              <div class="res-detail">
                <div class="source">
                  <div class="source-title">
                    源出参
                  </div>
                  <div class="input">
                    <pre>{{ sourceMsg }}</pre>
                  </div>
                </div>
                <div class="source">
                  <div class="source-title">
                    格式化出参
                  </div>
                  <div class="output">
                    <pre>{{ formatMsg }}</pre>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">关 闭</el-button>
      <!-- <el-button type="primary" @click="handleExport">导 出</el-button> -->
    </div>
  </el-dialog>
</template>

<script>
  import customApi from '@/api/customApi'
  import { CUSTOM_API_SESSION_ID, CUSTOM_API_RESPONSE_MAP } from '@/config'
  import { Factory } from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  const VueJsonPretty = new Factory({ virtualLines: 50 })
  export default {
    name: 'ApiMock',
    components: {
      VueJsonPretty,
    },
    inject: ['customApiProvide'],
    data() {
      return {
        dialogVisible: false,
        loading: false,
        apiDetail: {},
        logData: null,
        selectedAction: null,
        selectedStep: null,
        responseHeight: 0,
        isShowResponse: false,
        response: null,
        isShowRequest: true,
        actionLogList: [],
      }
    },
    computed: {
      time() {
        return this.logData
          ? this.logData.executeResult.endTime -
              this.logData.executeResult.beginTime
          : 0
      },
      status() {
        return this.logData?.executeResult?.success ? '执行成功' : '执行失败'
      },
      successCount() {
        return this.logData?.executeResult?.successCount || 0
      },
      failedCount() {
        return this.logData?.executeResult?.failedCount || 0
      },
      inputMsg() {
        const inputMsg = this.selectedAction?.executeResult?.input
        return inputMsg ? JSON.stringify(inputMsg, function(k, v) { return v === undefined ? null : v }, 2) : '无数据'
      },
      outputMsg() {
        const outputMsg = this.selectedAction?.executeResult?.output
        return outputMsg ? JSON.stringify(outputMsg, function(k, v) { return v === undefined ? null : v }, 2) : '无数据'
      },
      errorMsg() {
        const errorMsg = this.selectedAction?.executeResult?.errorReason
        return errorMsg
      },
      sourceMsg() {
        const outputMsg = this.selectedStep?.stepOutput?.output
        return outputMsg ? JSON.stringify(outputMsg, function(k, v) { return v === undefined ? null : v }, 2) : '无数据'
      },
      formatMsg() {
        const outputMsg = this.selectedStep?.stepOutput?.hasMapped
          ? this.selectedStep?.stepOutput?.mappedOutput
          : ''
        return outputMsg ? JSON.stringify(outputMsg, function(k, v) { return v === undefined ? null : v }, 2) : '无数据'
      },
    },
    methods: {
      handleClose(done) {
        this.apiDetail = {}
        this.logData = null
        this.selectedAction = null
        this.selectedStep = null
        this.responseHeight = 0
        this.isShowResponse = false
        this.response = null
        this.isShowRequest = true
        this.actionLogList = []
        this.dialogVisible = false
        done()
      },
      open({ apiDetail } = {}) {
        this.apiDetail = apiDetail
        this.handleMock()
        this.dialogVisible = true
      },
      handleMock() {
        this.loading = true
        this.$http({
          url: customApi.apiDesignMockRunApi,
          data: {
            sessionId: window[CUSTOM_API_SESSION_ID],
            api: this.apiDetail,
          },
        })
          .then((res) => {
            this.response = res
            window[CUSTOM_API_RESPONSE_MAP][this.apiDetail.id] = this.response
            this.handleGetLog()
            this.handleUpdateOutputMap()
          })
          .catch(() => {
            this.dialogVisible = false
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleExport() {},
      handleGetLog() {
        this.$http({
          url: customApi.apiDesignGetRunApiDetail,
          data: {
            sessionId: window[CUSTOM_API_SESSION_ID],
          },
        })
          .then((res) => {
            this.logData = res.data
            this.logData.stepDetails = this.logData.stepDetails.map((step) => ({
              ...step,
              isShow: false,
            }))
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleUpdateOutputMap() {
        this.$http({
          url: customApi.apiDesignGetAllStepOutput,
          data: {
            sessionId: window[CUSTOM_API_SESSION_ID],
          },
        })
          .then((res) => {
            Object.keys(res.data).forEach(code => {
              window[CUSTOM_API_RESPONSE_MAP][`${code}_source`] = res.data[code].output
              window[CUSTOM_API_RESPONSE_MAP][`${code}`] = res.data[code].mappedOutput
            })
          })
      },
      updateHeight() {
        const dialogElement = this.$refs.dialog.$el.firstChild
        this.responseHeight = dialogElement?.getBoundingClientRect().height - 40
      },
      handleChangeStepShow(step) {
        const isShow = step.isShow
        this.logData.stepDetails.forEach((item) => (item.isShow = false))
        if (!step.actionResults) {
          this.loading = true
          this.$http({
            url: customApi.apiDesignGetRunStepDetail,
            data: {
              sessionId: window[CUSTOM_API_SESSION_ID],
              stepCode: step.stepInfo.code,
            },
          })
            .then((res) => {
              this.$set(step, 'actionResults', res.data.actionResults)
              this.$set(step, 'hasMapped', res.data.hasMapped)
              this.$set(step, 'output', res.data.output)
              this.$set(step, 'mappedOutput', res.data.mappedOutput)
              this.$set(step, 'stepOutput', res.data.stepOutput)
              this.selectedAction = step.actionResults[0]
              this.selectedStep = step
              step.isShow = true
              this.handleGetActionListLog()
              this.$nextTick(() => {
                this.updateHeight()
              })
            })
            .finally(() => {
              this.loading = false
            })
        } else {
          step.isShow = !isShow
          this.selectedAction = step.actionResults[0]
          this.selectedStep = step
          this.$nextTick(() => {
            this.updateHeight()
          })
        }
      },
      handleGetActionListLog() {
        this.loading = true
        this.$http({
          url: customApi.apiDesignGetListRunActionLog,
          data: {
            sessionId: window[CUSTOM_API_SESSION_ID],
            actionCode: this.selectedAction.actionInfo.code,
          },
        })
          .then((res) => {
            this.actionLogList = res.data.map(item => ({showDetail: item.indexOf('[JSON_FORMAT') !== 0, detail: item}))
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleToggleShowResponse() {
        if (this.isShowResponse) {
          this.isShowResponse = false
          return
        }
        this.updateHeight()
        this.$nextTick(() => {
          this.isShowResponse = true
        })
      },
      changeAction(action) {
        this.selectedAction = action
        this.handleGetActionListLog()
      }
    },
  }
</script>

