<template>
  <div class="api-config" v-if="apiRequest" v-loading="loading">
    <n-form
      class="data-select-form"
      ref="apiRequest"
      label-position="top"
      :model="apiRequest"
      :rules="rules"
      @submit.native.prevent
      size="mini"
    >
      <el-button-group class="api-button-group" style="margin-left: 10px;">
        <!-- <el-button
          class="api-button"
          :type="apiRequest.type === 4 ? 'primary' : ''"
          @click="changeType(4)"
          >网关API
        </el-button> -->
        <!-- <el-button
          class="api-button"
          :type="apiRequest.type === 6 ? 'primary' : ''"
          @click="changeType(6)"
          >外部API
        </el-button> -->
      </el-button-group>
      <div class="form-item-wrapper">
        <div class="url-title">
          URL
          <el-tooltip popper-class="tooltip-question" placement="top">
            <div slot="content">1.动态路径参数需要在url路径前加冒号表示，动态参数在params中配置，如/user/:id/:name<br />2.测试阶段url的参数由所填的测试值决定<br />3.运行阶段url的参数由所填写的参数值决定</div>
            <i class="el-icon-question"></i>
          </el-tooltip>
        </div>
        <el-select
          size="mini"
          class="method-select"
          v-model="apiRequest.method"
        >
          <el-option
            v-for="m in methods"
            :key="m"
            :label="m"
            :value="m"
          ></el-option>
        </el-select>
        <n-form-item class="no-border" prop="url">
          <div class="inline-form-item" style="padding: 0">
            <template v-if='[6].includes(apiRequest.type)'>
              <el-checkbox v-model="apiRequest.useDynamicOrigin" @change="handleUseDynamicOriginChange" style="margin: 0 10px;">动态协议域名端口</el-checkbox>
              <el-tooltip popper-class="tooltip-question" placement="top">
                <div slot="content">启用动态协议域名端口，则允许选取一个宏变量作为协议域名端口，系统会自动过滤掉填写的协议域名端口</div>
                <i style="margin-right: 5px;" class="el-icon-question"></i>
              </el-tooltip>
              <template v-if="apiRequest.useDynamicOrigin">
                <el-tooltip
                  v-if="apiRequest.dynamicOrigin !== undefined"
                  effect="dark"
                  :content="formatTip(apiRequest.dynamicOrigin)"
                  placement="top"
                >
                  <el-tag
                    class="value-tag"
                    closable
                    disable-transitions
                    @click="handleTagClick(apiRequest, 'dynamicOrigin')"
                    @close="apiRequest.dynamicOrigin = undefined"
                    :type='getTagType(apiRequest.dynamicOrigin)'
                  >
                    {{ formatTag(getElementName(apiRequest.dynamicOrigin), 12) }}
                  </el-tag>
                </el-tooltip>
                <div v-else class="value-input-content">
                  <el-button
                      @click="
                        handleShowMacroVariableSelect(apiRequest, 'dynamicOrigin')
                      "
                      >选择协议域名端口
                    </el-button>
                </div>
                <el-input v-model="url" placeholder="请输入path"></el-input>
              </template>
            </template>
            <el-input v-if="![6].includes(apiRequest.type) || ([6].includes(apiRequest.type) && !apiRequest.useDynamicOrigin)" v-model="url" :disabled='[4, 5].includes(apiRequest.type)' placeholder="请输入URL"></el-input>
            <el-button
              v-if="apiRequest.type === 4"
              type="primary"
              @click="handleShowGatewayDataSelect"
              >选择</el-button
            >
            <el-button type="primary" @click="handleMock">测试 </el-button>
          </div>
        </n-form-item>
      </div>
      <div class="sql-desc" v-if="apiRequest.type === 4 && apiRequest.desc">
        <div class="desc-item">描述：{{ apiRequest.desc }}</div>
      </div>
      <n-form-item label="">
        <div class="params-config">
          <el-button
            class="source-edit-button"
            type="primary"
            @click="
              showInputDialog(
                'object',
                paramsSource,
                activeParamsName,
                'paramsSource'
              )
            "
            >转换器</el-button
          >
          <el-button-group class="api-button-group">
            <el-button
              class="api-button"
              :type="activeParamsName === 'body' ? 'primary' : ''"
              @click="activeParamsName = 'body'"
              >Body（{{ bodyLength }}）
            </el-button>
            <el-button
              class="api-button"
              :type="activeParamsName === 'params' ? 'primary' : ''"
              @click="activeParamsName = 'params'"
              >Params（{{ paramsLength }}）
            </el-button>
            <el-button
              class="api-button"
              :type="activeParamsName === 'headers' ? 'primary' : ''"
              @click="activeParamsName = 'headers'"
              >Headers（{{ headersLength }}）
            </el-button>
          </el-button-group>
          <n-table
            :data="currentParamsData"
            border
            style="width: 100%"
            header-cell-class-name="header-cell"
          >
            <n-table-column prop="key" label="参数名">
              <template slot-scope="scope">
                <el-select
                  style="width: 100%;"
                  v-if="activeParamsName === 'headers'"
                  v-model="scope.row.key"
                  filterable
                  allow-create
                  default-first-option
                >
                  <el-option
                    v-for="option in headersOptions"
                    :key="option"
                    :value="option"
                    :label="option"
                  ></el-option>
                </el-select>
                <el-input
                  v-else
                  v-model="scope.row.key"
                  :disabled="paramsInputDisabled(scope.row.key)"
                ></el-input>
              </template>
            </n-table-column>
            <n-table-column prop="value" label="参数值">
              <template slot-scope="scope">
                <div class="value-input">
                  <el-tooltip
                    v-if="scope.row.value !== undefined"
                    effect="dark"
                    :content="formatTip(scope.row.value)"
                    placement="top"
                  >
                    <el-tag
                      class="value-tag"
                      closable
                      disable-transitions
                      @click="handleTagClick(scope.row, 'value')"
                      @close="scope.row.value = undefined"
                      :type="getTagType(scope.row.value)"
                    >
                      {{ formatTag(getElementName(scope.row.value), 12) }}
                    </el-tag>
                  </el-tooltip>
                  <div v-else class="value-input-content">
                    <el-button-group v-if="activeParamsName !== 'headers'" style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                      <el-button
                        @click="showInputDialog('text', scope.row, 'value')"
                        >文本
                      </el-button>
                      <el-button
                        @click="showInputDialog('number', scope.row, 'value')"
                        :disabled="
                          ['params', 'headers'].includes(activeParamsName)
                        "
                        >数值
                      </el-button>
                      <el-button
                        @click="showInputDialog('object', scope.row, 'value')"
                        :disabled="
                          ['params', 'headers'].includes(activeParamsName)
                        "
                        >对象
                      </el-button>
                      <el-button
                        @click="handleShowVariableSelect(scope.row, 'value')"
                        >参数
                      </el-button>
                    </el-button-group>
                    <div class="headers-input" v-else>
                      <el-select
                        v-if="scope.row.key === 'Content-Type'"
                        v-model="scope.row.value"
                        filterable
                        allow-create
                        default-first-option
                      >
                        <el-option
                          v-for="option in contentTypeOptions"
                          :key="option"
                          :value="option"
                          :label="option"
                        ></el-option>
                      </el-select>
                      <el-input
                        v-else
                        :disabled="!scope.row.key"
                        v-model="scope.row.value"
                        clearable
                      ></el-input>
                      <el-button
                        @click="handleShowVariableSelect(scope.row, 'value')"
                        >参数
                      </el-button>
                    </div>
                    <MoreTypeDropdown @change="(v) => scope.row.value = v" />
                  </div>
                </div>
              </template>
            </n-table-column>
            <n-table-column prop="mockValue" label="测试值">
              <template slot-scope="scope">
                <div class="value-input">
                  <el-tooltip
                    v-if="
                      scope.row.mockValue !== undefined &&
                        !['params', 'headers'].includes(activeParamsName)
                    "
                    effect="dark"
                    :content="formatTip(scope.row.mockValue)"
                    placement="top"
                  >
                    <el-tag
                      class="value-tag"
                      closable
                      disable-transitions
                      @click="handleTagClick(scope.row, 'mockValue')"
                      @close="scope.row.mockValue = undefined"
                    >
                      {{ formatTag(scope.row.mockValue) }}
                    </el-tag>
                  </el-tooltip>
                  <div v-else class="value-input-content">
                    <el-button-group
                      style="flex: 1;display: flex; align-items:center; margin-right: 4px;"
                      v-if="!['params', 'headers'].includes(activeParamsName)"
                    >
                      <el-button
                        @click="showInputDialog('text', scope.row, 'mockValue')"
                        >文本
                      </el-button>
                      <el-button
                        @click="
                          showInputDialog('number', scope.row, 'mockValue')
                        "
                        >数值
                      </el-button>
                      <el-button
                        @click="
                          showInputDialog('object', scope.row, 'mockValue')
                        "
                        >对象
                      </el-button>
                    </el-button-group>
                    <template v-else>
                      <el-select
                        v-if="
                          scope.row.key === 'Content-Type' &&
                            activeParamsName === 'headers'
                        "
                        v-model="scope.row.mockValue"
                        filterable
                        allow-create
                        default-first-option
                      >
                        <el-option
                          v-for="option in contentTypeOptions"
                          :key="option"
                          :value="option"
                          :label="option"
                        ></el-option>
                      </el-select>
                      <el-input
                        v-else
                        :disabled="!scope.row.key"
                        v-model="scope.row.mockValue"
                        clearable
                      ></el-input>
                    </template>
                    <MoreTypeDropdown @change="(v) => scope.row.mockValue = v" />
                  </div>
                </div>
              </template>
            </n-table-column>
            <n-table-column width="180" label="操作">
              <template slot-scope="scope">
                <div class="btns">
                  <div class="add-btn-primary" @click="handleAddParams">
                    +
                  </div>
                  <div
                    v-if="!paramsInputDisabled(scope.row.key)"
                    class="add-btn-primary"
                    @click="handleDeleteParams(scope.$index)"
                  >
                    -
                  </div>
                </div>
              </template>
            </n-table-column>
          </n-table>
        </div>
      </n-form-item>
    </n-form>
    <CustomApiVariableSelect
      ref="variableSelect"
      @confirm="handleVariableConfirm"
    ></CustomApiVariableSelect>
    <MacroVariableSelect
      ref="macroVariableSelect"
      @confirm="handleVariableConfirm"
    ></MacroVariableSelect>
    <gateway-data-select
      ref="gatewayDataSelect"
      @confirm="handleGatewayDataConfirm"
    ></gateway-data-select>
    <custom-input-dialog @confirm="handleUpdateValue" ref="inputDialog"></custom-input-dialog>
  </div>
</template>

<script>
  import GatewayDataSelect from '@/components/GatewayDataSelect'
  import customApi from '@/api/customApi'
  import { CUSTOM_API_SESSION_ID } from '@/config';
  import _ from 'lodash'
  import mixins from './mixins'
  import { getValidators, required, validURL } from '@/utils/validateUtils'
  import { parse } from 'path-to-regexp'
  import URI from 'urijs'
  import { isValidateParams } from '@/utils';
  import { CUSTOM_API_MACRO_VARIABLES } from '@/config';
  export default {
    name: 'ApiAction',
    components: {
      GatewayDataSelect,
    },
    mixins: [mixins],
    props: {
      actionData: {
        type: Object,
      },
      stepData: {
        type: Object,
      },
    },
    data() {
      return {
        loading: false,
        methods: ['POST', 'GET', 'PUT', 'DELETE', 'PATCH'],
        activeParamsName: 'body',
        rules: {
          url: getValidators([
            {
              message: '请输入',
              include: required,
              trigger: 'change',
            }
          ]),
        },
        contentTypeOptions: [
          'application/json',
          'application/x-www-form-urlencoded',
          'application/octet-stream',
          'multipart/form-data',
        ],
        headersOptions: ['snc-token', 'Content-Type', 'Authorization'],
        apiRequest: null
      }
    },
    computed: {
      url: {
        get() {
          return this.apiRequest.url
        },
        set(value) {
          const preNoQueryValue = this.apiRequest.url.split('?')[0]
          const noQueryValue = value.split('?')[0]
          const preUri = new URI(preNoQueryValue)
          const uri = new URI(noQueryValue)
          const prePathString = preUri.pathname()
          const pathString = uri.pathname()
          let dynamicPathArray = []
          let preDynamicPathArray = []
          try {
            preDynamicPathArray = parse(prePathString).filter(item => _.isObject(item)).map(item => item.name)
            dynamicPathArray = parse(pathString).filter(item => _.isObject(item)).map(item => item.name)
          } catch (e) {
            this.apiRequest.url = noQueryValue
            if (this.apiRequest.useDynamicOrigin) {
              this.actionData.config.http.urlBuildConfig.path = pathString
            }
            return
          }
          const dynamicPathParams = dynamicPathArray.map(key => {
            const current = this.apiRequest.params.find((d) => d.key === key)
            return {
              key,
              value: current?.value,
              mockValue: current?.mockValue
            }
          })
          const params = this.apiRequest.params.filter(item => ![...preDynamicPathArray, ...dynamicPathArray].includes(item.key))
          this.apiRequest.params = dynamicPathParams.length ? _.uniqBy([...dynamicPathParams, ...params], 'key') : params.length ? params : [{key: '', value: undefined, mockValue: undefined}]
          this.apiRequest.url = noQueryValue
        },
      },
      currentParamsData() {
        return this.apiRequest[this.activeParamsName]
      },
      paramsSource: {
        get() {
          return {
            body: Object.fromEntries(
              this.apiRequest.body.map((item) => [[item.key], item.value])
            ),
            params: Object.fromEntries(
              this.apiRequest.params.map((item) => [[item.key], item.value])
            ),
            headers: Object.fromEntries(
              this.apiRequest.headers.map((item) => [[item.key], item.value])
            ),
          }
        },
        set(n) {
          const codeList = this.customApiProvide.apiDetail.steps.map(item => item.code)
          this.apiRequest[this.activeParamsName] = Object.entries(n).map(
            ([key, value]) => ({
              key,
              value,
              mockValue: isValidateParams(value, codeList) ? undefined : JSON.parse(JSON.stringify(value === undefined ? null : value, function(k, v) { return isValidateParams(v, codeList) ? null : v })),
            })
          )
        },
      },
      bodyLength() {
        return this.apiRequest.body?.filter((d) => d.key).length || 0
      },
      paramsLength() {
        return this.apiRequest.params?.filter((d) => d.key).length || 0
      },
      headersLength() {
        return this.apiRequest.headers?.filter((d) => d.key).length || 0
      },
    },
    watch: {
      actionData: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.apiRequest = _.cloneDeep(n.config.http)
        },
        deep: true,
        immediate: true
      }
    },
    methods: {
      handleAddParams() {
        this.currentParamsData.push({
          key: '',
          value: undefined,
          mockValue: undefined,
        })
      },
      handleDeleteParams(index) {
        const paramsData = this.currentParamsData
        if (paramsData.length === 1) {
          paramsData[0].key = ''
          paramsData[0].value = undefined
          paramsData[0].mockValue = undefined
          return
        }
        paramsData.splice(index, 1)
      },
      handleShowGatewayDataSelect() {
        this.$refs.gatewayDataSelect.open()
      },
      handleGatewayDataConfirm({ apiRequest, response, apiInfo }) {
        if (!this.bodyLength && !this.paramsLength && !this.headersLength) {
          this.apiRequest = _.cloneDeep({
            url: apiRequest.url,
            method: apiRequest.method,
            type: apiRequest.type,
            body: apiRequest.bodyData.map((item) => ({
              key: item.key,
              value: item.value,
              mockValue: item.test,
            })),
            params: apiRequest.paramsData.map((item) => ({
              key: item.key,
              value: item.value,
              mockValue: item.test,
            })),
            headers: apiRequest.headersData.map((item) => ({
              key: item.key,
              value: item.value,
              mockValue: item.test,
            })),
            desc: apiInfo.description,
          })
          return
        }
        this.$confirm('是否覆盖已配置的请求参数？').then(() => {
          this.apiRequest = _.cloneDeep({
            url: apiRequest.url,
            method: apiRequest.method,
            type: apiRequest.type,
            body: apiRequest.bodyData.map((item) => ({
              key: item.key,
              value: item.value,
              mockValue: item.test,
            })),
            params: apiRequest.paramsData.map((item) => ({
              key: item.key,
              value: item.value,
              mockValue: item.test,
            })),
            headers: apiRequest.headersData.map((item) => ({
              key: item.key,
              value: item.value,
              mockValue: item.test,
            })),
            desc: apiInfo.description,
          })
        }).catch(() => {
          this.apiRequest.url = apiRequest.url
          this.apiRequest.desc = apiInfo.description
        })
      },
      changeType(type) {
        if (this.apiRequest.type === type) {
          return
        }
        this.apiRequest.url = ''
        this.apiRequest.type = type
        this.apiRequest.desc = ''
        this.$nextTick(() => {
          this.$refs.apiRequest.clearValidate()
        })
      },
      paramsInputDisabled(key) {
        if (this.activeParamsName !== 'params') {
          return false
        }
        const uri = new URI(`${this.url}`)
        const pathString = uri.pathname()
        let dynamicPathArray = []
        try {
          dynamicPathArray = parse(pathString)
            .filter((item) => _.isObject(item))
            .map((item) => item.name)
        } catch (e) {
          return false
        }
        return dynamicPathArray.includes(key)
      },
      async handleMock() {
        const http = await this.getResult()
        this.$emit('response', { response: null })
        this.loading = true
        this.$http({
          url: customApi.apiDesignMockRunAction,
          data: {
            appId: Number(this.$route.query.id),
            sessionId: window[CUSTOM_API_SESSION_ID],
            stepCode: this.actionData.code,
            action: {
              ...this.actionData,
              config: {
                ...this.actionData.config,
                http,
                db: null
              }
            },
            requestBody: this.customApiProvide.apiDetail.requestBody
          },
        })
          .then((res) => {
            this.$emit('response', { response: res.data })
          })
          .finally(() => {
            this.loading = false
          })
      },
      async getResult() {
        await this.$refs.apiRequest.validate()
        if (this.apiRequest.useDynamicOrigin) {
          if (!this.apiRequest.dynamicOrigin) {
            this.$message.error('请选择动态协议域名端口')
            return Promise.reject('请选择动态协议域名端口')
          }
        }
        const uri = new URI(this.apiRequest.url)
        const pathString = uri.pathname()
        const result = {
          ...this.apiRequest,
          body: this.apiRequest.body.filter(item => item.key),
          headers: this.apiRequest.headers.filter(item => item.key),
          params: this.apiRequest.params.filter(item => item.key),
          urlBuildConfig: this.apiRequest.useDynamicOrigin ? {
            address: this.apiRequest.dynamicOrigin,
            path: pathString
          } : null
        }
        return Promise.resolve(result)
      },
      handleUseDynamicOriginChange(isUse) {
        const uri = new URI(`${this.url}`)
        this.url = isUse ? uri.pathname() || '/' : this.url
        this.apiRequest.dynamicOrigin = this.apiRequest.dynamicOrigin || undefined
      },
      handleShowVariableSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.variableSelect.open({
          fullVariable: row[key],
          stepCode: this.stepProvide?.stepData?.code,
          onlyGlobal: row === this.apiRequest && this.apiRequest.useDynamicOrigin && key === 'dynamicOrigin'
        })
      },
      handleVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (key) {
          this.$set(row, key, selectedVariable)
          if (row === this.apiRequest && this.apiRequest.useDynamicOrigin && key === 'dynamicOrigin') {
            const macroVariableList = window[CUSTOM_API_MACRO_VARIABLES]
            const currentVar = macroVariableList.find(item => `$macroVariables.${item.key}` === selectedVariable)
            const dynamicUrl = currentVar?.defaultValue || ''
            if (dynamicUrl.indexOf('http') !== 0) {
              this.$message.error('动态协议域名端口格式不正确，请确认当前选择的宏变量的默认值为正确的url格式，如https://....')
              this.$set(row, key, undefined)
            }
          }
        }
        cb?.(selectedVariable)
      },
    },
  }
</script>

