<template>
  <div class="json-tree">
    <vxe-table
      class="json-tree-table"
      ref="xTable"
      size="mini"
      resizable
      max-height="500"
      :data="treeNode.children"
    >
      <vxe-column field="key" title="出参名称">
        <template #default="{ row }">
          <el-input
            :disabled="['Array'].includes(treeNode.type)"
            size="mini"
            v-model="row.key"
            placeholder="出参名称"
          ></el-input>
        </template>
      </vxe-column>
      <vxe-column field="map" title="格式化操作">
        <template #default="{ row }">
          <div class="value-input">
            <el-tooltip
              v-if="isShowFormat(row)"
              effect="dark"
              :content="getTips(row)"
              placement="top"
            >
              <el-tag
                class="value-tag"
                closable
                disable-transitions
                @click="handleTagClick(row)"
                @close="handleDeleteFormatter(row)"
                :type="getTagErrorType(row)"
              >
                {{ getTag(row) }}
              </el-tag>
            </el-tooltip>
            <div v-else class="value-input-content">
              <el-button-group>
                <el-button
                  size="mini"
                  @click="handleSelectVar(row)"
                  >选择出参</el-button
                >
                <el-button size="mini" @click="handleShowFormatter(row)"
                  >格式化</el-button
                >
              </el-button-group>
            </div>
          </div>
        </template>
      </vxe-column>
      <vxe-column field="type" title="出参类型">
        <template #default="{ row }">
          <el-select size="mini" v-model="row.type" :disabled="row.mapperConfig">
            <el-option
              v-for="type in types"
              :key="type.value"
              :label="type.label"
              :value="type.value"
            ></el-option>
          </el-select>
        </template>
      </vxe-column>
      <vxe-column title="操作" width="120">
        <template #default="{ rowIndex }">
          <div class="btns">
            <div class="add-btn-primary" @click="handleAddNode">+</div>
            <div class="add-btn-primary" @click="handleDeleteNode(rowIndex)">
              -
            </div>
          </div>
        </template>
      </vxe-column>
    </vxe-table>
    <CustomApiVariableSelect
      ref="variableSelect"
      :showSelf="true"
      @confirm="handleVariableConfirm"
    ></CustomApiVariableSelect>
    <ObjectFormatter
      ref="objectFormatter"
      @confirm="handleFormatConfirm"
    />
    <StringFormatter
      ref="stringFormatter"
      @confirm="handleFormatConfirm"
    />
    <BooleanFormatter
      ref="booleanFormatter"
      @confirm="handleFormatConfirm"
    />
    <NumberFormatter
      ref="numberFormatter"
      @confirm="handleFormatConfirm"
    />
  </div>
</template>

<script>
  import mixins from '@/components/CustomApi/components/mixins'
  import ObjectFormatter from './ObjectFormatter.vue'
  import StringFormatter from './StringFormatter.vue'
  import BooleanFormatter from './BooleanFormatter.vue'
  import NumberFormatter from './NumberFormatter.vue'
  export default {
    components: { ObjectFormatter, StringFormatter, BooleanFormatter, NumberFormatter },
    name: 'JsonTree',
    mixins: [mixins],
    props: {
      treeNode: {
        type: Object,
      },
      stepData: {
        type: Object,
      },
      isFlat: {
        type: Boolean,
        default: false,
      },
    },
    data() {
      return {
        types: [
          {
            label: '文本',
            value: 'String',
          },
          {
            label: '数值',
            value: 'Number',
          },
          {
            label: '布尔',
            value: 'Boolean',
          },
          {
            label: '对象',
            value: 'Object',
          },
          {
            label: '数组',
            value: 'Array',
          },
        ],
      }
    },
    methods: {
      handleAddNode() {
        const newNode = {
          key: '',
          type: 'String',
        }
        this.treeNode.children.push(newNode)
        this.handleUpdateIndex()
      },
      handleDeleteNode(index) {
        if (this.treeNode.children.length === 1) {
          this.treeNode.children[0].key = ''
          this.treeNode.children[0].type = 'String'
          this.treeNode.children[0].mapperConfig = null
          this.treeNode.children[0].reference = null
          this.handleUpdateIndex()
          return
        }
        this.treeNode.children.splice(index, 1)
        this.handleUpdateIndex()
      },
      handleUpdateIndex() {
        if (this.treeNode.type === 'Array') {
          this.treeNode.children.forEach(
            (item, index) => (item.key = `${index}`)
          )
        }
      },
      handleShowFormatter(row) {
        if (!row.key) {
          this.$message.error('请先输入参数名称!')
          return
        }
        switch (row.type) {
          case 'String':
            this.$refs.stringFormatter.open({
              currentNode: row
            })
            break
          case 'Number':
            this.$refs.numberFormatter.open({
              currentNode: row
            })
            break
          case 'Boolean':
            this.$refs.booleanFormatter.open({
              currentNode: row
            })
            break
          case 'Object':
          case 'Array':
            if (this.isFlat) {
              this.$emit('addLevel', row)
            } else {
              this.$refs.objectFormatter.open({
                stepData: this.stepData,
                rootNode: row,
              })
            }
            break
          case 'Null':
          case 'Undefined':
            break
        }
      },
      getTips(row) {
        if (row.mapperConfig) {
          switch (row.mapperConfig.type) {
            case 'OBJECT_MAP':
              return '对象格式化'
            case 'ARRAY_MAP':
              return '数组格式化'
            case 'TEXT_EXTRACT':
              return '文本提取'
            case 'TEXT_REPLACE':
              return '文本替换'
            case 'TEXT_JOIN':
              return '文本合并'
            case 'TEXT_DATE_FORMAT':
              return '日期格式化'
            case 'BOOLEAN_MAP':
              return '布尔值格式化'
            case 'NUMBER_COUNT':
              return '数值统计'
            case 'NUMBER_CALCULATE':
              return '数值计算'
            case 'DECIMAL_POINT_FORMAT':
              return '小数点格式化'
          }
        }
        if (row.reference) {
          return this.formatTip(row.reference.name)
        }
      },
      getTag(row) {
        if (row.mapperConfig) {
          return this.getTips(row)
        }
        if (row.reference) {
          return this.getElementName(row.reference.name, true)
        }
      },
      handleTagClick(row) {
        if (!row.key) {
          this.$message.error('请先输入参数名称!')
          return
        }
        if (row.mapperConfig) {
          this.handleShowFormatter(row)
          return
        }
        if (row.reference) {
          this.handleShowVariableSelect(row.reference, 'name')
        }
      },
      handleDeleteFormatter(row) {
        row.reference = undefined
        row.mapperConfig = undefined
      },
      getTagErrorType(row) {
        if (row.mapperConfig) {
          return ''
        }
        if (row.reference) {
          return this.getTagType(row.reference.name, true)
        }
      },
      handleSelectVar(row) {
        if (!row.key) {
          this.$message.error('请先输入参数名称!')
          return
        }
        if (!row.reference) {
          this.$set(row, 'reference', { name: '', expression: '' })
        }
        this.handleShowVariableSelect(row.reference, 'name')
      },
      isShowFormat(row) {
        if (row.mapperConfig) {
          switch (true) {
            case row.mapperConfig.type === 'OBJECT_MAP' &&
              !row.mapperConfig.objectMap:
            case row.mapperConfig.type === 'ARRAY_MAP' &&
              !row.mapperConfig.arrayMap:
            case row.mapperConfig.type === 'TEXT_EXTRACT' &&
              !row.mapperConfig.textExtract:
            case row.mapperConfig.type === 'TEXT_REPLACE' &&
              !row.mapperConfig.textReplace:
            case row.mapperConfig.type === 'TEXT_JOIN' &&
              !row.mapperConfig.textJoin:
            case row.mapperConfig.type === 'TEXT_DATE_FORMAT' &&
              !row.mapperConfig.textDateFormat:
            case row.mapperConfig.type === 'BOOLEAN_MAP' &&
              !row.mapperConfig.booleanMap:
            case row.mapperConfig.type === 'NUMBER_COUNT' &&
              !row.mapperConfig.numberCount:
            case row.mapperConfig.type === 'NUMBER_CALCULATE' &&
              !row.mapperConfig.numberCalculate:
            case row.mapperConfig.type === 'DECIMAL_POINT_FORMAT' &&
              !row.mapperConfig.decimalPointFormat:
              return false
          }
          return true
        }
        if (row.reference) {
          return row.reference.name
        }
      },
      handleFormatConfirm(node) {
        const index = this.treeNode.children.findIndex(
          (item) => item.key === node.key
        )
        this.$set(this.treeNode.children, index, node)
      },
    },
  }
</script>

<style lang="less" scoped>
  .json-tree::v-deep {
    .json-tree-table {
      height: 500px;

      .btns {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-around;
      }
    }
  }
</style>
