<template>
  <div class="block_content">
    <div
      v-for="(item, index) in value"
      :key="`${item.type}${index}`"
      :class="[
        'block',
        'clearfix',
        { 'hide-block': hideMyBlock[index] === true },
      ]"
    >
      <span class="json-key">
        <i
          class="collapse-down el-icon-arrow-down"
          v-if="['object', 'array'].includes(item.type)"
          @click="closeBlock(index, $event)"
        ></i>
        <el-input
          v-if="typeof item.name === 'string'"
          :title="formatTip(item.name)"
          type="text"
          v-model="item.name"
          class="key-input"
          @blur="keyInputBlur(item, $event)"
        />
      </span>
      <span class="json-val">
        <template v-if="item.type === 'object'">
          <json-view :showVariable="showVariable" :valueLabel="valueLabel" v-model="item.childParams"></json-view>
        </template>

        <template v-if="item.type === 'array'">
          <array-view :showVariable="showVariable" :valueLabel="valueLabel" v-model="item.childParams"></array-view>
        </template>

        <template v-if="!['object', 'array'].includes(item.type)">
          <span class="val">
            <template
              v-if="
                item.type === 'variable' || isValidateVariableParams(item.remark)
              "
            >
              <el-tag
                v-if="item.remark && item.remark !== '$global.__'"
                :title="formatTip(item.remark)"
                class="value-tag"
                closable
                disable-transitions
                @click="handleShowVariableSelect(item, 'remark')"
                @close="item.remark = '$global.__'"
              >
                {{ formatTag(item.remark) }}
              </el-tag>
              <el-button
                v-else
                @click="handleShowVariableSelect(item, 'remark')"
                >选择参数
              </el-button>
              <el-tag
                v-if="item.remark !== '$global.__' && getElementName(item.remark)"
                :title="getElementName(item.remark)"
                class='value-tag'
                style='margin-left: 10px;'
                :type='getTagType(item.remark)'
              >{{formatTag(getElementName(item.remark), 12)}}</el-tag>
            </template>
            <el-input
              :title="formatTip(valueLabel + item.remark)"
              type="text"
              v-model="item.remark"
              class="val-input"
              v-if="item.type === 'string' && !isValidateVariableParams(item.remark)"
            />
            <el-input
              :title="formatTip(valueLabel + item.remark)"
              type="number"
              v-model.number="item.remark"
              class="val-input"
              v-if="item.type === 'number'"
              @input="numberInputChange(item)"
            />
            <el-select
              :title="formatTip(valueLabel + item.remark)"
              name="value"
              v-model="item.remark"
              class="val-input"
              v-if="item.type === 'boolean'"
            >
              <el-option :value="true" label="true"></el-option>
              <el-option :value="false" label="false"></el-option>
            </el-select>
          </span>
        </template>
      </span>

      <div class="tools">
        <i v-if="item.type === 'object'" class="i-type">{{
          '{' + item.childParams.length + '}'
        }}</i>
        <i v-if="item.type === 'array'" class="i-type">{{
          '[' + item.childParams.length + ']'
        }}</i>
        <el-select
          v-model="item.type"
          class="tools-types"
          @change="itemTypeChange(item)"
        >
          <el-option
            v-for="type in formats"
            :value="type.value"
            :key="type.value"
            :label="type.label"
            :disabled="type.value === 'variable' && !showVariable"
          ></el-option>
        </el-select>
        <i v-if='editorProvide.showButtons' title="新增同级节点" class="del-btn" @click="addItem">
          <i class="add-btn-primary">+</i>
        </i>
        <i v-if='editorProvide.showButtons' class="del-btn" @click="delItem(value, item, index)">
          <i class="add-btn-primary">-</i>
        </i>
      </div>
    </div>
    <div v-if='!value.length && editorProvide.showButtons' class="block add-key" @click="addItem">
      <i title="新增子节点" class="add-btn-primary">+</i>
    </div>
  </div>
</template>

<script>
  import mixins from '@/components/CustomApi/components/mixins'

  export default {
    name: 'JsonView',
    mixins: [mixins],
    props: {
      showVariable: {
        type: Boolean,
        default: true
      },
      valueLabel: {
        type: String,
        default: ''
      },
      value: {
        type: [Object, Array],
      },
    },
    inject: ['editorProvide'],
    data() {
      return {
        formats: [
          {
            label: '参数',
            value: 'variable',
          },
          {
            label: '文本',
            value: 'string',
          },
          {
            label: '数值',
            value: 'number',
          },
          {
            label: '布尔',
            value: 'boolean',
          },
          {
            label: '对象',
            value: 'object',
          },
          {
            label: '数组',
            value: 'array',
          },
        ],
        hideMyBlock: {},
      }
    },
    components: {
      'array-view': () => import('./ArrayView.vue'),
    },
    methods: {
      delItem: function(parentDom, item, index) {
        this.value.splice(index, 1)
        if (this.hideMyBlock[index]) this.hideMyBlock[index] = false
        this.$emit('input', this.value)
      },

      closeBlock: function(index, e) {
        this.$set(this.hideMyBlock, index, !this.hideMyBlock[index])
      },

      addItem: function() {
        this.editorProvide.addItem(this)
      },

      newItem: function(obj) {
        let oj = {
          name: obj.key,
          type: obj.type,
        }
        if (obj.type === 'array' || obj.type === 'object') {
          oj.childParams = obj.val
          oj.remark = null
        } else {
          oj.childParams = null
          oj.remark = obj.val
        }

        if (!oj.name) {
          this.$message.error('请输入Key')
        } else {
          this.value.push(oj)
          this.$emit('input', this.value)
        }
      },

      keyInputBlur: function(item, e) {
        if (item.name.length <= 0) {
          this.$message.error('请输入Key')
          item.name = 'null'
          e.target.focus()
        }
      },

      onDragEnd: function() {
        this.$emit('input', this.value)
      },

      itemTypeChange: function(item) {
        if (item.type === 'array' || item.type === 'object') {
          item.childParams = []
          item.remark = null
        }
        if (item.type === 'boolean') {
          item.remark = true
        }
        if (item.type === 'string') {
          item.remark = ''
        }
        if (item.type === 'number') {
          item.remark = 0
        }
        if (item.type === 'variable') {
          item.remark = '$global.__'
        }
      },

      numberInputChange: function(item) {
        if (!item.remark) item.remark = 0
      },
      handleShowVariableSelect(row, key, cb) {
        this.editorProvide.handleShowVariableSelect(row, key, cb)
      },
    },
  }
</script>
<style lang="less" scoped src="./styles/common.less"></style>
