<template>
  <div class="api-item">
    <div class="api-detail" v-loading="loading">
      <div class="title">
        <span class="t">自定义API</span>
        <el-tag :type="statusMap.type">{{ statusMap.title }}</el-tag>
      </div>
      <div class="content">
        <n-tabs class="content-tabs" :stretch="true" type="border-card" tab-position="left" v-model="activeMenu">
          <n-tab-pane label="API定义" name="apiDefine">
            <n-form
              ref="apiDefineForm"
              :model="formData"
              :rules="rules"
              label-position="right"
              label-width="70px"
              size="medium"
              @submit.native.prevent
            >
              <el-row>
                <el-col :span="12">
                  <n-form-item label="API名称" prop="name">
                    <el-input
                      v-model="formData.name"
                      placeholder="请输入"
                    ></el-input>
                  </n-form-item>
                </el-col>
                <el-col :span="12" style="padding: 0 0 0 20px;">
                  <n-form-item label="分组" prop="groupId">
                    <el-select v-model="formData.groupId" style="width: 100%;">
                      <el-option
                        v-for="group in apiGroupList"
                        :key="group.id"
                        :label="group.name"
                        :value="group.id"
                      ></el-option>
                    </el-select>
                  </n-form-item>
                </el-col>
              </el-row>
              <el-row>
                <n-form-item label="API路径" prop="path">
                  <el-col :span="24">
                    <el-input
                      v-model="formData.path"
                      placeholder="请输入API路径"
                      class="url-input"
                    >
                      <div slot="prepend" class="input-group">
                        <el-input class="pre" style="width: 100px; margin-right: 10px;border-right: 1px solid #E4E7ED;" value="POST" disabled></el-input>
                        <el-input class="next" style="width: 100px;" :value="pathPrepend" disabled></el-input>
                      </div>
                      <api-list v-if="formData.status === undefined" :tree="apiGroupList" slot="append" @change="handleApiChange">
                        <el-button>选择</el-button>
                      </api-list>
                    </el-input>
                  </el-col>
                </n-form-item>
              </el-row>
              <el-row>
                <n-form-item label="API描述" prop="description">
                  <el-input
                    v-model="formData.description"
                    type="textarea"
                    :rows="4"
                    placeholder="  请完善自定义API的描述，便于团队后续维护"
                  ></el-input>
                </n-form-item>
              </el-row>
            </n-form>
          </n-tab-pane>
          <n-tab-pane lazy class="params-define" label="入参定义" name="paramsDefine">
            <n-tabs v-model="activeJsonType" type="card">
              <n-tab-pane label="树形结构" name="tree">
                <JsonEditor
                  :showVariable="false"
                  valueLabel="测试值："
                  class="json-editor"
                  v-if="activeJsonType === 'tree'"
                  v-model="formData.requestBody"
                >
                </JsonEditor>
              </n-tab-pane>
              <n-tab-pane name="json">
                <span slot="label">
                  JSON
                  <el-tooltip popper-class="tooltip-question" placement="top">
                    <div slot="content">
JSON文件都是被包裹在一个大括号{}或者方括号[]中，通过key-value的方式来表达数据。JSON的Key必须包裹在一个双引号中，在实践中，编写 JSON 的时候，忘了给 Key 值加双引号或者是把双引号写成单引号是常见错误。

JSON的值只能是以下几种数据格式，其他任何格式都会触发报错，例如 JavaScript 中的 undefined。

1.数字，包含浮点数和整数
2.字符串，需要包裹在双引号中
3.Bool值，true 或者 false
4.数组，需要包裹在方括号中 []
5.对象，需要包裹在大括号中 {}
6.Null

还需要注意的是 JSON 文件中无法使用注释，试图添加注释将会引发报错
                    </div>
                    <i class="el-icon-question"></i>
                  </el-tooltip>
                  </span>
                <json-code
                  v-if="activeJsonType === 'json'"
                  class="json-code-editor"
                  :isObject="true"
                  v-model="formData.requestBody"
                ></json-code>
              </n-tab-pane>
            </n-tabs>
          </n-tab-pane>
        </n-tabs>
      </div>
    </div>
    <div class="api-content" v-loading="loading">
      <div class="title">
        <div class="left">
          <span class="t">业务逻辑定义</span>
          <el-tooltip popper-class="tooltip-question" placement="top">
            <div slot="content">步骤之间为串行关系</div>
            <i class="el-icon-question"></i>
          </el-tooltip>
        </div>
        <div class="right">专业模式
         <el-switch
            v-model="formData.useScript"
            :active-value="true"
            :inactive-value="false"></el-switch>
        </div>
      </div>
      <template v-if="!formData.useScript">
        <template v-if="formData.steps">
          <StepItem
            v-for="(step, index) in formData.steps"
            :key="step.code"
            :stepData="step"
            :order="index + 1"
            @remove="formData.steps.splice(index, 1)"
          />
        </template>
        <div class="api-step-plus" @click="handleAddStep">
          <el-button type="text" icon="el-icon-plus">新增步骤</el-button>
        </div>
      </template>

      <div class="config" v-else>
        <div class="config-title">
          <span class="t">专业模式</span>
          <div>
            <el-tooltip popper-class="tooltip-question" placement="left">
              <div
                slot="content"
                v-html="templateContent"
              ></div>
              <i class="el-icon-question test-btn tooltip"></i>
            </el-tooltip>
            <el-button-group style="margin-left: 20px;">
              <el-button
                size="mini"
                :type="
                  formData.scriptConfig.scriptType === 'GROOVY'
                    ? 'primary'
                    : ''
                "
                @click="changeScriptType('GROOVY')"
                >Groovy</el-button
              >
              <el-button
                size="mini"
                :type="
                  formData.scriptConfig.scriptType === 'JAVASCRIPT'
                    ? 'primary'
                    : ''
                "
                @click="changeScriptType('JAVASCRIPT')"
                >JavaScript</el-button
              >
              <el-button
                size="mini"
                :type="
                  formData.scriptConfig.scriptType === 'PYTHON'
                    ? 'primary'
                    : ''
                "
                @click="changeScriptType('PYTHON')"
                >Python</el-button
              >
            </el-button-group>
          </div>
        </div>

        <div class="code-wrapper">
          <div class="empty-code" v-html="templateText"></div>
          <js-code
            v-if="formData.scriptConfig.scriptType === 'JAVASCRIPT'"
            class="js-code"
            ref="jsCode"
            @focus-change="focusChange"
            :params-data="paramsData"
            :resizable="true"
            v-model="scriptCode"
          ></js-code>
          <groovy-code
            v-if="formData.scriptConfig.scriptType === 'GROOVY'"
            class="js-code"
            ref="groovyCode"
            @focus-change="focusChange"
            :params-data="paramsData"
            :resizable="true"
            v-model="scriptCode"
          ></groovy-code>
          <python-code
            v-if="formData.scriptConfig.scriptType === 'PYTHON'"
            class="js-code"
            ref="pythonCode"
            @focus-change="focusChange"
            :params-data="paramsData"
            :resizable="true"
            v-model="scriptCode"
          ></python-code>
        </div>

        <div class="params-config">
          <vxe-table
            class="x-table"
            ref="xTable"
            size="mini"
            :data="innerParams"
            border
            header-cell-class-name="header-cell"
          >
            <vxe-table-column field="name" title="参数名词" width="140"></vxe-table-column>
            <vxe-table-column field="key" title="参数编码">
              <template #default="{ row }">
                <el-tag>{{row.key}}</el-tag>
              </template>
            </vxe-table-column>
            <vxe-table-column field="desc" type="html" title="参数描述"></vxe-table-column>
          </vxe-table>
        </div>
      </div>
    </div>
    <div class="api-btn-group" v-loading="loading">
      <el-button type="primary" v-if="this.formData.status !== undefined" @click="handleMock">API测试</el-button>
      <el-button type="danger" v-if="[0, 3].includes(this.formData.status)" @click="handleRemove">删除</el-button>
      <el-button type="primary" v-if="this.formData.status !== 2" @click="handleSave">保存</el-button>
      <el-button type="primary" v-if="this.formData.status !== 2" @click="handlePublish">发布</el-button>
      <el-button type="primary" v-if="this.formData.status === 2" @click="handleUnpublish">下线</el-button>
    </div>
    <Publish ref="publish" :apiGroupList="apiGroupList" @success="$emit('refresh')" />
    <ApiMock ref="apiMock" />
    <ApiScriptMock ref="apiScriptMock" />
  </div>
</template>

<script>
  import _ from 'lodash'
  import JsonCode from '@/components/JsonCode'
  import JsonEditor from './JsonTreeEditor/JsonEditor'
  import StepItem from './StepItem.vue'
  import ApiList from './ApiList.vue'
  import Publish from './Publish.vue';
  import ApiMock from './ApiMock.vue';
  import ApiScriptMock from './ApiScriptMock.vue';
  import JsCode from '@/components/JsCode'
  import GroovyCode from '@/components/GroovyCode'
  import PythonCode from '@/components/PythonCode'
  import { getValidators, required } from '@/utils/validateUtils'
  import customApi from '@/api/customApi'
  import { getRandomHexadecimal } from '@/utils/tree-utils'
  import { CUSTOM_API_RESPONSE_MAP } from '@/config'
  import { decryptByDES, encryptByDES } from '@/utils/encrypt'

  const templateText1 = `支持JS原生语法，使用“参数名”进行取值, 使用return返回结果，示例JS：<br>
var age = in_param.age; <br>
return age + 1;`

  const templateText2 = `支持Groovy语法，使用“参数名”进行取值，示例Groovy：<br>
def age = in_param.age; <br>
return age + 1;`

  const templateText3 = `支持Python语法，使用“参数名”进行取值，示例Python：<br>
age = in_param['age'] <br>
out_param= age + 1`

const innerParams1 = [
  {
    key: 'in_param',
    name: '入参定义',
    desc: 'API的入参定义，对象格式',
  },
]
const innerParams2 = [
  {
    key: 'in_param',
    name: '入参定义',
    desc: 'API的入参定义，对象格式',
  },
  {
    key: 'out_param',
    name: '返回值定义',
    desc: '程序会返回代码中同名的变量',
  },
]

  export default {
    name: 'ApiItem',
    components: {
      JsonCode,
      JsonEditor,
      StepItem,
      ApiList,
      Publish,
      ApiMock,
      JsCode,
      GroovyCode,
      PythonCode,
      ApiScriptMock,
    },
    props: {
      apiData: {
        type: Object,
      },
      apiGroupList: {
        type: Array,
        default: () => [],
      },
    },
    provide() {
      return {
        customApiProvide: {
          apiDetail: this.formData,
        }
      }
    },
    data() {
      return {
        loading: false,
        activeMenu: 'apiDefine',
        activeJsonType: 'tree',
        formData: {
          appId: +this.$route.query.id,
          description: '',
          groupId: undefined,
          name: '',
          path: '',
          requestBody: {},
          status: undefined,
          steps: [],
          useScript: false,
          scriptConfig: {
            scriptType: 'GROOVY',
            script: ''
          }
        },
        rules: {
          name: getValidators([
            {
              message: '请输入API名称',
              include: required,
              trigger: 'change',
              required: true,
            },
          ]),
          groupId: getValidators([
            {
              message: '请选择分组',
              include: required,
              trigger: 'change',
              required: true,
            },
          ]),
          path: getValidators([
            {
              message: '请输入路径',
              include: required,
              trigger: 'change',
              required: true,
            },
          ]),
        },
        pathPrepend: `/app${this.$route.query.id}/`,
        editorFocus: false,
      }
    },
    computed: {
      statusMap() {
        switch (this.formData.status) {
          case 0: {
            return {
              type: '',
              title: '未发布'
            }
          }
          case 1: {
            return {
              type: 'warning',
              title: '发布中'
            }
          }
          case 2: {
            return {
              type: 'success',
              title: '已发布'
            }
          }
          case 3: {
            return {
              type: 'danger',
              title: '已下线'
            }
          }
          default: {
            return {
              type: 'info',
              title: '草稿'
            }
          }
        }
      },
      templateText() {
        if (
          this.formData?.scriptConfig?.script ||
          this.editorFocus ||
          this.isShowLog
        ) {
          return ''
        }
        return this.templateContent
      },
      templateContent() {
        switch (this.formData?.scriptConfig?.scriptType) {
          case 'JAVASCRIPT': return templateText1
          case 'GROOVY': return templateText2
          case 'PYTHON': return templateText3
        }
      },
      paramsData() {
        const params1 = [
          {
            key: 'in_param',
            value: {},
            test: {},
          },
        ]
        const params2 = [
          {
            key: 'in_param',
            value: {},
            test: {},
          },
          {
            key: 'out_param',
            value: {},
            test: {},
          },
        ]
        switch (this.formData?.scriptConfig?.scriptType) {
          case 'JAVASCRIPT': return params1
          case 'GROOVY': return params1
          case 'PYTHON': return params2
        }
      },
      innerParams() {
        switch (this.formData?.scriptConfig?.scriptType) {
          case 'JAVASCRIPT': return innerParams1
          case 'GROOVY': return innerParams1
          case 'PYTHON': return innerParams2
        }
      },
      scriptCode: {
        get() {
          const code = this.formData?.scriptConfig?.script || ''
          return decryptByDES(code)
        },
        set(n) {
          this.formData.scriptConfig.script = encryptByDES(n)
        }
      }
    },
    watch: {
      apiData: {
        deep: true,
        immediate: true,
        handler(n, o) {
          if (!n.id && !_.isEqual(n, o)) {
            Object.assign(this.formData, n)
            this.formData.requestBody = _.cloneDeep(n.requestBody)
            this.formData.steps = _.cloneDeep(n.steps)
            this.$nextTick(() => {
              this.$refs.apiDefineForm.clearValidate()
            })
          }
          if (n.id) {
            this.getApiDetailById(n.id)
          }
        }
      }
    },
    methods: {
      getApiDetailById(id, isCopy) {
        this.loading = true
        this.$http({
          url: customApi.apiDesignDetail,
          data: {
            id,
          },
        })
          .then((res) => {
            Object.assign(this.formData, res.data)
            if (!this.formData.useScript) {
              this.formData.useScript = false
              this.formData.scriptConfig = {
                scriptType: 'GROOVY',
                script: ''
              }
            }
            if (isCopy) {
              this.formData.status = undefined
              this.formData.id = undefined
            }
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleAddStep() {
        const newStep = {
          actions: [],
          apiId: this.formData.id,
          code: getRandomHexadecimal(6),
          config: {
            ignoreError: false,
            timeout: 5,
          },
          name: '',
        }
        this.formData.steps.push(newStep)
      },
      handleApiChange(selectedList) {
        this.getApiDetailById(selectedList[0].id, true)
      },
      async handleMock() {
        this.activeMenu = 'apiDefine'
        await this.$refs.apiDefineForm.validate()
        if (!this.formData.path) {
          this.$message.error('请输入路径！')
          return
        }
        if (this.formData.requestBody) {
          if (!_.isObject(this.formData.requestBody)) {
            this.$message.error('入参定义必须为对象！')
            return
          }
          if (Array.isArray(this.formData.requestBody)) {
            if (this.formData.requestBody.length) {
              this.$message.error('入参定义必须为对象！')
              return
            } else {
              this.formData.requestBody = {}
            }
          }
        }
        if (this.formData.useScript) {
          if (!this.formData.scriptConfig.script) {
            this.$message.error('代码不能为空！')
            return
          }
          this.$refs.apiScriptMock.open({ apiDetail: this.formData })
          return
        }
        if (!this.formData.steps?.length) {
          this.$message.error('请配置业务逻辑！')
          return
        }
        if (this.formData.steps.some(item => !item.name)) {
          this.$message.error('请输入步骤名称！')
          return
        }
        this.$refs.apiMock.open({ apiDetail: this.formData })
      },
      async handleRemove() {
        await this.$confirm('删除后不可恢复', '确认删除API吗？', { type: 'error' })
        this.loading = true
        this.$http({
          url: customApi.apiDesignRemove,
          data: {
            id: this.formData.id,
          },
        })
          .then((res) => {
            this.$emit('refresh')
            this.$message.success('删除成功！')
          })
          .finally(() => {
            this.loading = false
          })
      },
      async handleSave() {
        this.activeMenu = 'apiDefine'
        await this.$refs.apiDefineForm.validate()
        if (!this.formData.path) {
          this.$message.error('请输入路径！')
          return
        }
        if (this.formData.requestBody) {
          if (!_.isObject(this.formData.requestBody)) {
            this.$message.error('入参定义必须为对象！')
            return
          }
          if (Array.isArray(this.formData.requestBody)) {
            if (this.formData.requestBody.length) {
              this.$message.error('入参定义必须为对象！')
              return
            } else {
              this.formData.requestBody = {}
            }
          }
        }
        if (this.formData.useScript) {
          if (!this.formData.scriptConfig.script) {
            this.$message.error('代码不能为空！')
            return
          }
        } else {
          if (!this.formData.steps?.length) {
            this.$message.error('请配置业务逻辑！')
            return
          }
          if (this.formData.steps.some(item => !item.name)) {
            this.$message.error('请输入步骤名称！')
            return
          }
        }
        this.loading = true
        this.$http({
          url: customApi.apiDesignSaveOrUpdate,
          data: JSON.parse(JSON.stringify(this.formData, function(k, v) { return v === undefined ? null : v })),
        })
          .then((res) => {
            if (this.formData.status === undefined) {
              this.formData.status = 0
            }
            this.formData.id = res.data
            this.$message.success('保存成功！')
            this.$emit('update')
          })
          .finally(() => {
            this.loading = false
          })
      },
      handlePublish() {
        const response = window[CUSTOM_API_RESPONSE_MAP][this.formData.id]
        this.$refs.publish.open({ apiData: this.formData, response })
      },
      async handleUnpublish() {
        await this.$confirm('API下线后将无法被调用', '是否下线自定义API', {type: 'warning'})
        this.loading = true
        this.$http({
          url: customApi.apiDesignUnpublish,
          data: {
            id: this.formData.id
          },
        })
          .then((res) => {
            this.formData.status = 3
            this.$emit('update')
            this.$message.success('下线成功！')
          })
          .finally(() => {
            this.loading = false
          })
      },
      changeScriptType(type) {
        if (type === this.formData.scriptConfig.scriptType) {
          return
        }
        this.formData.scriptConfig.scriptType = type
      },
      focusChange(n) {
        this.editorFocus = n
      },
    },
  }
</script>

