<template>
  <div class="sql-config" v-if="sqlRequest" v-loading="loading">
    <n-form
      class="data-select-form"
      ref="sqlRequest"
      label-position="top"
      :model="sqlRequest"
      :rules="rules"
      size="mini"
      @submit.native.prevent
    >
      <n-form-item class="sql-form-item" label="">
        <div class="inline-form-item">
          <div class="form-item-label">数据源</div>
          <div class="sql-data-select-wrapper">
            <div class="left-buttons">
              <el-input
                class="sql-name"
                v-if="sqlRequest.sqlData"
                :value="sqlRequest.sqlData.name"
              ></el-input>
              <el-input class="sql-name" v-else readonly></el-input>
              <el-button type="primary" @click="handleShowSqlDataSelect"
                >选择</el-button
              >
            </div>
            <el-button type="primary" @click="handleMock">测试</el-button>
          </div>
        </div>
        <div class="sql-desc" v-if="sqlRequest.sqlData">
          <div class="desc-item">
            类型：
            <el-tag>{{ sqlRequest.sqlData.type }}</el-tag>
          </div>
          <div class="desc-item">
            数据库：{{ sqlRequest.sqlData.databaseName }}
          </div>
          <div class="desc-item">
            描述：{{ sqlRequest.sqlData.description }}
          </div>
        </div>
      </n-form-item>
      <n-form-item class="sql-form-item" label="SQL" prop="sql">
        <el-button-group class="sql-type-buttons">
          <el-button
            :type="sqlRequest.sqlType === 1 ? 'primary' : ''"
            @click="sqlRequest.sqlType = 1"
            >动态SQL</el-button
          >
          <el-button
            :type="sqlRequest.sqlType === 2 ? 'primary' : ''"
            @click="sqlRequest.sqlType = 2"
            >模板SQL</el-button
          >
        </el-button-group>
        <el-tooltip popper-class="tooltip-question" placement="left">
          <div
            slot="content"
            v-html="sqlRequest.sqlType === 1 ? sqlTemplate1 : sqlTemplate2"
          ></div>
          <i class="el-icon-question sql-type-buttons tooltip"></i>
        </el-tooltip>
        <div class="empty-code" v-html="sqlTemplate"></div>
        <sql-code
          class="sql-code"
          @focus-change="focusChange"
          :dynamic-hints="dynamicHints"
          :resizable="true"
          v-model="sqlRequest.sql"
        ></sql-code>
      </n-form-item>
      <n-form-item label="">
        <div class="params-config">
          <el-button
            class="source-edit-button"
            type="primary"
            @click="
              showInputDialog('object', paramsSource, 'params', 'paramsSource')
            "
            >转换器</el-button
          >
          <n-table :data="params" border style="width: 100%;margin-top: 10px;">
            <n-table-column prop="key" label="参数名">
              <template slot-scope="scope">
                <el-input v-model="scope.row.key"></el-input>
              </template>
            </n-table-column>
            <n-table-column prop="value" label="参数值">
              <template slot-scope="scope">
                <div class="value-input">
                  <el-tooltip
                    v-if="scope.row.value !== undefined"
                    effect="dark"
                    :content="formatTip(scope.row.value)"
                    placement="top"
                  >
                    <el-tag
                      class="value-tag"
                      closable
                      disable-transitions
                      @click="handleTagClick(scope.row, 'value')"
                      @close="scope.row.value = undefined"
                      :type="getTagType(scope.row.value)"
                    >
                      {{ formatTag(getElementName(scope.row.value), 12) }}
                    </el-tag>
                  </el-tooltip>
                  <div v-else class="value-input-content">
                    <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                      <el-button
                        @click="showInputDialog('text', scope.row, 'value')"
                        >文本
                      </el-button>
                      <el-button
                        @click="showInputDialog('number', scope.row, 'value')"
                        >数值
                      </el-button>
                      <el-button
                        @click="handleShowVariableSelect(scope.row, 'value')"
                        >参数
                      </el-button>
                    </el-button-group>
                    <MoreTypeDropdown @change="(v) => scope.row.value = v" />
                  </div>
                </div>
              </template>
            </n-table-column>
            <n-table-column prop="mockValue" label="测试值">
              <template slot-scope="scope">
                <div class="value-input">
                  <el-tooltip
                    v-if="scope.row.mockValue !== undefined"
                    effect="dark"
                    :content="formatTip(scope.row.mockValue)"
                    placement="top"
                  >
                    <el-tag
                      class="value-tag"
                      closable
                      disable-transitions
                      @click="handleTagClick(scope.row, 'mockValue')"
                      @close="scope.row.mockValue = undefined"
                    >
                      {{ formatTag(scope.row.mockValue) }}
                    </el-tag>
                  </el-tooltip>
                  <div v-else class="value-input-content">
                    <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                      <el-button
                        @click="showInputDialog('text', scope.row, 'mockValue')"
                        >文本
                      </el-button>
                      <el-button
                        @click="
                          showInputDialog('number', scope.row, 'mockValue')
                        "
                        >数值
                      </el-button>
                    </el-button-group>
                    <MoreTypeDropdown @change="(v) => scope.row.mockValue = v" />
                  </div>
                </div>
              </template>
            </n-table-column>
            <n-table-column width="180" label="操作">
              <template slot-scope="scope">
                <div class="btns">
                  <div class="add-btn-primary" @click="handleAddParams">+</div>
                  <div
                    class="add-btn-primary"
                    @click="handleDeleteParams(scope.$index)"
                  >
                    -
                  </div>
                </div>
              </template>
            </n-table-column>
          </n-table>
        </div>
      </n-form-item>
    </n-form>
    <CustomApiVariableSelect
      ref="variableSelect"
      @confirm="handleVariableConfirm"
    ></CustomApiVariableSelect>
    <sql-data-select
      ref="sqlDataSelect"
      @confirm="handleSqlDataConfirm"
    ></sql-data-select>
    <custom-input-dialog @confirm="handleUpdateValue" ref="inputDialog"></custom-input-dialog>
  </div>
</template>

<script>
  import SqlDataSelect from '@/components/SqlDataSelect/index'
  import SqlCode from '@/components/SqlCode'
  import { getValidators, required } from '@/utils/validateUtils'
  import mixins from './mixins'
  import customApi from '@/api/customApi'
  import Api from '@/api/application'
  import { CUSTOM_API_SESSION_ID } from '@/config'
  import _ from 'lodash'
  import { decryptByDES, encryptByDES } from '@/utils/encrypt'
import { isValidateParams } from '@/utils'

  const sqlTemplate1 = `使用“#{参数名}”格式进行参数取值（系统自动进行非空判断），示例SQL：<br>
  select id,name,age,sex,create_time from user <br>
  where id=#{id} and name=#{name} and age=#age`
  const sqlTemplate2 = `使用freemarker模板语法进行参数非空判断和取值，示例SQL：<br>
  select id,name,age,sex,create_time from user <br>
  where 1=1 <br>
  <#if id??> and id=\${id}</#if> <br>
  <#if id??> and name='\${name}'</#if> <br>
  <#if id??> and age=\${age}</#if>`
  export default {
    name: 'SqlAction',
    components: {
      SqlCode,
      SqlDataSelect,
    },
    mixins: [mixins],
    props: {
      actionData: {
        type: Object,
      },
      stepData: {
        type: Object,
      },
    },
    data() {
      return {
        sqlTemplate1,
        sqlTemplate2,
        loading: false,
        sqlRequest: null,
        activeCollapse: ['config'],
        rules: {
          sql: getValidators([
            {
              message: '请输入SQL',
              include: required,
              trigger: 'change',
            },
          ]),
        },
        editorFocus: false,
      }
    },
    computed: {
      sqlTemplate() {
        if (this.sqlRequest.sql || this.editorFocus) {
          return ''
        }
        return this.sqlRequest.sqlType === 1 ? sqlTemplate1 : sqlTemplate2
      },
      params() {
        return this.sqlRequest.params
      },
      dynamicHints() {
        return this.params.map((item) => `#{${item.key}}`)
      },
      paramsSource: {
        get() {
          return {
            params: Object.fromEntries(
              this.sqlRequest.params.map((item) => [[item.key], item.value])
            ),
          }
        },
        set(n) {
          const codeList = this.customApiProvide.apiDetail.steps.map(item => item.code)
          this.sqlRequest.params = Object.entries(n).map(([key, value]) => ({
            key,
            value,
            mockValue: isValidateParams(value, codeList) ? undefined : JSON.parse(JSON.stringify(value === undefined ? null : value, function(k, v) { return isValidateParams(v, codeList) ? null : v })),
          }))
        },
      },
    },
    watch: {
      actionData: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.sqlRequest = _.cloneDeep(n.config.db)
          this.sqlRequest.sql = decryptByDES(this.sqlRequest.sql)
          this.handleGetDataSetDetail()
        },
        deep: true,
        immediate: true,
      },
    },
    methods: {
      focusChange(n) {
        this.editorFocus = n
      },
      handleShowVariableSelect(row, key, cb) {
        this.variableRowDetail = { row, key, cb }
        this.$refs.variableSelect.open({
          fullVariable: row[key],
          callbackId: this.callbackId,
        })
      },
      handleVariableConfirm(selectedVariable) {
        const { row, key, cb } = this.variableRowDetail
        if (key) {
          this.$set(row, key, selectedVariable)
        }
        cb?.(selectedVariable)
      },
      handleAddParams() {
        this.sqlRequest.params.push({
          key: '',
          value: undefined,
          mockValue: undefined,
        })
      },
      handleDeleteParams(index) {
        const paramsData = this.sqlRequest.params
        if (paramsData.length === 1) {
          paramsData[0].key = ''
          paramsData[0].value = undefined
          paramsData[0].mockValue = undefined
          return
        }
        paramsData.splice(index, 1)
      },
      handleShowSqlDataSelect() {
        this.$refs.sqlDataSelect.open()
      },
      handleSqlDataConfirm(data) {
        this.$set(this.sqlRequest, 'sqlData', data)
        this.sqlRequest.dataSourceId = data.id
      },
      async handleMock() {
        const db = await this.getResult()
        delete db.sqlData
        this.$emit('response', { response: null })
        this.loading = true
        this.$http({
          url: customApi.apiDesignMockRunAction,
          data: {
            appId: Number(this.$route.query.id),
            sessionId: window[CUSTOM_API_SESSION_ID],
            stepCode: this.actionData.code,
            action: {
              ...this.actionData,
              config: {
                ...this.actionData.config,
                http: null,
                db
              }
            },
            requestBody: this.customApiProvide.apiDetail.requestBody
          },
        })
          .then((res) => {
            this.$emit('response', { response: res.data })
          })
          .finally(() => {
            this.loading = false
          })
      },
      async getResult() {
        await this.$refs.sqlRequest.validate()
        if (!this.sqlRequest.dataSourceId) {
          this.$message.error('请选择数据源')
          return Promise.reject('请选择数据源')
        }
        const result = {
          ...this.sqlRequest,
          params: this.sqlRequest.params.filter((item) => item.key),
          sql: encryptByDES(this.sqlRequest.sql)
        }
        return Promise.resolve(result)
      },
      handleGetDataSetDetail() {
        if (!this.sqlRequest.dataSourceId) {
          return
        }
        this.loading = true
        this.$http({
          url: Api.sqlDataSourceDetailById,
          data: {
            id: this.sqlRequest.dataSourceId
          },
        })
          .then((res) => {
            this.sqlRequest.sqlData = res.data
          })
          .finally(() => {
            this.loading = false
          })
      }
    },
  }
</script>

