import { formatTip, formatTag, isValidateParams } from '@/utils'
import { getPathList } from '@/utils/format'

export default {
  inject: ['customApiProvide', 'stepProvide'],
  data() {
    return {
      variableRowDetail: null,
    }
  },
  methods: {
    formatTip,
    formatTag,
    isValidateVariableParams(value) {
      const codeList = this.customApiProvide.apiDetail.steps.map(item => item.code)
      return isValidateParams(value, codeList)
    },
    handleTagClick(data, key) {
      const value = data[key]
      if (typeof value === 'string') {
        if (!['test', 'mockValue'].includes(key) && this.isValidateVariableParams(value)) {
          if (value.indexOf('$macroVariables') === 0) {
            this.handleShowMacroVariableSelect(data, key)
            return
          }
          this.handleShowVariableSelect(data, key)
          return
        }
        this.showInputDialog('text', data, key)
        return
      }
      if (value && typeof value === 'object') {
        if (!(value instanceof File)) {
          this.showInputDialog('object', data, key)
          return
        }
      }
      if (typeof value === 'number') {
        this.showInputDialog('number', data, key)
        return
      }
    },
    showInputDialog(type, data, key, from) {
      this.$refs.inputDialog.open({ type, data, key, from })
    },
    handleUpdateValue(data, from) {
      if (from) {
        this[from] = data
      }
    },
    getElementName(variable, showSelf) {
      if (typeof variable !== 'string') {
        return formatTip(variable)
      }
      const pathList = getPathList(variable)
      const $key = pathList[0]
      const codeList = this.customApiProvide.apiDetail.steps.map(item => item.code)
      if (this.isValidateVariableParams($key)) {
        if ($key === '$global') {
          return 'API请求出参'
        }
        if ($key === '$macroVariables') {
          return '宏变量'
        }
        const index = codeList.findIndex(item => item === this.stepProvide?.stepData?.code)
        const disableList = showSelf || this.showSelf ? codeList.slice(index + 1) : codeList.slice(index)
        if (disableList.includes($key.slice(1))) {
          return '参数不合法，请重新选择'
        }
        const currentStep = this.customApiProvide.apiDetail.steps.find(item =>`$${item.code}` === $key)
        return currentStep ? currentStep.name || '未命名步骤' : formatTip(variable)
      } else {
        return formatTip(variable)
      }
    },
    getTagType(variable, showSelf) {
      return this.getElementName(variable, showSelf || this.showSelf) === '参数不合法，请重新选择' ? 'danger' : ''
    },
    handleShowVariableSelect(row, key, cb) {
      this.variableRowDetail = { row, key, cb }
      this.$refs.variableSelect.open({
        fullVariable: row[key],
        stepCode: this.stepProvide?.stepData?.code
      })
    },
    handleVariableConfirm(selectedVariable) {
      const { row, key, cb } = this.variableRowDetail
      if (key) {
        this.$set(row, key, selectedVariable)
      }
      cb?.(selectedVariable)
    },
    handleShowMacroVariableSelect(row, key) {
      this.variableRowDetail = { row, key }
      this.$refs.macroVariableSelect.open({
        fullVariable: row[key],
        isManage: false
      })
    },
  },
}
