<template>
<div class='json-editor' v-loading="loading">
  <div class='edit-mode'>
    <el-button type='primary' @click='editorProvide.showButtons = !editorProvide.showButtons'>编辑</el-button>
  </div>
  <json-view
    v-if="!loading"
    :showVariable="showVariable"
    :valueLabel="valueLabel"
    v-model="parsedData">
  </json-view>
  <CustomApiVariableSelect
    ref="variableSelect"
    @confirm="handleVariableConfirm"
  />
  <item-add-form ref="popover" :showVariable="currentContext.showVariable" :needName="currentContext.needName" @confirm="newItem"></item-add-form>
</div>
</template>

<script>
import JsonView from './JsonView.vue';
import { FUNCTION_FLAG } from '@/config'
import mixins from '@/components/CustomApi/components/mixins'
import ItemAddForm from './ItemAddForm.vue'
export default {
  name: 'JsonEditor',
  mixins: [mixins],
  props: {
    value: {
      type: [Object, Array, String],
      required: true
    },
    showVariable: {
      type: Boolean,
      default: true
    },
    valueLabel: {
      type: String,
      default: ''
    },
  },
  data () {
    return {
      parsedData: {},
      editorProvide: {
        showButtons: false,
        handleShowVariableSelect: this.handleShowVariableSelect,
        handleVariableConfirm: this.handleVariableConfirm,
        addItem: this.addItem
      },
      currentContext: {},
      loading: true
    };
  },
  provide() {
    return {
      editorProvide: this.editorProvide
    }
  },
  created () {
    this.lastParsedData = {};
    let value
    try {
      value = typeof this.value === 'object' ? this.value : JSON.parse(this.value || '{}')
    } catch (e) {
      this.$message.error('JSON解析失败')
      console.log(e)
      value = {}
    }
    this.parsedData = this.jsonParse(value);
  },
  mounted() {
    setTimeout(() => {
      this.loading = false
    }, 100)
  },
  watch: {
    value: {
      handler(newValue, oldValue) {
        const value = typeof this.value === 'object' ? this.value : JSON.parse(this.value || '{}')
        this.parsedData = this.jsonParse(value);
      }
    },
    parsedData: {
      handler(newValue, oldValue) {
        if (JSON.stringify(newValue) === JSON.stringify(this.lastParsedData)) {
          return;
        }

        this.lastParsedData = newValue;
        let res
        const json = this.makeJson(this.parsedData)
        if (Object.keys(json).length && Object.keys(json).every(key => /^[0-9]+.?[0-9]*$/.test(key))) {
          res = Object.values(json)
        } else {
          res = json
        }
        this.$emit('input', res);
      },
      deep: true
    }
  },
  components: {
    'item-add-form': ItemAddForm,
    'json-view': JsonView
  },
  methods: {
    jsonParse: function (jsonStr) {
      let parseJson = json => {
        let result = [];
        let keys = Object.keys(json);
        keys.forEach((k, index) => {
          if (k === '' || typeof k === 'undefined') {
            return
          }
          let val = json[k];
          let parsedVal = val;

          if (this.getType(val) === 'object') {
            parsedVal = parseJson(val);

          } else if (this.getType(val) === 'array') {
            // eslint-disable-next-line no-use-before-define
            parsedVal = parseArray(val);
          }

          let opt = {
            name: k,
            type: this.getType(val)
          };

          if (opt.type === 'array' || opt.type === 'object') {
            opt.childParams = parsedVal;
            opt.remark = null;
          } else {
            opt.childParams = null;
            opt.remark = parsedVal;
          }

          result.push(opt);
        });
        return result;
      };

      //
      let parseArray = arrayObj => {
        let result = [];
        for (let i = 0; i < arrayObj.length; ++i) {
          let val = arrayObj[i];
          let parsedVal = val;
          if (this.getType(val) === 'object') {
            parsedVal = parseJson(val);

          } else if (this.getType(val) === 'array') {
            parsedVal = parseArray(val);
          }

          let opt = {
            name: null,
            type: this.getType(val)
          };

          if (opt.type === 'array' || opt.type === 'object') {
            opt.childParams = parsedVal;
            opt.remark = null;
          } else {
            opt.childParams = null;
            opt.remark = parsedVal;
          }

          result.push(opt);
        }
        return result;
      };

      // --
      let parseBody = json => {
        let r = parseJson(json);
        return r;
      };

      return parseBody(jsonStr);
    },

    getType: function (obj) {
      switch (Object.prototype.toString.call(obj)) {
        case '[object Array]':
          return 'array';
        case '[object Object]':
          return 'object';
        case '[object Null]':
        case '[object Function]':
        case '[object Undefined]':
          return 'string'
        case '[object String]':
          if (obj.includes(FUNCTION_FLAG)) {
            return 'variable'
          }
          return this.isValidateVariableParams(obj) ? 'variable' : 'string'
        default:
          return typeof obj;
      }
    },

    makeJson: function (dataArr) {
      let revertWithObj = function(data) {
        let r = {};
        for (let i = 0; i < data.length; ++i) {
          let el = data[i];
          let key, val;
          key = el.name;
          if (el.type === 'array') {
            // eslint-disable-next-line no-use-before-define
            val = revertWithArray(el.childParams);
          } else if (el.type === 'object') {
            val = revertWithObj(el.childParams);
          } else {
            val = el.remark;
          }

          r[key] = val;
        }
        return r;
      };

      let revertWithArray = function(data) {
        let arr = [];
        for (let i = 0; i < data.length; ++i) {
          let el = data[i];
          let r;
          if (el.type === 'array') {
            r = revertWithArray(el.childParams);
          } else if (el.type === 'object') {
            r = revertWithObj(el.childParams);
          } else {
            r = el.remark;
          }

          arr.push(r);
        }
        return arr;
      };

      let revertMain = function(data) {
        let r = revertWithObj(data);
        return r;
      };

      return revertMain(dataArr);
    },
    handleShowVariableSelect(row, key, cb) {
      this.variableRowDetail = { row, key, cb }
      this.$refs.variableSelect.open({ fullVariable: row[key], callbackId: this.callbackId })
    },
    handleVariableConfirm(selectedVariable) {
      const { row, key, cb } = this.variableRowDetail
      if (key) {
        this.$set(row, key, selectedVariable)
      }
      cb?.(selectedVariable)
    },
    addItem: function(context) {
      this.$refs.popover.show()
      this.currentContext = context
    },
    newItem(obj) {
      this.currentContext.newItem(obj)
    }
  }
};
</script>

<style lang="less" scoped src="./styles/common.less"></style>
