<template>
  <div class="source-select">
    <span v-if="showLabel" class="label"><span style="color: red;">*</span>选择参数：</span>
    <el-button
      size="mini"
      type="primary"
      v-if="!source.name"
      @click="handleShowVariableSelect(source, 'name', handleBuildAttr)"
      >{{title}}</el-button
    >
    <el-tooltip v-else effect="dark" :content="source.name" placement="top">
      <el-tag
        class="value-tag"
        closable
        disable-transitions
        @click="handleShowVariableSelect(source, 'name', handleBuildAttr)"
        @close="handleBuildAttr('')"
        :type="getTagType(source.name, true)"
      >
        {{ getElementName(source.name, true) }}
      </el-tag>
    </el-tooltip>
    <CustomApiVariableSelect
      ref="variableSelect"
      :showSelf="true"
      @confirm="handleVariableConfirm"
    ></CustomApiVariableSelect>
  </div>
</template>

<script>
  import mixins from '@/components/CustomApi/components/mixins'
  export default {
    name: 'SourceSelect',
    mixins: [mixins],
    props: {
      source: {
        type: Object,
      },
      showLabel: {
        type: Boolean,
        default: true
      },
      title: {
        type: String,
        default: '选择'
      },
    },
    methods: {
      handleBuildAttr(fullVariable) {
        this.source.name = fullVariable
        this.$emit('change', fullVariable)
      },
    },
  }
</script>

<style lang="less" scoped>
  .source-select::v-deep {
    display: flex;
    align-items: center;
    .label {
      margin-right: 10px;
    }
  }
</style>
