<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    :title="title"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible">
      <n-breadcrumb
        class="tree-breadcrumb"
        separator-class="el-icon-arrow-right"
      >
        <n-breadcrumb-item
          class="tree-breadcrumb-item"
          v-for="(node, index) in nodeList"
          :key="index"
          :class="{ isCurrent: currentNode === node }"
          @click.native="handleChangeLevel(node, index)"
          >{{ node.key }}</n-breadcrumb-item
        >
      </n-breadcrumb>
      <div class="source-select" v-if="currentNode.type === 'Array'">
        选择源数组：
        <el-button
          size="mini"
          type="primary"
          v-if="!currentNode.mapperConfig.arrayMap.source.name"
          @click="
            handleShowVariableSelect(
              currentNode.mapperConfig.arrayMap.source,
              'name',
              handleBuildAttr
            )
          "
          >选择</el-button
        >
        <el-tooltip
          v-else
          effect="dark"
          :content="currentNode.mapperConfig.arrayMap.source.name"
          placement="top"
        >
          <el-tag
            class="value-tag"
            closable
            disable-transitions
            @click="
              handleShowVariableSelect(
                currentNode.mapperConfig.arrayMap.source,
                'name',
                handleBuildAttr
              )
            "
            @close="currentNode.mapperConfig.arrayMap.source.name = ''"
            :type="
              getTagType(currentNode.mapperConfig.arrayMap.source.name, true)
            "
          >
            {{
              getElementName(
                currentNode.mapperConfig.arrayMap.source.name,
                true
              )
            }}
          </el-tag>
        </el-tooltip>
        <el-tooltip
          effect="dark"
          placement="top"
        >
          <div slot="content">说明：选择源数组后默认该数组下的所有元素的数据结构是相同的，且无法新增数组下标；不选择源数组则可自由新增数组下标并定义相应的数据结构</div>
          <i style='margin-left: 5px;' class="el-icon-question"></i>
        </el-tooltip>
      </div>
      <JsonTree
        :treeNode="currentNode"
        :stepData="stepData"
        :isFlat="true"
        @addLevel="handleAddLevel"
      />
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="handleConfirm">确 认</el-button>
    </div>
    <CustomApiVariableSelect
      ref="variableSelect"
      :showSelf="true"
      @confirm="handleVariableConfirm"
    ></CustomApiVariableSelect>
  </el-dialog>
</template>

<script>
  import mixins from '@/components/CustomApi/components/mixins'
  import _ from 'lodash'
  import { getPathList } from '@/utils/format'
  import { CUSTOM_API_RESPONSE_MAP } from '@/config'
  import { getMapType } from '@/utils'
  export default {
    name: 'ObjectFormatter',
    mixins: [mixins],
    data() {
      return {
        dialogVisible: false,
        stepData: null,
        rootNode: null,
        currentNode: null,
        nodeList: [],
      }
    },
    computed: {
      title() {
        return this.currentNode?.type === 'Array' ? '数组格式化' : '对象格式化'
      }
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      handleAddAttr() {
        if (!this.currentNode.children) {
          if (this.currentNode.type === 'Array') {
            this.$set(this.currentNode, 'children', [
              { key: '0', type: 'String' },
            ])
          } else {
            this.$set(this.currentNode, 'children', [
              { key: '', type: 'String' },
            ])
          }
        }
        const configKey =
          this.currentNode.type === 'Object' ? 'objectMap' : 'arrayMap'
        if (!this.currentNode.mapperConfig) {
          this.$set(this.currentNode, 'mapperConfig', {
            type:
              this.currentNode.type === 'Object' ? 'OBJECT_MAP' : 'ARRAY_MAP',
            [configKey]: {
              source: {
                name: '',
              },
            },
          })
        }
      },
      open({ stepData, rootNode }) {
        this.stepData = stepData
        this.rootNode = rootNode
        this.currentNode = _.cloneDeep(rootNode)
        this.handleAddAttr()
        this.nodeList = [this.currentNode]
        this.dialogVisible = true
      },
      handleAddLevel(node) {
        this.currentNode = node
        this.handleAddAttr()
        this.nodeList.push(this.currentNode)
      },
      handleChangeLevel(node, index) {
        this.currentNode = node
        this.nodeList.splice(index + 1, this.nodeList.length - index)
      },
      handleConfirm() {
        const formattNode = _.cloneDeep(this.nodeList[0])
        this.$emit('confirm', formattNode)
        this.dialogVisible = false
      },
      handleBuildAttr(fullVariable) {
        const pathList = getPathList(fullVariable)
        const $key = pathList[0]
        let root
        switch ($key) {
          case '$global':
            root = this.customApiProvide.apiDetail.requestBody
            break
          case `$${this.stepData.code}`:
            root =
              window[CUSTOM_API_RESPONSE_MAP][`${this.stepData.code}_source`]
            break
          default:
            root = window[CUSTOM_API_RESPONSE_MAP][this.stepData.code]
            break
        }
        const currentNode = _.get(root, pathList.slice(1))
        if (!Array.isArray(currentNode)) {
          this.$message.error('数据源必须为数组！')
          this.currentNode.mapperConfig.arrayMap.source.name = ''
          return
        }

        if (_.isObject(currentNode[0])) {
          const children = Object.entries(currentNode[0]).map(
            ([key, value]) => {
              return {
                key,
                type: getMapType(value),
                reference: {
                  name: `${fullVariable}[0].${key}`,
                },
              }
            }
          )
          this.$set(this.currentNode.children, 0, {
            key: '0',
            type: Array.isArray(currentNode[0]) ? 'Array' : 'Object',
            children: children.length
              ? children
              : [{ key: '', type: 'String' }],
          })
        } else {
          this.$set(this.currentNode.children, 0, {
            key: '0',
            type: getMapType(currentNode[0]),
          })
        }
        this.$set(this.currentNode.mapperConfig.arrayMap.source, 'name', fullVariable)
        this.$forceUpdate()
      },
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .dialog-content::v-deep {
    .tree-breadcrumb {
      margin: 0 0 20px 0;
      .tree-breadcrumb-item {
        .el-breadcrumb__inner {
          &:hover {
            cursor: pointer;
            color: @drop-main-color;
          }
        }
        &.isCurrent {
          .el-breadcrumb__inner {
            font-weight: 700;
            color: @drop-main-color;
          }
        }
      }
    }

    .source-select {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
    }
  }
</style>
