<script>
  import customApi from '@/api/customApi'
  import { CUSTOM_API_SESSION_ID, CUSTOM_API_RESPONSE_MAP } from '@/config'
  import { Factory } from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  const VueJsonPretty = new Factory({ virtualLines: 50 })

  export default {
    name: 'StepMock',
    components: {
      VueJsonPretty,
    },
    inject: ['customApiProvide'],
    data() {
      return {
        dialogVisible: false,
        loading: false,
        stepData: {},
        logData: null,
        selectedAction: null,
        responseHeight: 0,
        isShowResponse: false,
        response: null,
        actionLogList: [],
      }
    },
    computed: {
      time() {
        return this.logData
          ? this.logData.executeResult.endTime -
              this.logData.executeResult.beginTime
          : 0
      },
      status() {
        return this.logData?.executeResult?.success ? '执行成功' : '执行失败'
      },
      successCount() {
        return this.logData?.executeResult?.successCount || 0
      },
      failedCount() {
        return this.logData?.executeResult?.failedCount || 0
      },
      inputMsg() {
        const inputMsg = this.selectedAction?.executeResult?.input
        return inputMsg ? JSON.stringify(inputMsg, function(k, v) { return v === undefined ? null : v }, 2) : '无数据'
      },
      outputMsg() {
        const outputMsg = this.selectedAction?.executeResult?.output
        return outputMsg ? JSON.stringify(outputMsg, function(k, v) { return v === undefined ? null : v }, 2) : '无数据'
      },
      errorMsg() {
        const errorMsg = this.selectedAction?.executeResult?.errorReason
        return errorMsg
      },
      sourceMsg() {
        const outputMsg = this.logData?.stepOutput?.output
        return outputMsg ? JSON.stringify(outputMsg, function(k, v) { return v === undefined ? null : v }, 2) : '无数据'
      },
      formatMsg() {
        const outputMsg = this.logData?.stepOutput?.hasMapped
          ? this.logData?.stepOutput?.mappedOutput
          : ''
        return outputMsg ? JSON.stringify(outputMsg, function(k, v) { return v === undefined ? null : v }, 2) : '无数据'
      },
    },
    methods: {
      handleClose(done) {
        this.stepData = {}
        this.logData = null
        this.selectedAction = null
        this.responseHeight = 0
        this.isShowResponse = false
        this.response = null
        this.actionLogList = []
        this.dialogVisible = false
        done()
      },
      open({ stepData } = {}) {
        this.stepData = stepData
        this.handleMock()
        this.dialogVisible = true
      },
      handleMock() {
        this.loading = true
        this.$http({
          url: customApi.apiDesignMockRunStep,
          data: {
            appId: Number(this.$route.query.id),
            sessionId: window[CUSTOM_API_SESSION_ID],
            requestBody: this.customApiProvide.apiDetail.requestBody,
            step: this.stepData,
          },
        })
          .then((res) => {
            window[CUSTOM_API_RESPONSE_MAP][this.stepData.code] = res.data.mappedOutput
            window[CUSTOM_API_RESPONSE_MAP][`${this.stepData.code}_source`] = res.data.output
            this.response = res.data.hasMapped ? res.data.mappedOutput : res.data.output
          })
          .finally(() => {
            this.handleGetLog()
          })
      },
      handleExport() {},
      handleGetLog() {
        this.$http({
          url: customApi.apiDesignGetRunStepDetail,
          data: {
            sessionId: window[CUSTOM_API_SESSION_ID],
            stepCode: this.stepData.code,
          },
        })
          .then((res) => {
            this.logData = res.data
            this.selectedAction = this.logData.actionResults[0]
            this.handleGetActionListLog()
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleGetActionListLog() {
        this.loading = true
        this.$http({
          url: customApi.apiDesignGetListRunActionLog,
          data: {
            sessionId: window[CUSTOM_API_SESSION_ID],
            actionCode: this.selectedAction.actionInfo.code,
          },
        })
          .then((res) => {
            this.actionLogList = res.data.map(item => ({showDetail: item.indexOf('[JSON_FORMAT') !== 0, detail: item}))
          })
          .finally(() => {
            this.loading = false
          })
      },
      updateHeight() {
        const dialogElement = this.$refs.dialog.$el.firstChild
        this.responseHeight = dialogElement?.getBoundingClientRect().height
      },
      handleChangeAction(action) {
        this.selectedAction = action
        this.handleGetActionListLog()
        this.$nextTick(() => {
          this.updateHeight()
        })
      },
      handleToggleShowResponse() {
        if (this.isShowResponse) {
          this.isShowResponse = false
          return
        }
        this.updateHeight()
        this.$nextTick(() => {
          this.isShowResponse = true
        })
      }
    },
  }
</script>

