<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="宏变量管理"
    :visible.sync="dialogVisible"
    width="1000px"
    top="5vh"
    :before-close="handleClose"
  >
    <div class="app-variables">
      <el-button type="primary" style="margin: 0 0 15px 0" @click="handleAdd"
        >新增
      </el-button>
      <vxe-table
        v-if="dialogVisible"
        ref="xTable"
        size="mini"
        :data="dataList"
        border
        show-overflow
        header-cell-class-name="header-cell"
        row-id="key"
        :radio-config="!isManage ? {trigger: 'row', highlight: true} : undefined"
      >
        <vxe-table-column v-if="!isManage" type="radio" title="选择" align="center" width="60"></vxe-table-column>
        <vxe-table-column field="key" title="变量编码"></vxe-table-column>
        <vxe-table-column
          field="desc"
          title="变量名"
          min-width="100px"
        ></vxe-table-column>
        <vxe-table-column title="默认值" field='defaultValue'>
          <template #default='{ row }'>
            <span>{{ getDefaultValue(row.defaultValue) }}</span>
          </template>
        </vxe-table-column>
        <vxe-table-column title="操作" width="100px">
          <template #default="{ row }">
            <el-button type="text" @click="handleEdit(row)">编辑</el-button>
            <el-button
              type="text"
              style="color: red;"
              @click="handleDelete(row)"
              >删除
            </el-button>
          </template>
        </vxe-table-column>
      </vxe-table>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button v-if="isManage" @click="cancel">关 闭</el-button>
      <el-button v-else type="primary" @click="confirm">确 认</el-button>
    </div>
    <el-dialog
      custom-class="low-code-dialog"
      :close-on-click-modal="false"
      :modal-append-to-body="true"
      :append-to-body="true"
      :title="detail.id ? '编辑' : '新增'"
      :visible.sync="editVisible"
      width="1000px"
      top="5vh"
      v-element-screen
    >
      <n-form
        label-position="left"
        ref="editForm"
        :rules="rules"
        :model="detail"
        @submit.native.prevent
        label-width="80px"
      >
        <n-form-item label="变量编码" prop="key">
          <el-input :disabled="detail.id" v-model="detail.key"></el-input>
        </n-form-item>
        <n-form-item label="变量名" prop="desc">
          <el-input v-model="detail.desc"></el-input>
        </n-form-item>
        <n-form-item label="变量值" prop="defaultValue">
          <el-tooltip
            v-if="detail.defaultValue !== undefined"
            effect="dark"
            :content="formatTip(detail.defaultValue)"
            placement="top"
          >
            <el-tag
              class="value-tag"
              closable
              disable-transitions
              @click="handleTagClick(detail, 'defaultValue')"
              @close="detail.defaultValue = undefined"
              :type='getTagType(detail.defaultValue)'
            >
              {{
                formatTag(getElementName(detail.defaultValue), 12)
              }}
            </el-tag>
          </el-tooltip>
          <template v-else>
            <el-button-group class="default-value">
              <el-button size="mini" @click="showInputDialog('text', detail, 'defaultValue')">文本</el-button>
            </el-button-group>
          </template>
        </n-form-item>
      </n-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false">取 消</el-button>
        <el-button type='primary' @click="handleEditConfirm">确 认</el-button>
      </div>
    </el-dialog>
    <input-dialog @confirm="handleUpdateValue" ref="inputDialog"></input-dialog>
  </el-dialog>
</template>

<script>
  import API from '@/api/application'
  import InputDialog from '@/components/CustomApi/components/CustomInputDialog'
  import { getValidators, required, hasSpecialCharacter } from '@/utils/validateUtils'
  import { CUSTOM_API_MACRO_VARIABLES } from '@/config';
  import _ from 'lodash'
  import mixins from '@/components/CustomApi/components/mixins'
  export default {
    name: 'MacroVariables',
    components: {
      InputDialog,
    },
    mixins: [mixins],
    data() {
      return {
        dialogVisible: false,
        editVisible: false,
        loading: false,
        dataList: [],
        detail: {
          key: '',
          defaultValue: undefined,
          desc: '',
          id: 0,
        },
        rules: {
          key: getValidators([
            {
              message: '请输入变量编码',
              include: required,
              required: true,
              trigger: 'change',
            },
            {
              message: '变量编码不能包含特殊字符',
              include: hasSpecialCharacter,
              trigger: 'change',
            },
          ]),
          desc: getValidators([
            {
              message: '请输入变量名',
              include: required,
              required: true,
              trigger: 'change',
            },
          ]),
        },
        isManage: true,
      }
    },
    methods: {
      async open({isManage = true, fullVariable} = {}) {
        this.isManage = isManage
        await this.handleSearch(true)
        this.dialogVisible = true
        if (fullVariable) {
          const row = this.dataList.find(item => `$macroVariables.${item.key}` === fullVariable)
          this.$nextTick(() => {
            if(row) {
              this.$refs.xTable.setRadioRow(row)
            }
          })
        }
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      cancel() {
        this.dialogVisible = false
      },
      handleSearch() {
        this.loading = true
        return this.$http({
          url: API.appDataMacroVariableList,
          data: {
            appId: Number(this.$route.query.id),
          },
        })
          .then(({ data }) => {
            this.dataList = data.map((item) => ({
              defaultValue: item.defaultValue,
              desc: item.description,
              id: item.id,
              key: item.name,
            }))
            window[CUSTOM_API_MACRO_VARIABLES] = this.dataList
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleUpdate(detail) {
        if (detail.defaultValue === undefined) {
          this.$message.error('请输入宏变量值！')
          return
        }
        this.loading = true
        this.$http({
          url: API.appDataMacroVariableUpdate,
          data: {
            defaultValue: detail.defaultValue,
            description: detail.desc,
            name: detail.key,
            id: detail.id,
            appId: Number(this.$route.query.id),
          },
        })
          .then(({ data }) => {
            this.handleSearch()
            this.editVisible = false
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleSave() {
        if (this.detail.defaultValue === undefined) {
          this.$message.error('请输入宏变量值！')
          return
        }
        this.loading = true
        this.$http({
          url: API.appDataMacroVariableSave,
          data: {
            defaultValue: this.detail.defaultValue,
            description: this.detail.desc,
            name: this.detail.key,
            appId: Number(this.$route.query.id),
          },
        })
          .then(({ data }) => {
            this.handleSearch()
            this.editVisible = false
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleEdit(row) {
        this.detail = _.cloneDeep(row)
        this.editVisible = true
        this.$nextTick(() => {
          this.$refs.editForm.clearValidate()
        })
      },
      async handleDelete(row) {
        await this.$confirm(`确认删除[${row.key}]吗？`)
        this.loading = true
        this.$http({
          url: API.appDataMacroVariableRemove,
          data: {
            id: row.id,
          },
        })
          .then(({ data }) => {
            this.handleSearch()
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleAdd() {
        this.detail = {
          key: '',
          defaultValue: undefined,
          desc: '',
          id: 0,
        }
        this.editVisible = true
        this.$nextTick(() => {
          this.$refs.editForm.clearValidate()
        })
      },
      async handleEditConfirm() {
        await this.$refs.editForm.validate()
        if (this.detail.id) {
          this.handleUpdate(this.detail)
        } else {
          if (this.dataList.some(item => item.key === this.detail.key)) {
            this.$message.error('编码不能重复！')
            return
          }
          this.handleSave()
        }
      },
      getDefaultValue(value) {
        if (_.isObject(value)) {
          return JSON.stringify(value, function(k, v) { return v === undefined ? null : v }, 2)
        }
        return value
      },
      confirm() {
        const xTable = this.$refs.xTable
        const selectedRow = xTable?.getRadioRecord()
        if (!selectedRow) {
          this.$message.error('请选择宏变量')
          return
        }
        this.$emit('confirm', `$macroVariables.${selectedRow.key}`)
        this.cancel()
      }
    },
  }
</script>

<style lang="less" scoped>
  .app-variables::v-deep {
  }
</style>
