<template>
  <div class="condition-panel-inner">
    <template v-if="!condition.conditionItems.length">
      <div class="condition-item">
        <div class="setting">
          <div class="btns">
            <div class="add-btn-primary" @click="handleAddCondition">
              +
            </div>
          </div>
        </div>
      </div>
    </template>
    <div
      class="condition-item"
      v-for="(c, index) in condition.conditionItems"
      :key="c"
    >
      <n-table :data="c.items" border header-cell-class-name="header-cell">
        <n-table-column v-if="isShowLeft" label="条件配置内容">
          <div class="value-input" slot-scope="scope">
            <el-tooltip
              v-if="
                scope.row.left.value !== undefined || scope.row.left.source.name
              "
              effect="dark"
              :content="formatCTip(scope.row.left)"
              placement="top"
            >
              <el-tag
                class="value-tag"
                closable
                disable-transitions
                @click="handleCTagClick(scope.row.left)"
                @close="handleClearValue(scope.row.left)"
                :type="getCTagType(scope.row.left)"
              >
                {{ formatCTag(scope.row.left) }}
              </el-tag>
            </el-tooltip>
            <div v-else class="value-input-content">
              <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                <el-button
                  size="mini"
                  @click="
                    showInputDialog(
                      'text',
                      scope.row.left,
                      'value',
                      scope.row.left
                    )
                  "
                  >文本
                </el-button>
                <el-button
                  size="mini"
                  @click="
                    showInputDialog(
                      'number',
                      scope.row.left,
                      'value',
                      scope.row.left
                    )
                  "
                  >数值
                </el-button>
                <el-button
                  size="mini"
                  @click="
                    showInputDialog(
                      'object',
                      scope.row.left,
                      'value',
                      scope.row.left
                    )
                  "
                  >对象
                </el-button>
                <el-button
                  size="mini"
                  @click="
                    handleShowVariableSelect(
                      scope.row.left.source,
                      'name',
                      () => {
                        scope.row.left.value = undefined
                      }
                    )
                  "
                  >选择出参
                </el-button>
              </el-button-group>
              <MoreTypeDropdown @change="(v) => scope.row.left.value = v" />
            </div>
          </div>
        </n-table-column>
        <n-table-column label="规则" width="120px">
          <template slot-scope="scope">
            <el-select size="mini" v-model="scope.row.op">
              <el-option
                v-for="item in ruleOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </template>
        </n-table-column>
        <n-table-column label="值">
          <div class="value-input" slot-scope="scope">
            <el-tooltip
              v-if="
                scope.row.right.value !== undefined ||
                  scope.row.right.source.name
              "
              effect="dark"
              :content="formatCTip(scope.row.right)"
              placement="top"
            >
              <el-tag
                class="value-tag"
                closable
                disable-transitions
                @click="handleCTagClick(scope.row.right)"
                @close="handleClearValue(scope.row.right)"
                :type="getCTagType(scope.row.right)"
              >
                {{ formatCTag(scope.row.right) }}
              </el-tag>
            </el-tooltip>
            <div v-else class="value-input-content">
              <el-button-group style="flex: 1;display: flex; align-items:center; margin-right: 4px;">
                <el-button
                  size="mini"
                  v-if="isShowLeft"
                  @click="
                    showInputDialog(
                      'text',
                      scope.row.right,
                      'value',
                      scope.row.right
                    )
                  "
                  >文本
                </el-button>
                <el-button
                  size="mini"
                  @click="
                    showInputDialog(
                      'number',
                      scope.row.right,
                      'value',
                      scope.row.right
                    )
                  "
                  >数值
                </el-button>
                <el-button
                  size="mini"
                  v-if="isShowLeft"
                  @click="
                    showInputDialog(
                      'object',
                      scope.row.right,
                      'value',
                      scope.row.right
                    )
                  "
                  >对象
                </el-button>
                <el-button
                  size="mini"
                  @click="
                    handleShowVariableSelect(
                      scope.row.right.source,
                      'name',
                      (n) => {
                        scope.row.right.value = undefined
                      }
                    )
                  "
                  >选择出参
                </el-button>
              </el-button-group>
              <MoreTypeDropdown @change="(v) => scope.row.right.value = v" />
            </div>
          </div>
        </n-table-column>
        <n-table-column label="操作" width="200px">
          <template slot-scope="scope">
            <div class="control-col">
              <el-switch
                v-if="scope.$index !== c.items.length - 1"
                v-model="scope.row.logic"
                active-value="or"
                inactive-value="and"
                active-text="或"
                inactive-text="且"
              >
              </el-switch>
              <div class="btns">
                <div
                  class="add-btn-primary"
                  @click="handleAddConditionChild(scope.$index, c.items)"
                >
                  +
                </div>
                <div
                  class="add-btn-primary"
                  @click="handleDeleteConditionChild(scope.$index, c.items)"
                >
                  -
                </div>
              </div>
            </div>
          </template>
        </n-table-column>
      </n-table>
      <div class="setting">
        <el-switch
          v-if="index !== condition.conditionItems.length - 1"
          v-model="c.logic"
          active-value="or"
          inactive-value="and"
          active-text="或"
          inactive-text="且"
        >
        </el-switch>
        <div class="btns">
          <div
            class="add-btn-primary"
            v-if="index === condition.conditionItems.length - 1"
            @click="handleAddCondition"
          >
            +
          </div>
          <div
            class="add-btn-primary"
            v-if="condition.conditionItems.length > 1"
            @click="handleDeleteCondition(index)"
          >
            -
          </div>
        </div>
      </div>
    </div>
    <input-dialog @confirm="handleUpdateValue" ref="inputDialog"></input-dialog>
    <CustomApiVariableSelect
      ref="variableSelect"
      @confirm="handleVariableConfirm"
      :showSelf="showSelf"
    ></CustomApiVariableSelect>
  </div>
</template>

<script>
  import mixins from './mixins'
  import CustomApiVariableSelect from './CustomApiVariableSelect.vue'

  export default {
    name: 'Condition',
    components: {
      CustomApiVariableSelect,
    },
    mixins: [mixins],
    props: {
      condition: {
        type: Object,
      },
      isShowLeft: {
        type: Boolean,
        default: true,
      },
      showSelf: {
        type: Boolean,
        default: false,
      },
    },
    data() {
      return {
        ruleOptions: [
          {
            label: '等于',
            value: 'EQ',
          },
          {
            label: '大于',
            value: 'GT',
          },
          {
            label: '小于',
            value: 'LT',
          },
          {
            label: '大于等于',
            value: 'GE',
          },
          {
            label: '小于等于',
            value: 'LE',
          },
          {
            label: '不等于',
            value: 'NE',
          },
          {
            label: '包含',
            value: 'INCL',
          },
          {
            label: '不包含',
            value: 'NON_INCL',
          },
          {
            label: '正则匹配',
            value: 'REGEX',
          },
        ],
      }
    },
    methods: {
      handleAddConditionChild(index, child) {
        child.push({
          left: {
            source: {
              name: '',
            },
            type: undefined,
            value: undefined,
          },
          logic: 'or',
          op: 'EQ',
          right: {
            source: {
              name: '',
            },
            type: undefined,
            value: undefined,
          },
        })
      },
      handleDeleteConditionChild(index, child) {
        child.splice(index, 1)
        if (!child.length) {
          this.condition.conditionItems = this.condition.conditionItems.filter((c) => c.items.length)
        }
      },
      handleAddCondition() {
        this.condition.conditionItems.push({
          items: [
            {
              left: {
                source: {
                  name: '',
                },
                type: undefined,
                value: undefined,
              },
              logic: 'or',
              op: 'EQ',
              right: {
                source: {
                  name: '',
                },
                type: undefined,
                value: undefined,
              },
            },
          ],
          logic: 'or',
        })
      },
      handleDeleteCondition(index) {
        this.condition.conditionItems.splice(index, 1)
      },
      formatCTip(c) {
        if (c.source.name) {
          return this.getElementName(c.source.name, this.showSelf)
        }
        return `${c.value}`
      },
      formatCTag(c) {
        if (c.source.name) {
          return this.getElementName(c.source.name, this.showSelf)
        }
        return `${c.value}`
      },
      handleCTagClick(c) {
        if (c.source.name) {
          this.handleShowVariableSelect(c.source, 'name')
        } else {
          this.handleTagClick(c, 'value')
        }
      },
      handleClearValue(c) {
        c.source.name = ''
        c.value = undefined
      },
      getCTagType(c) {
        if (c.source.name) {
          return this.getTagType(c.source.name)
        } else {
          return ''
        }
      },
      handleUpdateValue(data, from) {
        if (from) {
          from.source.name = ''
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  .condition-panel-inner::v-deep {
    margin-top: 10px;
    .condition-item {
      .value-input {
        display: flex;
        align-items: center;
        justify-content: space-between;

        & > .value-input-element {
          flex: 1;

          & > .el-form-item {
            margin-bottom: 0;
          }
        }

        & > .select-btn {
          width: 60px;
          margin-left: 10px;
        }
      }
    }

    .setting {
      display: flex;
      align-items: center;
      padding: 10px;
      background: #ffffff;
    }

    .control-col {
      display: flex;
      align-items: center;

      & > * {
        margin: 0 10px 0 0;

        &:last-child {
          margin: 0;
        }
      }
    }
    .btns {
      display: flex;
      width: 60px;
      margin-left: 10px;

      .add-btn-primary {
        margin-left: 10px;
      }
    }

    .variable-column {
      &:hover {
        cursor: pointer;
      }
    }
  }

  .value-input-content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
</style>
