<template>
  <el-popover
    v-model="popoverVisible"
    placement="bottom"
    title="选择逻辑"
    trigger="click"
  >
    <el-input
      class="tree-select-search"
      placeholder="输入名称筛选"
      clearable
      v-model="filterText"
    ></el-input>
    <n-tree
      :style="{
        minWidth: typeof width === 'string' ? width : `${width - 24}px`,
      }"
      class="tree-select-popper-content"
      :data="treeData"
      show-checkbox
      :default-expand-all="true"
      :default-expanded-keys="defaultExpandedKeys"
      :check-on-click-node="true"
      :expand-on-click-node="true"
      :node-key="nodeKey"
      :check-strictly="true"
      :filter-node-method="filterNode"
      ref="tree"
      icon-class="icon-right"
      :props="treeProps"
      :default-checked-keys="defaultSelectedKeys"
      @check="handleCheckClick"
      @check-change="handleCheckChange"
    >
      <div slot-scope="{ data }" class="widget-tree-node-name">
        <img class="widget-tree-node-icon" :src="getIcon(data)" alt="" />
        <span>{{ data.name }}</span>
      </div>
    </n-tree>
    <slot slot="reference"></slot>
  </el-popover>
</template>

<script>
  import _ from 'lodash'
  export default {
    name: 'LogicList',
    inject: ['customApiProvide'],
    props: {
      tree: {
        type: Array,
      },
      defaultSelectedKeys: {
        type: Array,
        default: () => [],
      },
      nodeKey: {
        type: String,
        default: 'code',
      },
      treeProps: {
        type: Object,
        default: () => ({
          children: 'actions',
          label: 'name',
          disabled: (data, node) => data.disabled,
        }),
      },
      width: {
        type: [String, Number],
        default: '380px',
      },
    },
    data() {
      return {
        popoverVisible: false,
        filterText: '',
        selectedNodes: [],
        defaultExpandedKeys: [],
      }
    },
    computed: {
      treeData() {
        if (this.tree) {
          return this.tree
        }
        return this.customApiProvide.apiDetail.steps.map(item => {
          return {
            ...item,
            disabled: true
          }
        })
      },
    },
    watch: {
      filterText(val) {
        this.$refs.tree.filter(val)
      },
    },
    methods: {
      filterNode(value, data) {
        if (!value) return true
        return data[this.treeProps.label].indexOf(value) !== -1
      },
      handleCheckClick(data, node, tree) {
        this.$refs.tree.setCheckedKeys([data[this.nodeKey]])
      },
      handleCheckChange: _.debounce(
        async function() {
          await this.$confirm('选择的配置将覆盖当前配置，是否继续？', '提示')
          const selectedNodes = this.$refs.tree.getCheckedNodes()
          this.$emit('change', selectedNodes)
        }, 100),
      getIcon(api) {
        if (api.actions) {
          return this.$getImgPrefix('/design/el-icon-folder-opened.png')
        }
        return this.$getImgPrefix('/design/el-icon-tickets.png')
      },
    },
  }
</script>

<style lang="less" scoped>
  .tree-select {
    width: 100%;
  }
  .tree-select-popper-trigger {
    display: none;
  }
  .tree-select-search {
    width: 100%;
    margin-bottom: 10px;
  }
  .tree-select-popper-content {
    max-height: 360px;
    overflow: auto;
  }
  .widget-tree-node-name {
    padding: 1px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    .widget-tree-node-icon {
      margin-right: 4px;
      width: 16px;
      height: 16px;
      background-size: 100% 100%;
    }
  }
</style>
