<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="结构预览"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible">
      <vue-json-pretty
        class="json-pretty"
        :data="json"
        :virtual="true"
        :itemHeight="32"
      >
      </vue-json-pretty>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">关 闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import _ from 'lodash'
  import treeTool from 'tree-tool'
  import { CUSTOM_API_RESPONSE_MAP } from '@/config'
  import { Factory } from '@/lib/vue-json-pretty'
  import 'vue-json-pretty/lib/styles.css'
  import { getPathList } from '@/utils/format'
  const VueJsonPretty = new Factory({ virtualLines: 50 })
  const treeUtil = treeTool.createInstance({
    id: 'key',
    children: 'children',
    pid: 'parentId',
  })
  export default {
    name: 'JsonView',
    components: {
      VueJsonPretty,
    },
    inject: ['customApiProvide'],
    props: {
      stepData: {
        type: Object
      }
    },
    data() {
      return {
        dialogVisible: false,
        json: {},
      }
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      open({ treeData }) {
        const filterTree = treeUtil.filter(treeData, (node) => {
          return node.mapperConfig || node.reference
        })
        const tree2Obj = (tree, isArray) => {
          let obj = isArray ? [] : {}
          tree.forEach((node) => {
            if (node.mapperConfig) {
              switch (node.type) {
                case 'String':
                  obj[node.key] = ''
                  break
                case 'Number':
                  obj[node.key] = 0
                  break
                case 'Boolean':
                  obj[node.key] = false
                  break
                case 'Object':
                  obj[node.key] = tree2Obj(node.children)
                  break
                case 'Array':
                  obj[node.key] = tree2Obj(node.children, true)
                  break
                case 'Null':
                case 'Undefined':
                  obj[node.key] = null
                  break
              }
            } else {
              const pathList = getPathList(node.reference.name)
              const $key = pathList[0]
              let root
              switch ($key) {
                case '$global':
                  root = this.customApiProvide.apiDetail.requestBody
                  break
                case `$${this.stepData.code}`:
                  root =
                    window[CUSTOM_API_RESPONSE_MAP][`${this.stepData.code}_source`]
                  break
                default:
                  root = window[CUSTOM_API_RESPONSE_MAP][this.stepData.code]
                  break
              }
              obj[node.key] = _.get(root, pathList.slice(1))
            }
          })
          return obj
        }
        this.json = tree2Obj(filterTree)
        this.dialogVisible = true
      },
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .dialog-content::v-deep {
    .json-pretty {
      height: 400px;
    }
  }
</style>
