<template>
  <div class='groovy-code'>
    <div class="highlight-code" ref="codeEditor" v-loading.lock="loading"></div>
    <ChangeHeight v-if="resizable" @heightChange="handleHeightChange" />
  </div>
</template>

<script>
  import _ from 'lodash'
  import ChangeHeight from '@/components/ChangeHeight'

  export default {
    name: 'GroovyCode',
    components: {
      ChangeHeight
    },
    props: {
      value: {
        type: String,
      },
      paramsData: {
        type: Array,
        default: () => [],
      },
      resizable: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        loading: false,
        hints: [],
        monacoInstance: null,
        provider: null,
        logList: [],
        code: ''
      }
    },
    computed: {
      dynamicHints() {
        return this.paramsData.map((item) => item.key).filter(item => item)
      },
    },
    watch: {
      paramsData: {
        handler(n, o) {
          if (n.length) {
            this.initHints()
          }
        },
        deep: true,
        immediate: true,
      },
      value: {
        handler(n, o) {
          if (_.isEqual(n, o)) {
            return
          }
          this.code = n
          this.monacoInstance?.setValue(n)
        },
        immediate: true
      }
    },
    methods: {
      initHints() {
        this.hints = Array.from(new Set(this.dynamicHints)).sort()
      },
      dispose() {
        if (this.monacoInstance) {
          if (this.monacoInstance.getModel()) {
            this.monacoInstance.getModel().dispose()
          }
          this.monacoInstance.dispose()
          this.monacoInstance = null
          if (this.provider) {
            this.provider.dispose()
            this.provider = null
          }
        }
      },
      async init() {
        const target = this.$refs.codeEditor
        if (!target) {
          return
        }
        this.loading = true
        this.code = this.value
        const src = [
          import('monaco-editor/esm/vs/editor/editor.api.js'),
          import('monaco-ace-tokenizer/lib/ace/definitions/groovy'),
          import('monaco-ace-tokenizer'),
        ]
        const [monaco, Groovy, t] = await Promise.all(src)
        monaco.languages.register({
          id: 'groovy',
        })
        t.registerRulesForLanguage('groovy', new Groovy.default())
        const that = this
        this.dispose()
        const createCompleters = (textUntilPosition) => {
          //过滤特殊字符
          const _textUntilPosition = textUntilPosition
            .replace(/[\*\[\]@\$\(\)]/g, '')
            .replace(/(\s+|\.)/g, ' ')
          //切割成数组
          const arr = _textUntilPosition.split(' ')
          //取当前输入值
          const activeStr = arr[arr.length - 1]
          //获得输入值的长度
          const len = activeStr.length

          //获得编辑区域内已经存在的内容
          const rexp = new RegExp('([^\\w]|^)' + activeStr + '\\w*', 'gim')
          const match = that.value.match(rexp)
          const _hints = !match
            ? []
            : match.map((ele) => {
                const rexp = new RegExp(activeStr, 'gim')
                const search = ele.search(rexp)
                return ele.substr(search)
              })

          //查找匹配当前输入值的元素
          const hints = Array.from(new Set([...that.hints, ..._hints]))
            .sort()
            .filter((ele) => {
              let rexp = new RegExp(ele.substr(0, len), 'gim')
              return (match && match.length === 1 && ele === activeStr) ||
                ele.length === 1
                ? false
                : activeStr.match(rexp)
            })
          //添加内容提示
          return hints.map((ele) => {
            return {
              label: ele,
              kind:
                that.hints.indexOf(ele) > -1
                  ? monaco.languages.CompletionItemKind.Keyword
                  : monaco.languages.CompletionItemKind.Text,
              documentation: ele,
              insertText: ele,
            }
          })
        }
        this.provider = monaco.languages.registerCompletionItemProvider('groovy', {
          // triggerCharacters: ['$'],
          provideCompletionItems(model, position) {
            const textUntilPosition = model.getValueInRange({
              startLineNumber: position.lineNumber,
              startColumn: 1,
              endLineNumber: position.lineNumber,
              endColumn: position.column,
            })
            const suggestions = createCompleters(textUntilPosition)
            return {
              suggestions,
            }
          },
        })

        // 初始化编辑器实例
        this.monacoInstance = monaco.editor.create(target, {
          value: this.code,
          language: 'groovy',
          theme: 'vs',
          folding: false,
          foldingHighlight: true,
          foldingStrategy: 'indentation',
          showFoldingControls: 'always',
          disableLayerHinting: true,
          emptySelectionClipboard: false,
          selectionClipboard: false,
          automaticLayout: true,
          codeLens: false,
          scrollBeyondLastLine: false,
          colorDecorators: true,
          accessibilitySupport: 'off',
          lineNumbers: 'on',
          lineNumbersMinChars: 5,
          enableSplitViewResizing: false,
          roundedSelection: false,
          overviewRulerBorder: false,
          minimap: {
            enabled: false,
          },
          autoIndent: 'full',
          readOnly: false,
          tabSize: 2,
        })

        // 监听编辑器content变化事件
        // this.monacoInstance.onDidChangeModelContent(
        //   _.debounce(
        //     () => {
        //       if (!this.monacoInstance) {
        //         return
        //       }
        //       this.code = this.monacoInstance.getValue()
        //     },
        //     500,
        //     { leading: false, trailing: true }
        //   )
        // )
        this.monacoInstance.onDidFocusEditorText(() => {
          this.$emit('focus-change', true)
        })
        this.monacoInstance.onDidBlurEditorText(() => {
          if (!this.monacoInstance) {
            return
          }
          this.code = this.monacoInstance.getValue()
          this.$emit('input', this.code)
          this.$emit('focus-change', false)
        })
        this.loading = false
      },
      getParams() {
        const params = this.paramsData.filter((d) => d.key)
        return Object.fromEntries(
          params.map((d) => {
            return [d.key, d.test]
          })
        )
      },
      handleHeightChange: _.throttle(function ({height}) {
        const calcHeight = this.$el.clientHeight + height
        const width = this.$el.clientWidth
        this.monacoInstance.layout({
          width,
          height: calcHeight < 244 ? 224 : calcHeight
        })
      }, 60)
    },
    created() {
      this.initHints()
    },
    mounted() {
      this.$nextTick(() => {
        this.init()
      })
    },
    beforeDestroy() {
      this.dispose()
    },
  }
</script>

<style lang="less" scoped>
  .groovy-code::v-deep {
    width: 100%;
    height: 100%;
    min-height: 244px;
    .highlight-code {
      width: 100%;
      height: 100%;
      min-height: 244px;
    }
  }
</style>
