<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="数据集管理"
    :visible.sync="dialogVisible"
    width="1000px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class='dialog-content' v-loading='loading'>
      <div class='table-search'>
        <el-select v-model='condition.type' @change="doSearch">
          <el-option
            v-for='option in optionList'
            :key='option.value'
            :value='option.value'
            :label='option.label'
          ></el-option>
        </el-select>
        <el-input style='width: 250px;' v-model='condition.keyword' clearable placeholder='输入关键字查询'></el-input>
        <el-button type='primary' icon='el-icon-search' @click='doSearch'></el-button>
      </div>
      <vxe-table
        ref="table"
        size="mini"
        :data="dataList"
        border
        show-overflow
        header-cell-class-name="header-cell"
      >
        <vxe-table-column field="name" title="名称"></vxe-table-column>
        <vxe-table-column field="type" title="类型">
          <template #default="{ row }">
            <span>{{ getTypeLabel(row.type) }}</span>
          </template>
        </vxe-table-column>
        <vxe-table-column
          field="description"
          title="描述"
          min-width="100px"
        ></vxe-table-column>
        <vxe-table-column title="操作" width="100px">
          <template #default="{ row }">
            <el-button type="text" @click="handleEdit(row)">编辑</el-button>
            <el-button
              type="text"
              style="color: red;"
              @click="handleDelete(row)"
            >删除
            </el-button>
          </template>
        </vxe-table-column>
      </vxe-table>
      <div class='pager'>
        <vxe-pager
          background
          size="small"
          :current-page="pagination.pagenum"
          :page-size="pagination.pagesize"
          :total="totalCount"
          :page-sizes="[
            10,
            20,
            50,
            100,
          ]"
          @page-change="handlePageChange"
        >
        </vxe-pager>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type='primary' @click="dialogVisible = false">确 认</el-button>
    </div>
    <data-set-dialog @refresh='handleSearch' ref='dataSetDialog'></data-set-dialog>
  </el-dialog>
</template>

<script>

  import API from '@/api/application'
  import DataSetDialog from './edit-dialog'

  export default {
    name: 'DataSetManage',
    components: {
      DataSetDialog
    },
    data() {
      return {
        loading: false,
        dialogVisible: false,
        dataList: [],
        condition: {
          type: 2,
          keyword: ''
        },
        pagination: {
          pagenum: 1,
          pagesize: 20
        },
        totalCount: 0,
        optionList: [
          {
            label: '静态数据',
            value: 1
          },
          {
            label: '模板SQL',
            value: 3
          },
          {
            label: '动态SQL',
            value: 2
          },
          {
            label: '网关API',
            value: 4
          },
          {
            label: '自定义API',
            value: 5
          },
          {
            label: '外部API',
            value: 6
          },
        ]
      }
    },
    methods: {
      open() {
        this.condition.type = 2
        this.condition.keyword = ''
        this.pagination.pagenum = 1
        this.pagination.pagesize = 20
        this.handleSearch()
        this.dialogVisible = true
      },
      getTypeLabel(type) {
        return this.optionList.find(item => item.value === type)?.label
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      handleEdit(row) {
        this.$refs.dataSetDialog.open(row)
      },
      async handleDelete(row) {
        await this.$confirm(`确认删除[${row.name}]吗？`)
        this.handleRemove(row.id)
      },
      doSearch() {
        this.pagination.pagenum = 1
        this.handleSearch()
      },
      handleRemove(id) {
        this.loading = true
        this.$http({
          url: API.datasetManageRemove,
          data: {
            id
          },
        })
          .then(({ data }) => {
            this.dataList = this.dataList.filter(item => item.id !== id)
            this.totalCount--
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleSearch() {
        this.loading = true
        this.$http({
          url: API.datasetManagePage,
          data: {
            condition: this.condition,
            pagination: this.pagination
          },
        })
          .then(({ data }) => {
            this.dataList = data.records
            this.totalCount = data.rowCount
          })
          .finally(() => {
            this.loading = false
          })
      },
      handlePageChange({ currentPage, pageSize }) {
        this.pagination.pagenum = currentPage
        this.pagination.pagesize = pageSize
        this.handleSearch()
      }
    }
  }
</script>

<style lang='less' scoped>
  .dialog-content::v-deep {
    .table-search {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-bottom: 10px;
      & > .el-input {
        margin: 0 10px;
      }
    }
  }
</style>
