<template>
  <el-dialog
    custom-class="low-code-dialog"
    ref="dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="API测试日志"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible" v-loading="loading">
      <div class="mock-info">
        <div class="info-item">
          <img :src="$getImgPrefix('/custom-api/do-status.svg')" class="info-item" />
          <div class="info-content">
            <div class="info-title">{{ status }}</div>
            <div class="info-desc">执行状态</div>
          </div>
        </div>
        <div class="info-item">
          <img :src="$getImgPrefix('/custom-api/time.svg')" class="info-item" />
          <div class="info-content">
            <div class="info-title">{{ time }} ms</div>
            <div class="info-desc">总耗时</div>
          </div>
        </div>
      </div>
      <div class="mock-request">
        <div class="title" @click="isShowRequest = !isShowRequest">
          <span class="t">入参JSON</span>
          <i
            :class="
              isShowRequest ? 'el-icon-arrow-down' : 'el-icon-arrow-right'
            "
          ></i>
        </div>
        <div class="mock-request-content" v-if="isShowRequest">
          <pre class="json-code">{{ JSON.stringify(apiDetail.requestBody, function(k, v) { return v === undefined ? null : v }, 2) }}</pre>
        </div>
      </div>
      <div class="mock-request">
        <div class="title" @click="isShowLog = !isShowLog">
          <span class="t">测试日志</span>
          <i
            :class="
              isShowLog ? 'el-icon-arrow-down' : 'el-icon-arrow-right'
            "
          ></i>
        </div>
        <div class="mock-request-content" v-if="isShowLog">
          <pre class="json-code">{{ logMessage }}</pre>
        </div>
      </div>
      <div class="mock-request">
        <div class="title" @click="isShowResult = !isShowResult">
          <span class="t">测试结果</span>
          <i
            :class="
              isShowResult ? 'el-icon-arrow-down' : 'el-icon-arrow-right'
            "
          ></i>
        </div>
        <div class="mock-request-content" v-if="isShowResult">
          <pre class="json-code">{{ JSON.stringify(response, function(k, v) { return v === undefined ? null : v }, 2) }}</pre>
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">关 闭</el-button>
      <!-- <el-button type="primary" @click="handleExport">导 出</el-button> -->
    </div>
  </el-dialog>
</template>

<script>
  import customApi from '@/api/customApi'
  import { CUSTOM_API_SESSION_ID, CUSTOM_API_RESPONSE_MAP } from '@/config'

  export default {
    name: 'ApiScriptMock',
    inject: ['customApiProvide'],
    data() {
      return {
        dialogVisible: false,
        loading: false,
        apiDetail: {},
        logData: null,
        response: null,
        isShowRequest: true,
        isShowLog: true,
        isShowResult: true,
      }
    },
    computed: {
      time() {
        return this.logData
          ? this.logData.scriptResult.endTime -
              this.logData.scriptResult.beginTime
          : 0
      },
      status() {
        return this.logData?.scriptResult?.success ? '执行成功' : '执行失败'
      },
      logMessage() {
        return this.logData
          ? this.logData.scriptResult.log || '无数据'
          : '无数据'
      },
    },
    methods: {
      handleClose(done) {
        this.apiDetail = {}
        this.logData = null
        this.selectedAction = null
        this.selectedStep = null
        this.responseHeight = 0
        this.isShowResponse = false
        this.response = null
        this.isShowRequest = true
        this.dialogVisible = false
        done()
      },
      open({ apiDetail } = {}) {
        this.apiDetail = apiDetail
        this.handleMock()
        this.dialogVisible = true
      },
      handleMock() {
        this.loading = true
        this.$http({
          url: customApi.apiDesignMockRunApi,
          data: {
            sessionId: window[CUSTOM_API_SESSION_ID],
            api: this.apiDetail,
          },
        })
          .then((res) => {
            this.response = res
            window[CUSTOM_API_RESPONSE_MAP][this.apiDetail.id] = this.response
            this.handleGetLog()
          })
          .catch(() => {
            this.dialogVisible = false
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleExport() {},
      handleGetLog() {
        this.$http({
          url: customApi.apiDesignGetRunApiDetail,
          data: {
            sessionId: window[CUSTOM_API_SESSION_ID],
          },
        })
          .then((res) => {
            this.logData = res.data
          })
          .finally(() => {
            this.loading = false
          })
      },
    },
  }
</script>

