<template>
  <el-dialog
    custom-class="low-code-dialog"
    title="新增属性"
    width="340px"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    :visible.sync="popoverVisible"
    top="15vh"
  >
    <div class="variable-body" v-if="popoverVisible">
      <n-form class='data-form' ref="variableForm" :model="formData" @submit.native.prevent inline label-width='60px'>
        <n-form-item v-if="needName" label="Key" prop="keyName">
          <el-input
            style="width: 180px"
            type="text"
            v-model="formData.keyName"
            placeholder="Key"
          />
        </n-form-item>
        <n-form-item label="值类型">
          <el-select
            style="width: 180px"
            :popper-append-to-body="false"
            v-model="formatSelected"
            @change="formData.valName = undefined"
          >
            <el-option
              v-for="item in formats"
              :value="item.value"
              :label="item.label"
              :key="item.value"
              :disabled="item.value === 'variable' && !showVariable"
            >
            </el-option>
          </el-select>
        </n-form-item>
        <template v-if="formatSelected === 'variable'">
          <n-form-item label="测试值">
            <div class="value-input-wrapper">
              <el-tooltip
                v-if="formData.valName !== undefined"
                effect="dark"
                :content="formatTip(formData.valName)"
                placement="top"
              >
                <el-tag
                  class="value-tag"
                  closable
                  disable-transitions
                  @click="handleShowVariableSelect(formData, 'valName')"
                  @close="formData.valName = undefined"
                  :type='getTagType(formData.valName)'
                >
                  {{ formatTag(getElementName(formData.valName), 12) }}
                </el-tag>
              </el-tooltip>
              <el-button
                v-else
                @click="handleShowVariableSelect(formData, 'valName')"
                >选择参数
              </el-button>
            </div>
          </n-form-item>
        </template>
        <template
          v-if="!['array', 'object', 'variable'].includes(formatSelected)"
        >
          <n-form-item label="测试值" prop="valName">
            <div class="value-input-wrapper">
              <el-input
                style="width: 180px"
                type="text"
                v-model="formData.valName"
                placeholder="value"
                v-if="formatSelected === 'string'"
              />
              <el-input
                style="width: 180px"
                type="number"
                v-model="formData.valName"
                placeholder="value"
                v-if="formatSelected === 'number'"
                @change="dealNumber"
              />
              <el-select
                style="width: 180px"
                v-model="formData.valName"
                v-if="formatSelected === 'boolean'"
                @change="dealBoolean"
              >
                <el-option :value="true" label="true"></el-option>
                <el-option :value="false" label="false"></el-option>
              </el-select>
            </div>
          </n-form-item>
        </template>
      </n-form>
      <div style="text-align: right; margin: 0">
        <el-button type="primary" size="mini" @click="confirm">确认 </el-button>
        <el-button size="mini" type="text" @click="cancel">取消 </el-button>
      </div>
    </div>
    <CustomApiVariableSelect
      ref="variableSelect"
      @confirm="handleVariableConfirm"
      />
  </el-dialog>
</template>

<script>
  import mixins from '@/components/CustomApi/components/mixins'

  export default {
    name: 'ItemAddForm',
    mixins: [mixins],
    inject: ['editorProvide'],
    data() {
      return {
        formats: [
          {
            label: '参数',
            value: 'variable',
          },
          {
            label: '文本',
            value: 'string',
          },
          {
            label: '数值',
            value: 'number',
          },
          {
            label: '布尔',
            value: 'boolean',
          },
          {
            label: '对象',
            value: 'object',
          },
          {
            label: '数组',
            value: 'array',
          },
        ],
        formatSelected: 'string',
        popoverVisible: false,
        formData: {
          keyName: '',
          valName: '',
        },
      }
    },
    props: {
      showVariable: {
        type: Boolean,
        default: true
      },
      needName: {
        default: true,
      },
    },
    methods: {
      show(key, value, type) {
        this.formData.keyName = key
        this.formData.valName = value
        this.formatSelected = type || 'string'
        this.popoverVisible = true
      },
      confirm: function() {
        if (this.needName && !this.formData.keyName) {
          this.$message.error('请输入key')
          return
        }
        if (this.formatSelected === 'variable' && !this.formData.valName) {
          this.$message.error('请选择变量')
          return
        }
        let val = null
        if (
          this.formatSelected === 'array' ||
          this.formatSelected === 'object'
        ) {
          val = []
        } else {
          val = this.formData.valName
        }

        let objData = {
          key: this.needName ? this.formData.keyName : null,
          val: val,
          type: this.formatSelected,
        }

        this.$emit('confirm', objData)
        this.popoverVisible = false
      },

      cancel: function() {
        this.popoverVisible = false
      },

      dealBoolean: function() {
        this.formData.valName = Boolean(this.formData.valName)
      },

      dealNumber: function() {
        this.formData.valName = Number(this.formData.valName)
      }
    },
  }
</script>

<style lang="less" scoped>
  .value-input-wrapper {
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;

    .el-button {
      margin-left: 10px;
    }
  }
</style>
