<template>
  <el-dialog
    custom-class="low-code-dialog"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    :title="title"
    :visible.sync="dialogVisible"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content" v-if="dialogVisible" v-loading="false">
      <div class="mode" v-if="mapConfig.mode === 'STANDARD'">
        <el-button @click="changeMode"
          >专业模式<i class="el-icon-sort pro-icon"></i
        ></el-button>
        <el-button @click="handleShowTree">树结构查看</el-button>
      </div>
      <div class="mode" v-else>
        <el-button @click="changeMode"
          >标准模式<i class="el-icon-sort pro-icon"></i
        ></el-button>
        <el-button @click="handleMockScript">测试</el-button>
      </div>
      <div class="config" v-if="mapConfig.mode === 'STANDARD'">
        <div class="title">
          <span class="t">出参结构定义</span>
          <el-button size="mini" type="primary" @click="handleShowTreeChange"
            >转换器</el-button
          >
        </div>

        <JsonTree
          :treeNode="mapConfig.standardMapConfig"
          :stepData="stepData"
        />
        <JsonView :stepData="stepData" ref="jsonView" />
        <TreeChange ref="treeChange" @confirm="handleTreeChangeConfirm" />
      </div>
      <div class="config" v-else>
        <div class="title">
          <span class="t">代码输入</span>
          <div>
            <el-tooltip popper-class="tooltip-question" placement="left">
              <div
                slot="content"
                v-html="templateContent"
              ></div>
              <i class="el-icon-question test-btn tooltip"></i>
            </el-tooltip>
            <el-button-group style="margin-left: 20px;">
              <el-button
                size="mini"
                :type="
                  mapConfig.professionalMapConfig.scriptType === 'GROOVY'
                    ? 'primary'
                    : ''
                "
                @click="changeScriptType('GROOVY')"
                >Groovy</el-button
              >
              <el-button
                size="mini"
                :type="
                  mapConfig.professionalMapConfig.scriptType === 'JAVASCRIPT'
                    ? 'primary'
                    : ''
                "
                @click="changeScriptType('JAVASCRIPT')"
                >JavaScript</el-button
              >
              <el-button
                size="mini"
                :type="
                  mapConfig.professionalMapConfig.scriptType === 'PYTHON'
                    ? 'primary'
                    : ''
                "
                @click="changeScriptType('PYTHON')"
                >Python</el-button
              >
            </el-button-group>
          </div>
        </div>

        <div class="code-wrapper" :class="{ 'is-show-log': isShowLog }">
          <div class="empty-code" v-html="templateText"></div>
          <js-code
            v-if="mapConfig.professionalMapConfig.scriptType === 'JAVASCRIPT'"
            class="js-code"
            ref="jsCode"
            @focus-change="focusChange"
            :params-data="paramsData"
            :resizable="true"
            v-model="mapConfig.professionalMapConfig.script"
          ></js-code>
          <groovy-code
            v-if="mapConfig.professionalMapConfig.scriptType === 'GROOVY'"
            class="js-code"
            ref="groovyCode"
            @focus-change="focusChange"
            :params-data="paramsData"
            :resizable="true"
            v-model="mapConfig.professionalMapConfig.script"
          ></groovy-code>
          <python-code
            v-if="mapConfig.professionalMapConfig.scriptType === 'PYTHON'"
            class="js-code"
            ref="pythonCode"
            @focus-change="focusChange"
            :params-data="paramsData"
            :resizable="true"
            v-model="mapConfig.professionalMapConfig.script"
          ></python-code>
          <div class="log-wrapper" v-if="isShowLog">
            <el-button
              class="clear-btn"
              size="mini"
              :disabled="!logList.length"
              @click="handleClearLog"
              >清空结果</el-button
            >
            <el-button class="clear-btn" size="mini" @click="isShowLog = false"
              >关闭</el-button
            >
            <div class="log-list">
              <div
                class="log-item"
                v-for="(log, index) in logList"
                :key="index"
              >
                {{ log }}
              </div>
            </div>
          </div>
        </div>

        <div class="params-config">
          <!-- <el-button
            size="mini"
            class="source-edit-button"
            type="primary"
            @click="
              showInputDialog('object', paramsSource, 'params', 'paramsSource')
            "
            >转换器</el-button
          > -->
          <n-table :data="params" border style="width: 100%;margin-top: 10px;">
            <n-table-column prop="key" label="参数名">
              <template slot-scope="scope">
                <el-input v-model="scope.row.key"></el-input>
              </template>
            </n-table-column>
            <n-table-column prop="value" label="参数值">
              <template slot-scope="scope">
                <div class="value-input">
                  <el-tooltip
                    v-if="scope.row.value !== undefined"
                    effect="dark"
                    :content="formatTip(scope.row.value)"
                    placement="top"
                  >
                    <el-tag
                      class="value-tag"
                      closable
                      disable-transitions
                      @click="handleTagClick(scope.row, 'value')"
                      @close="scope.row.value = undefined"
                      :type="getTagType(scope.row.value, true)"
                    >
                      {{ formatTag(getElementName(scope.row.value, true), 12) }}
                    </el-tag>
                  </el-tooltip>
                  <div v-else class="value-input-content">
                    <el-button
                      size="mini"
                      @click="handleShowVariableSelect(scope.row, 'value')"
                      >参数
                    </el-button>
                  </div>
                </div>
              </template>
            </n-table-column>
            <n-table-column prop="mockValue" label="测试值">
              <template slot-scope="scope">
                <div class="value-input">
                  <el-tooltip
                    v-if="scope.row.mockValue !== undefined"
                    effect="dark"
                    :content="formatTip(scope.row.mockValue)"
                    placement="top"
                  >
                    <el-tag
                      class="value-tag"
                      closable
                      disable-transitions
                      @click="handleTagClick(scope.row, 'mockValue')"
                      @close="scope.row.mockValue = undefined"
                    >
                      {{ formatTag(scope.row.mockValue) }}
                    </el-tag>
                  </el-tooltip>
                  <div v-else class="value-input-content">
                    <el-button-group style="display: flex; align-items:center; margin-right: 4px;">
                      <el-button
                        size="mini"
                        @click="showInputDialog('text', scope.row, 'mockValue')"
                        >文本
                      </el-button>
                      <el-button
                        size="mini"
                        @click="
                          showInputDialog('number', scope.row, 'mockValue')
                        "
                        >数值
                      </el-button>
                      <el-button
                        size="mini"
                        @click="
                          showInputDialog('object', scope.row, 'mockValue')
                        "
                        >对象
                      </el-button>
                    </el-button-group>
                    <MoreTypeDropdown @change="(v) => scope.row.mockValue = v" />
                  </div>
                </div>
              </template>
            </n-table-column>
            <n-table-column width="180" label="操作">
              <template slot-scope="scope">
                <div class="btns">
                  <div class="add-btn-primary" @click="handleAddParams">+</div>
                  <div
                    style="margin-left: 10px;"
                    class="add-btn-primary"
                    @click="handleDeleteParams(scope.$index)"
                  >
                    -
                  </div>
                </div>
              </template>
            </n-table-column>
          </n-table>
          <div v-if="mapConfig.professionalMapConfig.scriptType === 'PYTHON'" class="return-params" style="margin-top: 10px">
            返回值定义：<el-tag style="margin: 0 10px">out_param</el-tag> 程序会返回代码中同名的变量
          </div>
          <custom-input-dialog
            @confirm="handleUpdateValue"
            ref="inputDialog"
          ></custom-input-dialog>
          <CustomApiVariableSelect
            ref="variableSelect"
            :showSelf="true"
            @confirm="handleVariableConfirm"
          ></CustomApiVariableSelect>
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="handleConfirm">确 认</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import _ from 'lodash'
  import customApi from '@/api/customApi'
  import TreeChange from './TreeChange.vue'
  import JsonView from './JsonView.vue'
  import JsCode from '@/components/JsCode'
  import GroovyCode from '@/components/GroovyCode'
  import PythonCode from '@/components/PythonCode'
  import mixins from '../mixins'
  import { getMapTree } from '@/utils/format'
  import { decryptByDES, encryptByDES } from '@/utils/encrypt'

  const templateText1 = `支持JS原生语法，使用“参数名”进行取值, 使用return返回结果，示例JS：<br>
var age = in_param.age; <br>
return age + 1;`

  const templateText2 = `支持Groovy语法，使用“参数名”进行取值，示例Groovy：<br>
def age = in_param.age; <br>
return age + 1;`

  const templateText3 = `支持Python语法，使用“参数名”进行取值，示例Python：<br>
age = in_param['age'] <br>
out_param= age + 1`

  const initialMapConfig = {
    mode: 'STANDARD',
    standardMapConfig: {
      children: [],
      type: 'Object',
    },
  }
  export default {
    name: 'Formatter',
    components: {
      TreeChange,
      JsonView,
      JsCode,
      GroovyCode,
      PythonCode,
    },
    mixins: [mixins],
    data() {
      return {
        loading: false,
        dialogVisible: false,
        mapConfig: undefined,
        stepData: null,
        editorFocus: false,
        isShowLog: false,
        logList: [],
      }
    },
    computed: {
      title() {
        return this.mapConfig?.mode === 'STANDARD'
          ? '出参格式化标准模式'
          : '出参格式化专业模式'
      },
      templateText() {
        if (
          this.mapConfig?.professionalMapConfig?.script ||
          this.editorFocus ||
          this.isShowLog
        ) {
          return ''
        }
        return this.templateContent
      },
      templateContent() {
        switch (this.mapConfig?.professionalMapConfig?.scriptType) {
          case 'JAVASCRIPT': return templateText1
          case 'GROOVY': return templateText2
          case 'PYTHON': return templateText3
        }
      },
      paramsData() {
        return this.params.map((item) => ({
          key: item.key,
          value: item.value,
          test: item.mockValue,
        }))
      },
      params() {
        return this.mapConfig?.professionalMapConfig?.params || []
      },
      paramsSource: {
        get() {
          return {
            params: Object.fromEntries(
              this.params.map((item) => [[item.key], item.value])
            ),
          }
        },
        set(n) {
          this.mapConfig.professionalMapConfig.params = Object.entries(n).map(
            ([key, value]) => ({
              key,
              value,
              mockValue: value,
            })
          )
        },
      },
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      handleConfirm() {
        if (this.mapConfig.professionalMapConfig) {
          this.mapConfig.professionalMapConfig.script = encryptByDES(this.mapConfig.professionalMapConfig.script)
          this.mapConfig.professionalMapConfig.params = this.mapConfig.professionalMapConfig.params.filter(
              (item) => item.key
            )
        }
        this.$emit('confirm', this.mapConfig)
        this.dialogVisible = false
      },
      open({ stepData } = {}) {
        this.stepData = _.cloneDeep(stepData)
        this.mapConfig =
          this.stepData.mapConfig || _.cloneDeep(initialMapConfig)
        this.mapConfig.standardMapConfig.children = this.mapConfig
          .standardMapConfig.children.length
          ? this.mapConfig.standardMapConfig.children
          : [{ key: '', type: 'String' }]
        if (this.mapConfig.professionalMapConfig) {
          this.mapConfig.professionalMapConfig.script = decryptByDES(this.mapConfig.professionalMapConfig.script)
          this.mapConfig.professionalMapConfig.params = this.mapConfig.professionalMapConfig.params.length ? this.mapConfig.professionalMapConfig.params : [{ key: '', value: undefined, mockValue: undefined }]
        }
        this.dialogVisible = true
      },
      changeMode() {
        this.mapConfig.mode =
          this.mapConfig.mode === 'STANDARD' ? 'PROFESSIONAL' : 'STANDARD'
        if (this.mapConfig.mode === 'PROFESSIONAL') {
          if (!this.mapConfig.professionalMapConfig) {
            this.$set(this.mapConfig, 'professionalMapConfig', {
              script: '',
              scriptType: 'GROOVY',
              params: [{ key: '', value: undefined, mockValue: undefined }],
            })
          }
        }
      },
      handleShowTree() {
        this.$refs.jsonView.open({
          treeData: this.mapConfig.standardMapConfig.children,
        })
      },
      handleShowTreeChange() {
        this.$refs.treeChange.open({ stepData: this.stepData })
      },
      handleTreeChangeConfirm(res) {
        const objTree = getMapTree(res, `$${this.stepData.code}`)
        this.mapConfig.standardMapConfig.children = objTree.length
          ? objTree
          : [{ key: '', type: 'String' }]
      },
      focusChange(n) {
        this.editorFocus = n
        if (n) {
          this.isShowLog = false
        }
      },
      changeScriptType(type) {
        if (type === this.mapConfig.professionalMapConfig.scriptType) {
          return
        }
        this.mapConfig.professionalMapConfig.scriptType = type
      },
      handleAddParams() {
        this.mapConfig.professionalMapConfig.params.push({
          key: '',
          value: undefined,
          mockValue: undefined,
        })
      },
      handleDeleteParams(index) {
        const paramsData = this.mapConfig.professionalMapConfig.params
        if (paramsData.length === 1) {
          paramsData[0].key = ''
          paramsData[0].value = undefined
          paramsData[0].mockValue = undefined
          return
        }
        paramsData.splice(index, 1)
      },
      handleMockScript() {
        this.loading = true
        this.$http({
          url: customApi.apiDesignMockEvalScript,
          data: {
            ...this.mapConfig.professionalMapConfig,
            params: this.mapConfig.professionalMapConfig.params.filter(
              (item) => item.key
            ),
            script: encryptByDES(this.mapConfig.professionalMapConfig.script)
          },
        })
          .then((res) => {
            this.logList.push(res.data)
            this.isShowLog = true
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleClearLog() {
        this.logList = []
      },
    },
  }
</script>

