<template>
  <el-dialog
    custom-class="body-no-padding-dialog no-margin-dialog low-code-dialog"
    :fullscreen="false"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    title="自定义API管理"
    :visible.sync="dialogVisible"
    :before-close="handleClose"
    width="90%"
    top="0"
  >
    <div class="dialog-content" v-loading="loading">
      <div class="left-panel">
        <GroupPanel
          ref="groupPanel"
          @create="handleCreate"
          :defaultSelectId="defaultSelectId"
          @select="(_) => (selectedApi = _)"
          @update="(_) => (apiGroupList = _)"
        />
      </div>
      <div class="right-content">
        <ApiItem
          v-if="selectedApi"
          :api-data="selectedApi"
          :apiGroupList="apiGroupList"
          @update="handleUpdate"
          @refresh="handleRefresh"
        />
        <n-empty
          v-else
          class="right-content-empty"
          description="暂无数据，请在左侧分组栏新增自定义API"
        ></n-empty>
      </div>
    </div>
  </el-dialog>
</template>

<script>
  import Vue from 'vue'
  import CustomApiVariableSelect from './components/CustomApiVariableSelect.vue'
  import MacroVariableSelect from '@/components/MacroVariables'
  import CustomInputDialog from './components/CustomInputDialog/index.vue'
  import JsonTree from './components/Format/JsonTree.vue'
  import GroupPanel from './components/GroupPanel.vue'
  import ApiItem from './components/ApiItem.vue'
  import customApi from '@/api/customApi'
  import { getRandomHexadecimal } from '@/utils/tree-utils'
  import { CUSTOM_API_SESSION_ID, CUSTOM_API_RESPONSE_MAP } from '@/config';
  import treeTool from 'tree-tool'
  const treeUtil = treeTool.createInstance({
    id: 'id',
    children: 'apis',
    pid: 'parentId',
  })

  Vue.component('CustomApiVariableSelect', CustomApiVariableSelect)
  Vue.component('MacroVariableSelect', MacroVariableSelect)
  Vue.component('CustomInputDialog', CustomInputDialog)
  Vue.component('JsonTree', JsonTree)

  export default {
    name: 'CustomApi',
    components: {
      GroupPanel,
      ApiItem,
    },
    data() {
      return {
        loading: false,
        dialogVisible: false,
        selectedApi: null,
        apiGroupList: [],
      }
    },
    computed: {
      defaultSelectId() {
        return this.selectedApi?.id
      }
    },
    methods: {
      open() {
        this.dialogVisible = true
        this.$nextTick(() => {
          this.handleRefresh()
        })
      },
      handleClose(done) {
        window[CUSTOM_API_RESPONSE_MAP] = {}
        this.dialogVisible = false
        done()
      },
      handleCreate(type) {
        switch (type) {
          case 'api': {
            this.selectedApi = {
              id: null,
              appId: +this.$route.query.id,
              description: '',
              groupId: undefined,
              name: '',
              path: '',
              requestBody: {},
              status: undefined,
              steps: [],
              useScript: false,
              scriptConfig: {
                scriptType: 'GROOVY',
                script: ''
              }
            }
            break
          }
          case 'group': {
            this.handleAddGroup()
            break
          }
        }
      },
      handleUpdate() {
        this.$refs.groupPanel.handleGetApiGroupList()
      },
      async handleRefresh() {
        await this.$refs.groupPanel.handleGetApiGroupList()
        this.selectedApi = this.selectedApi ? treeUtil.toList(this.apiGroupList).find(item => item.id === this.selectedApi.id) || null : null
      },
      handleAddGroup() {
        this.loading = true
        this.$http({
          url: customApi.apiGroupSave,
          data: {
            appId: +this.$route.query.id,
            name: `新增分组${this.apiGroupList.length + 1}`,
          },
        })
          .then((res) => {
            this.$refs.groupPanel.handleGetApiGroupList()
          })
          .finally(() => {
            this.loading = false
          })
      },
    },
    created() {
      if (!window[CUSTOM_API_SESSION_ID]) {
        window[CUSTOM_API_SESSION_ID] = getRandomHexadecimal(12)
      }
      if (!window[CUSTOM_API_RESPONSE_MAP]) {
        window[CUSTOM_API_RESPONSE_MAP] = {}
      }
    }
  }
</script>

<style scoped lang="less">
  .dialog-content::v-deep {
    position: relative;
    height: calc(100vh - 40px);
    min-height: 600px;
    display: flex;
    .left-panel {
      height: 100%;
      width: 300px;
      border-right: 1px solid #eee;
    }
    .right-content {
      flex: 1;
      height: 100%;
      padding: 10px 20px;
      overflow: auto;
      & > .right-content-empty {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(0, -50%);
      }
    }
  }
</style>
